<!--PG사 수수료율 -->
<template>
  <v-container>
    <v-row dense>
      <v-col cols=12>


    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">PG사 수수료율</span>
        <v-spacer></v-spacer>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="deleteEvent">
         삭제
       </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="regEvent">
         입력
       </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
             </v-card>
       </v-col>
      </v-row>

      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="list"
        dense
        dark
        show-select
        item-key="idx"
        @click:row="detailEvent"
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      </v-data-table>
    <PgFeeEdit 
      :dialog='dialogFlag' 
      :dialogData='item' 
      :title='title' 
      @dialogEvent='dialogOFFEvent' 
    />
  </v-container>
</template>

<script>
import {mapActions} from 'vuex'
import commInfo from "@/mixins/comInfo";
import PgFeeEdit from "@/components/notice/PgFeeEdit";
import api from "@/api/notice.js";
export default {
  mixins: [commInfo],
  components : {
    PgFeeEdit
  },

  created() {
    document.title = 'PG사 수수료율';
    this.fetch_pg_fee_list();

  },
  computed : {
    list(){
      try {
        const list = this.$store.state.noticeStore.pgFeeList.map(item=>{
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }

    }
  },
  data() {
    return {
      title : '공지사항수정',
      dialogFlag : false,
      item : null,
      headers : [
        {text: '순번', value:'idx', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: 'PG사', value:'pgName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '기본수수료율', value:'defaultFee', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '익일수수료율', value:'fee01', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '5일수수료율', value:'fee02', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],
    }
  },
  methods: {
    ...mapActions('noticeStore',[
      'fetch_pg_fee_list',
    ]),
    dialogOFFEvent(){
      this.dialogFlag=false;
    },
    searchEvent(){
      this.fetch_pg_fee_list();
    },
    detailEvent(val){
      this.dialogFlag=true;
      this.item= val;
      this.title = 'PG사 수수료율 수정';
    },
    regEvent(){
      this.dialogFlag=true;
      this.title = 'PG사 수수료율 입력';
      this.item={
        idx : -1,
        defaultFee : 0,
        fee01 : 0,
        fee02 : 0,
        fee03 : 0,
        pgName : ''
      }
    },
    deleteEvent(){
      let idxStr = '';
      if(this.selected.length <1) {
        return;
      }
      this.selected.forEach(element => {
        if(idxStr.length >0) idxStr = idxStr + ','+ element.idx;
        else idxStr = String(element.idx);
      });
      api.deletePgFee(idxStr)
      .then(_=>this.fetch_pg_fee_list())

    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>