<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      persistent 
      max-width="700px"
    >
    <v-card
      class="mx-auto"
    >
      <v-app-bar
        color="blue"
        dense
        dark
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>입출내역관리</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-container>
        <v-row dense>
          <v-col cols="12" sm="6" md="6">
            <v-menu
              v-model="menu1"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
              >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  dense
                  v-model="dateStart"
                  label="검색시작일"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker 
                v-model="dateStart" 
                locale="ko"
                no-title
                @input="menu1 = false">
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols="12" sm="6" md="6">
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
              >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  dense
                    v-model="dateEnd"
                    label="검색종료일"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker 
                v-model="dateEnd" 
                locale="ko"
                no-title
                @input="menu2 = false">
              </v-date-picker>
            </v-menu>
            </v-col>
          </v-row>
          <v-row dense >    
          <v-col cols="12" sm="6" md="6" >
            <v-select
              :items="dealerList"
              item-text="cmpnm"
              item-value="bizCode"
              v-model="bizCode"  
              dense
              solo
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="6">
            <v-select
              label="입출금내역구분"
              :items="items1"
              item-text="text"
              item-value="val"
              v-model="inoutCode"
              dense
              solo
            ></v-select>
            </v-col>
   
          </v-row>
        <v-row dense>
          <v-col class="d-flex" cols="12" sm="12" md="6">   
            <v-select  
              class="mr-2"
              :items="items3"
              item-text="text"
              item-value="val"
              v-model="selected1"
              label="조회조건선택"
              dense
              solo
              @change="selected1Change"
            ></v-select>
            <v-text-field
              outlined
              dense
              v-model="selected1Val"
              :readonly="selected1Read"
              ref="selected1Ref"
            ></v-text-field>
          </v-col>
          <v-col class="d-flex" cols="12" sm="12" md="6">
            <v-select
              class="mr-2"
              :items="items3"
              item-text="text"
              item-value="val"
              v-model="selected2"
              label="조회조건선택"
              dense
              solo
              @change="selected2Change"
            ></v-select>
            <v-text-field
              v-model="selected2Val"
              outlined
              dense
              :readonly="selected2Read"
              ref="selected2Ref"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row dense>
          <v-col cols="12">
            <v-card-actions >
              <v-spacer></v-spacer>
              <v-btn
                color="blue-grey"
                tile
                @click="searchEvent"
                dark
              >
                <v-icon left>mdi-magnify</v-icon> 조회
              </v-btn>
            
            </v-card-actions>
          </v-col>
        </v-row>
      </v-container>       
    </v-card>
    </v-dialog>
   </v-row>
</template>

<script>
import { mapMutations } from 'vuex'
import commInfo from "@/mixins/comInfo";
export default {
  props : ['dialog' 
    , 'startIndex' 
    , 'endIndex'
  ],
  mixins: [commInfo],
  data() {
    return {
      dateStart : new Date().toISOString().substr(0, 10),
      dateEnd : new Date().toISOString().substr(0, 10),
      menu1 : false,
			menu2 : false,  
      payload : {},
      bizCode : '',

      items1 : [
        {text : '전체' , val : ''},
        {text : '입금' , val : '입금'},
        {text : '본사정산입금' , val : '본사정산입금'},
        {text : '출금' , val : '출금'},
        {text : '매출취소' , val : '매출취소'},
        {text : '본사출금' , val : '본사출금'},
      ],
      items2 : [
        {text : '전체' , val : ''},
        {text : '판매자성명' , val : 'mber_name'},
        {text : '아이디' , val : 'mberId'},
        {text : '상호' , val : 'nm'},
        {text : '사업자번호' , val : 'biz_num'},
      ],
      
      items3 : [
        {text : '전체' , val : ''},
        {text : '판매자성명' , val : 'mber_name'},
        {text : '아이디' , val : 'mberId'},
        {text : '상호' , val : 'nm'},
        {text : '사업자번호' , val : 'biz_num'},
      ],
      selected1 : '',
      selected2 : '',
      selected1Val : '',
      selected2Val : '',
      selected1Read : true,
      selected2Read : true
    }
  },
  methods: {
    ...mapMutations([
       'SET_ALERT_DIALOG'
		]),
    searchEvent(){
      this.payload = {};
			this.payload.dateStart = this.dateStart.replace(/-/g,'');
			this.payload.dateEnd = this.dateEnd.replace(/-/g,'');
      this.payload.bizCode = this.bizCode;
      this.payload.mberName = this.mberName;
      this.payload.indutyId = this.indutyId;
      this.payload.bizNum = this.bizNum;
      this.payload.searchOption = this.searchOption;
			this.payload.memberBizeCode = this.memberBizCode;
      this.payload.startIndex = 0;
      this.payload.endIndex = 20;
      this.payload.inoutCode = this.inoutCode;

      // 검색기간 1주일 제한 -- 1주일을 한경우 디비느려짐
      if(this.payload.dateEnd  - this.payload.dateStart >7){
        this.SET_ALERT_DIALOG({flag : true , message :'검색기간은 7일 이내입니다.'});
        return;
      }
      if( this.payload.dateStart > this.payload.dateEnd) {
        this.SET_ALERT_DIALOG({flag : true , message :'검색기간시간일은 검색종료일보다 늦을 수 없습니다.'});
        return;
      }       
      this.$emit('dialogEvent' , false);


      if(this.delngSeCode !=''){
        this.payload.delngSeCode = this.delngSeCode;
      }
      // 첫번째 검색항목
      if(this.selected1!=='전체' && this.selected1Val!==''){
        switch (this.selected1) {
          case 'mber_name':
            this.payload.mberName = this.selected1Val;
            break;
          case 'confm_no':
            this.payload.confmNo = this.selected1Val;
            break;
          case 'mberId':
            this.payload.mberId = this.selected1Val;
            break;
          case 'biz_num':
            this.payload.bizNum = this.selected1Val;
            break;
          case 'issueCmpnyNm':
            this.payload.issueCmpnyNm = this.selected1Val;
            break;
          case 'tot':
            this.payload.tot = this.selected1Val;
            break;
          default:
            break;
        }
      }

      // 두번째 검색항목 첫번째 항목과 같은 경우 덮어쓰기 한다.
      if(this.selected2!=='전체' && this.selected2Val!==''){
        switch (this.selected2) {
          case 'mber_name':
            this.payload.mberName = this.selected2Val;
            break;
          case 'confm_no':
            this.payload.confmNo = this.selected2Val;
            break;
          case 'mberId':
            this.payload.mberId = this.selected2Val;
            break;
          case 'biz_num':
            this.payload.bizNum = this.selected2Val;
            break;
          case 'issueCmpnyNm':
            this.payload.issueCmpnyNm = this.selected2Val;
            break;
          case 'tot':
            this.payload.tot = this.selected2Val;
            break;
          default:
            break;
        }
      }
      this.$emit('searchEvent' , this.payload);

    },
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
    selected1Change(val){
      if(val!=='') {
        this.selected1Read=false;
        this.$refs.selected1Ref.focus();
      }
      else this.selected1Read=true;
    },
    selected2Change(val){
      if(val!=='') {
        this.selected2Read=false;
        this.$refs.selected2Ref.focus();
      }
      else this.selected2Read=true;
    }
  },
}
</script>

