<template>
   <v-container>
    <v-speed-dial
      absolute='true'
      fixed
      v-model="fab"
      direction="left"
      bottom
      right
      :open-on-hover="true"
      transition="scale-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="blue darken-2"
          dark
          fab
          rounded
        >
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-magnify-plus-outline</v-icon>
        </v-btn>
      </template>
      <v-btn
        rounded
        dark
        small
        color="green"
        @click="dialog = true"
        
      >
        <v-icon small dark>mdi-magnify</v-icon>검색
      </v-btn>
      <v-btn
        rounded
        @click="excelDown(payload)"
        dark
        small
        color="indigo"
      >
        <v-icon small dark>mdi-cloud-download-outline</v-icon>
        엑셀다운
      </v-btn>
      <v-btn
        rounded
        dark
        small
        color="red"
        @click="atmStopEvent(true)"
      >
        <v-icon small dark>mdi-alarm-plus</v-icon>
        출금정지
      </v-btn>
    </v-speed-dial>
    <v-data-table
      :headers="headers"
      :items="list"
      :loading="loading"
      fixed-header
      height="700px"
      class="elevation-1"
      dense
      loading-text="조회중입니다."
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-arrow-collapse-left',
        lastIcon: 'mdi-arrow-collapse-right',
        prevIcon: 'mdi-arrow-left',
        nextIcon: 'mdi-arrow-right',
        itemsPerPageText : '표시갯수 :',
        itemsPerPageOptions : [20 , 50 , 100 , -1]
      }"
    >
      <template  v-slot:body="{ items }">
        <tbody>
          <tr v-for="(item, index) in items" :key="index" class="text-sm-caption" >
            <td :class="{sumover : item.count >0 }">{{ item.cmpnm }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.companyName }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.bizNum }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.indutyId }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.mberName }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.reqDt }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.inoutCode }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.reqAmt | currency( item.inoutCode) }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.charge | currency }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.balance | currency }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.bank }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.account }}</td>
            <td :class="{sumover : item.count >0 }">{{ item.depositor }}</td>
          </tr>
          <tr>
            <td v-if="sumInfo" colspan="13" style="text-align:center;background-color:gray;color:#fff">
              입금 : 총 {{sumInfo.inCnt}} 건 , {{sumInfo.inSum |currency}} 원 ||
              출금 : 총 {{sumInfo.outCnt }} 건 ,{{sumInfo.outSum | currency}} 원 ||
              매출취소 : 총 {{sumInfo.cancelCnt}} 건, {{sumInfo.cancelSum |currency}} 원
            </td>
          </tr>
        </tbody>
      </template>
    </v-data-table >
    <AtmStop :dialog='atmStopDialog' @atmStop="atmStopEvent" />
    
    <InqireAtmSearch :dialog='dialog' @dialogEvent="dialogEvent" @searchEvent="searchEvent" />
  </v-container>
</template>

<script>
import { mapActions , mapMutations } from 'vuex';
import InqireAtmSearch from "@/components/InqireAtmSearch";
import AtmStop from "@/components/AtmStop";
import {excel} from "@/mixins/excel";
import {currency} from "@/util/filter.js"
export default {
  components : {
    InqireAtmSearch,AtmStop
  },
  mixins: [excel],
  created() {
    document.title = '입출내역전체관리';
    this.set_inqire_atm_list([]);
    this.set_inqire_atm_sum(null);
    this.set_inqire_atm_tot(0)
  },
  computed: {
    list(){
      let list = [];
      try {
        if(this.$store.state.bpwStore.inqireAtmList){
          list = this.$store.state.bpwStore.inqireAtmList;
          for (let i = 1; i < list.length; i++) {
            list[i].count = 0;
            if(list[i-1].mberName === list[i].mberName && list[i-1].inoutCode === list[i].inoutCode){
              list[i].count = 1;
            }
          }
          return list;
        } 
        else return list
      } catch (error) {
        console.error(error);
        return list;
      }
    },
    sumInfo(){
      try {
        return this.$store.state.bpwStore.inqireAtmSum
      } catch (error) {
        console.error(error);
        return {}
      }
    }
  },

  
    data() {
      return {
        atmStopDialog : false,
        fab : false,
        direction: 'bottom',
        dialog : false,
        loading : false,
        payload : {
          dateStart : new Date().toISOString().substr(0, 10).replace(/-/g,''),
          dateEnd : new Date().toISOString().substr(0, 10).replace(/-/g,''), 
          memberBizeCode : '0000002', // 로그인 세션 기능 만들어 진후 변경한다.
          startIndex : 0,
          endIndex :0
        },
        headers : [
          {text : '대리점', value: 'cmpnm'},
          {text : '상호', value: 'companyName'},
          {text : '사업자번호', value: 'bizNum'},
          {text : '판매자ID', value: 'indutyId'},
          {text : '판매자이름', value: 'mberName'},
          {text : '거래일자', value: 'reqDt'},
          {text : '구분', value: 'inoutCode'},
          {text : '입출금액', value: 'reqAmt' },
          {text : '출금수수료', value : 'charge' },
          {text : '잔액', value: 'balance'},
          {text : '은행', value: 'bank'},
          {text : '계좌번호', value: 'account'},
          {text : '예금주', value: 'depositor'}
        ]
      }
    },
    methods: {
      ...mapActions('bpwStore' , ['fetch_inqire_atm_list']),
      searchEvent(payload){
        this.$store.state.mainLoading = true;
        this.payload = payload;
        payload.startIndex = 0;
        payload.endIndex = 0;
        this.fetch_inqire_atm_list(payload)
        .then( _=>{
          this.$store.state.mainLoading =false;
        })
      },
      atmStopEvent(val){
        this.atmStopDialog = val;
      },
      dialogEvent(){
        this.dialog = false;
      },
      async excelDown(payload){
      try {
        this.$store.state.mainLoading = true;

        let list = await this.excelApiCallEvent('atm' , payload);
        if(!list) {
          //this.$store.state.mainLoading = false;
          return;
        }
        list = list.list.map((item)=>{
          let retItem = {};
          if(item.hasOwnProperty("cmpnm")) {
            retItem.대리점 = item.cmpnm;
          }
          if(item.hasOwnProperty("indutyId")) {
            retItem.판매자ID = item.indutyId;
          }
          if(item.hasOwnProperty("mberName")) {
            retItem.판매자이름 = item.mberName;
          }
          if(item.hasOwnProperty("reqDt")) {
            retItem.거래일시 = item.reqDt;
          }
          if(item.hasOwnProperty("inoutCode")) {
            retItem.구분 = item.inoutCode;
          }
          if(item.hasOwnProperty("reqAmt")) {
            retItem.입출금액 = currency(item.reqAmt,item.inoutCode);
          }
          if(item.hasOwnProperty("charge")) {
            retItem.출금수수료 = currency(item.charge);
          }
          if(item.hasOwnProperty("balance")) {
            retItem.잔액 = currency(item.balance);
          }
          if(item.hasOwnProperty("bank")) {
            retItem.은행 = item.bank;
          }
          if(item.hasOwnProperty("account")) {
            retItem.계좌번호 = item.account;
          }
          if(item.hasOwnProperty("depositor")) {
            retItem.예금주 = item.depositor;
          }
          return retItem;
        });
        
        if(!this.excelEvent(list ,`입출력내역관리-${payload.dateStart}-${payload.dateEnd}` ,'입출력' )){ 
          // 실패시 메세지 출력
        }
        
      } catch (error) {
        console.error(error);
        
      } finally {
        this.$store.state.mainLoading = false;
      }
    }
    },
}
</script>

<style scoped>
  .sumover {
    background-color:#827717;
    color: #fff;
    font-size: xx-large;
  }
  .header {
    background-color:#DCEDC8;
    font-size: xx-large;
  }
</style>