<template>
  <v-app>
    <Header @drawerEvent="drawerEvent" />
    <Navigation :drawer="drawer" @drawerEvent="drawerEvent"/>
     <v-main >

        <v-slide-y-reverse-transition>
          <router-view></router-view>
        </v-slide-y-reverse-transition>

     </v-main>

    <Info/>
    <OverLay/>
  </v-app>
</template>

<script>
import {mapActions} from 'vuex'
import Header from "./Header";
import Footer from "./Footer";
import Navigation from "./Navigation";
import Info from "../snacbar/Info";
import OverLay from "@/components/layout/OverLay.vue";
export default {
  name: 'MainLayout',
  created() {
    const { authkey , userId } = localStorage;
    if(this.$store.state.agencyStore.dealerList.length < 1){
        this.fetch_dealer_list()
      }
    if(!!authkey && !!userId ) this.$store.actions.authStore.FETCH_RELOGIN( {authkey , userId });
  },

  components : {
    Header,
    Navigation,
    Footer,
    Info,
    OverLay
  },
  data() {
    return {
      drawer : false
    }
  },
  methods: {
    ...mapActions('agencyStore',[
        'fetch_dealer_list'
      ]),
    drawerEvent(val){
       this.drawer =val;
      if(!!val){
        this.drawer=false;
        this.drawer=true;
      }
      else this.drawer =false;
    }
  },
}
</script>

<style>
 .v-main{
   background-color:#E0F2F1 !important;
   padding: 35px 0px 0px !important;; 
 }
</style>