<!-- 판매자별매출화면 -->
<template>
    <v-container>
      <v-row dense>
        <v-col cols=12>
           <v-card dark color="deep-purple darken-3">
             <v-app-bar dense class="deep-purple lighten-1 white--text">
               <span class="subtitle-1 font-weight-black">판매자매출</span>
               <v-spacer></v-spacer>
               <v-divider light vertical inset></v-divider>
               <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
                조회
              </v-btn>
              <v-divider light vertical inset></v-divider>
              <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
                엑셀
              </v-btn>
             </v-app-bar>
             <v-row dense>
               <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색시작일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateStart" 
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateStart" 
                    locale="ko"
                    no-title
                    :max= 'this.dateStartMax'
                    @input="dateMenu1 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색종료일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateEnd"
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateEnd" 
                    locale="ko"
                    no-title
                    :max='new Date().toISOString().substr(0, 10)'
                    @change="dateEndEvent"
                    @input="dateMenu2 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  연도선택
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  dense
                  v-model="year"
                  :items="yearList"
                  label="전체" 
                  single-line
                  @change="yearChange('year')"
                >
                </v-select>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  반기선택
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  :style="{fontSize:'5px'}"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="banki"
                  :items="bankiList"
                  label="대리점전체" 
                  single-line
                  @change="yearChange('ban')"
                >
                </v-select>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  분기선택
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="bunki"
                  :items="bunkiList"
                  label="전체" 
                  single-line
                  @change="yearChange('bunki')"
                >
                </v-select>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  월선택
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="month"
                  :items="monthiList"
                  label="전체" 
                  single-line
                   @change="yearChange('month')"
                >
                </v-select>
              </v-col>
             </v-row>
             <v-row dense>
                <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option1"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal1"
                    v-model="textVal1"
                  ></v-text-field>
                </v-col>
                <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal2"
                    v-model="textVal2"
                  ></v-text-field>
                </v-col>
              </v-row>
           </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols=12>
        <v-alert
            v-if="sumInfo.totCount!=0"
            text
            prominent
            type="error"
            icon="mdi-tray-minus"
            border="left"
          >
            전체합계 : {{sumInfo.totCount | currency}} 건 ,{{sumInfo.totSum | currency}} 원 
            , 일반합계 : {{sumInfo.normalCount | currency}} 건 ,{{sumInfo.normalSum | currency}} 원 
            , 익일합계 : {{sumInfo.ikilCount | currency}} 건 ,{{sumInfo.ikilSum | currency}} 원 
            , 바로장산 합계 :{{sumInfo.baroCount | currency}} 건 ,{{sumInfo.baroSum | currency}} 원 
          </v-alert>
          </v-col>
      </v-row>

      <v-row dense>
        <v-col cols=12>
          <v-data-table
            :headers="headers"
            :items="list"
            dense
            dark
            no-results-text="조회된 데이터가 없습니다."
            no-data-text="데이터를 조회를 하세요"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-arrow-collapse-left',
              lastIcon: 'mdi-arrow-collapse-right',
              prevIcon: 'mdi-arrow-left',
              nextIcon: 'mdi-arrow-right',
              itemsPerPageText : '표시갯수 :',
              itemsPerPageOptions : [20 , 50 , 100 , -1]
            }"
             
          >
          <template v-slot:[`item.cardTot`]="{ item }">
            {{ item.cardTot | currency}} 건
          </template>
          <template v-slot:[`item.cardCnt`]="{ item }">
            {{ item.cardCnt | currency}} 건
          </template>
          <template v-slot:[`item.fee`]="{ item }">
            {{ item.fee | currency}} 건
          </template>
          <template v-slot:[`item.pay`]="{ item }">
            {{ item.pay | currency}} 건
          </template>
          <template v-slot:[`item.cashTot`]="{ item }">
            {{ item.cashTot | currency}} 건
          </template>
          <template v-slot:[`item.cashCnt`]="{ item }">
            {{ item.cashCnt | currency}} 건
          </template>
          <template v-slot:[`item.tot`]="{ item }">
            {{ item.tot | currency}} 건
          </template>
          <template v-slot:[`item.totCnt`]="{ item }">
            {{ item.totCnt | currency}} 건
          </template>
          </v-data-table>
        </v-col>
      </v-row>

    </v-container>
</template>

<script>
import { mapActions , mapMutations ,mapGetters } from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
import moment from 'moment';
export default {
  created() {
    document.title = '판매자매출';
    this.$store.state.overLay = false;
    this.getYearList();
  },
  mixins: [excel , commInfo],
  data() {
    return {
      year : 0,
      banki : 0 ,
      bangi : 0,
      month : 0,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        confmNo : "", // 승인번호
        userId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호 
      },

      sumInfo :{
        totCount : 0,
        totSum : 0,
        normalCount : 0,
        normalSum : 0,
        ikilCount : 0,
        ikilSum : 0,
        baroCount : 0,
        baroSum : 0
      },

      yearList : [],
      bankiList : [
        {text : '반기선택' , val : 0},
        {text : '1~6월' , val : 1},
        {text : '7~12월' , val : 2},
      ],

      bunkiList : [
        {text : '분기선택' , val : 0},
        {text : '1분기' , val : 1},
        {text : '2분기' , val : 2},
        {text : '3분기' , val : 3},
        {text : '4분기' , val : 4},
      ],
      monthiList : [
        {text : '월선택' , val : 0},
        {text : '1월' , val : 1},
        {text : '2월' , val : 2},
        {text : '3월' , val : 3},
        {text : '4월' , val : 4},
        {text : '5월' , val : 5},
        {text : '6월' , val : 6},
        {text : '7월' , val : 7},
        {text : '8월' , val : 8},
        {text : '9월' , val : 9},
        {text : '10월' , val : 10},
        {text : '11월' , val : 11},
        {text : '12월' , val : 12},
      ],

      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mber_name'},
        {text : '판매자아이디', val : 'mberId'},
        {text : '사업자번호', val : 'bizNum'},
        {text : '상호', val : 'issueCmpnyNm'},        
      ],

      headers: [
       {text: '대리점', value:'cmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '코드' , value:'kpayType' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
       {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '상업자등록번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '카드매출', value:'cardTot', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '건수', value:'cardCnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료율(%)', value:'feeRate', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료', value:'fee', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '지급금액', value:'pay', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '현금매출', value:'cashTot', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '현금건수', value:'cashCnt', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '총매출', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '총건수', value:'totCnt', class :'d-flexcaption font-weight-bold align-center pt-1'},       
     ],
    }
  },
  computed : {
    list(){
      try {
        return this.$store.state.agencyStore.sellerList;
      } catch (error) {
        console.error(error);
        return [];
      }
    },
  },

  methods: {
    ...mapActions('agencyStore' , 
      ['fetch_seller_list']),
    searchEvent(){
      // 옵션별 선택 값 구하기
      this.payload.mberName = '';
      this.payload.confm_no = '';
      this.payload.mberId = '';
      this.payload.bizNum = '';
      this.payload.companyName = '';
      this.payload.sellPrice = '';
      if(this.option1 === 'mber_name') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'confm_no') {
        this.payload.confm_no = this.textVal1;
      }
      else if(this.option1 === 'mberId') {
        this.payload.mberId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'issueCmpnyNm') {
        this.payload.companyName = this.textVal1;
      }
      else if(this.option1 === 'sellPrice') {
        this.payload.sellPrice = this.textVal1;
      }

      if(this.option2 === 'mber_name') { 
        this.payload.mberName = this.textVal2;
      }
      else if(this.option2 === 'confm_no') {
        this.payload.confm_no = this.textVal2;
      }
      else if(this.option2 === 'mberId') {
        this.payload.mberId = this.textVal2;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal2;
      }
      else if(this.option2 === 'issueCmpnyNm') {
        this.payload.companyName = this.textVal2;
      }
      else if(this.option2 === 'sellPrice') {
        this.payload.sellPrice = this.textVal2;
      }
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_seller_list(retPayload)
      .then(_=>{
        // 전체합정보구한다.
        this.sumInfo.totCount = 0;
        this.sumInfo.tot = 0;
        this.sumInfo.normalCount = 0;
        this.sumInfo.normalSum = 0;
        this.sumInfo.baroCount = 0;
        this.sumInfo.baroSum = 0;
        this.sumInfo.ikilSum = 0;
        this.sumInfo.ikilCount = 0;
        this.list.forEach(el => {
          this.sumInfo.totCount += Number(el.totCnt);
          this.sumInfo.totSum += Number(el.tot);
          if(el.kpayType === '바로정산'){
            this.sumInfo.baroCount +=Number(el.totCnt);
            this.sumInfo.baroSum +=Number(el.tot);

          }
          if(el.kpayType === '익일입금'){
            this.sumInfo.ikilSum +=Number(el.tot);
            this.sumInfo.ikilCount +=Number(el.totCnt);

          }
          if(el.kpayType === '일반'){
            this.sumInfo.normalSum +=Number(el.tot);
            this.sumInfo.normalCount +=Number(el.totCnt);

          }
        });
      })
      .catch(err=>console.log(err));

    },
    // 엑셀다운로드
     excelDown(){
      try {
        this.$store.state.mainLoading = true;
        const wscols = []
        const exList = this.list.map(item=>{
            const retItem = {};
     
            if(item.hasOwnProperty("cmpnm")) {
              retItem.대리점 = item.cmpnm;
              wscols.push({wpx:80});
            }
  
            if(item.hasOwnProperty("kpayType")) {
              retItem.코드 = item.kpayType;
              wscols.push({wpx:60});
            }
            if(item.hasOwnProperty("usid")) {
              retItem.판매자ID = item.usid;
              wscols.push({wpx:80});
            }
            if(item.hasOwnProperty("mberName")) {
              retItem.판매자성명 = item.mberName;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("companyName")) {
              retItem.상호 = item.companyName;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("bizNum ")) {
              retItem.사업자등록번호 = item.bizNum;
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("cardTot")) {
              retItem.카드매출 = currency(item.cardTot);
              wscols.push({wpx:80});
            }
            if(item.hasOwnProperty("cardCnt")) {
              retItem.카드건수 = currency(item.cardCnt);
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("feeRate")) {
              retItem.수수료율 = item.feeRate+'%';
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("fee")) {
              retItem.수수료 = currency(item.fee);
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("pay")) {
              retItem.지급금액 = currency(item.pay);
              wscols.push({wpx:60});
            }

            if(item.hasOwnProperty("cashTot")) {
              retItem.현금매출 = currency(item.cashTot);
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("cashCnt")) {
              retItem.현금건수 = currency(item.cashCnt);
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("tot")) {
              retItem.총매출 = currency(item.tot);
              wscols.push({wpx:80});
            }
            if(item.hasOwnProperty("totCnt")) {
              retItem.총건수 = currency(item.totCnt);
              wscols.push({wpx:40});
            }
           
            return retItem;
          });      
    
          this.excelEvent(exList ,`판매자매출-${this.payload.dateStart}-${this.payload.dateEnd}` ,'거래조회',wscols );

      } catch (error) {
        console.error(error);
      } finally{
        this.$store.state.mainLoading = false;
      }
    },


   // 현재 년도 구하기및 이전 년도 목록만들기
    getYearList(){
      this.yearList = [];
      const today = new Date();
      let nowYear = today.getFullYear();
      this.year = nowYear;
      this.yearList.push(nowYear);
      while (nowYear > 2015 ) {
        nowYear--;
        this.yearList.push(nowYear);
      }
    },


    // 년도를 수정한경우
    yearChange(val){
      //debugger
      let tempDate = moment(String(this.year) );
      let tempNow = moment().format('YYYY-MM-DD');
      if(val === 'year'){
        this.banki=0;
        this.bunki=0;
        this.month=0;
        tempDate = moment(String(this.year) );
        this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
        this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
      }
      // 반기수정
      if(val==='ban'){
        this.bunki=0;
        this.month=0;
        if(this.banki===1){
          tempDate = moment(String(this.year+"01") );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          tempDate = moment(String(this.year+"06") );
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        }
        if(this.banki===2){
          tempDate = moment(String(this.year+"07") );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          tempDate = moment(String(this.year+"12") );
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        }
        if(this.banki===0){
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }
      // 분기수정
      if(val==='bunki'){
        this.banki=0;
        this.month=0;
        tempDate = moment(String(this.year))
        if(this.bunki >0 && this.bunki <5){
          tempDate = tempDate.quarter(this.bunki) ;
          this.payload.dateStart = tempDate.startOf('quarter').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('quarter').format('YYYY-MM-DD');
        } 
        else {
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }
      // 월수정
      if(val==='month'){
        this.banki=0;
        this.bunki=0;
        tempDate = moment(String(this.year));
        if(this.month >0 && this.month <13){
          if(this.month <10 )tempDate = moment(String(this.year)+ "0"+String(this.month) );
          else tempDate = moment(String(this.year)+String(this.month) );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        } 
        else {
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }

      if(this.payload.dateStart >  tempNow )this.payload.dateStart = tempNow;
      if(this.payload.dateEnd >  tempNow )this.payload.dateEnd = tempNow;
    }, 
  },
}
</script>

