<!-- 대리점관리 -->
<template>
    <v-container>
      <v-row dense>
        <v-col cols=12>
           <v-card dark color="deep-purple darken-3">
             <v-app-bar dense class="deep-purple lighten-1 white--text">
               <span class="subtitle-1 font-weight-black">대리점관리</span>
               <v-spacer></v-spacer>
               <v-divider light vertical inset></v-divider>
               <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
                조회
              </v-btn>
             </v-app-bar>
             <v-row dense class="pt-2" >
                <v-col cols=2 xs=4 sm=3 md=2 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option1"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=3 xs=8 md=4 sm=9 lg=4 xl=3>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal1"
                    v-model="textVal1"
                  ></v-text-field>
                </v-col>
                <v-col cols=2 xs=4 md=2 sm=3 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=3 xs=8 md=4 sm=9 lg=4 xl=3>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal2"
                    v-model="textVal2"
                  ></v-text-field>
                </v-col>
                <v-col cols=2 xs=4 md=12 sm=3 lg=2 xl=2>
                  <v-select
                    class="caption pr-2 pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="payload.useAt"
                    :items="transList"
                    label="거래상태" 
                    outlined
                  >
                  </v-select>
                </v-col>

              </v-row >
         
               
      


            
           </v-card>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col>
          <v-btn block @click="excelEvent1">
            엑셀다운로드
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="regEvent">
            대리점등록
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="excelEvent2">
            계좌이체용다운로드
          </v-btn>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols=12>
          <v-data-table
            :headers="headers"
            :items="list"
            dense
            dark
            no-results-text="조회된 데이터가 없습니다."
            no-data-text="데이터를 조회를 하세요"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-arrow-collapse-left',
              lastIcon: 'mdi-arrow-collapse-right',
              prevIcon: 'mdi-arrow-left',
              nextIcon: 'mdi-arrow-right',
              itemsPerPageText : '표시갯수 :',
              itemsPerPageOptions : [20 , 50 , 100 , -1]
            }"
            @dblclick:row="detailEvent"
          >
         
          </v-data-table>
        </v-col>
      </v-row>
      <agencyEdit :infodialog='dialog' @reSearchEvent='searchEvent'  @dialogEvent='dialogEvent' :item='dialogData' :preItem='dialogData' :title='dialogTitle'></agencyEdit>
    </v-container>
</template>
<script>
import { mapActions} from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {removeCharObject} from "@/util/filter.js";
import agencyEdit from "@/components/agencyEdit.vue";
export default {
  created() {
    document.title = '대리점관리';
    this.$store.state.overLay = false;

  },
  computed : {
    list(){
      try {
        if(!!this.$store.state.agencyManageStore.agencyList) {
          return this.$store.state.agencyManageStore.agencyList;
        }else return [];
      } catch (error) {
        return [];
      }
    }
  },
  components : {
    agencyEdit
  },
  mixins: [excel , commInfo],
  data() {
    return {
      option1 : null,
      option2 : null,
      textVal1 : null,
      textVal2 : null,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        confmNo : "", // 승인번호
        dealerId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호 
        delngSeCode : '',
        calculateType:'',
        cmpnm : '',
        useAt : ''
      },
      // 거래구분 거래중 거래중지
      transList : [
        {text : '전체', val : ''},
        {text : '거래중', val : 'Y'},
        {text : '거래중지', val : 'N'},
      ],


      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '상호', val : 'cmpnm'},
        {text : '아이디', val : 'dealerId'},    
        {text : '사업자번호', val : 'bizNum'},
      ],

      headers: [
        {text: '대리점구분', value:'gubun', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호(이름)' , value:'cmpnm' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
        {text: '사업자번호', value:'bizrno', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '아이디', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '활성회원수', value:'mberCnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '등록일', value:'creatDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '거래중지일', value:'거래중지일', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '총판', value:'distributor', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '대리점', value:'agent', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

      dialog : false,
      dialogData : {},
      dialogTitle : '',
    }
  },
  computed : {
    list(){
      try {
        return this.$store.state.agencyManageStore.agencyList.map((item)=>{
          let reItem = {};
          reItem = item;

          if(item.useAt==='N'){
            reItem.거래중지일 = item.creatDt;
          }else{
            reItem.거래중지일 = '-';
          }
          return reItem;
        })
      } catch (error) {
        console.error(error);
        return [];
      }
    },
  },

  methods: {
    ...mapActions('agencyManageStore' , 
      ['fetch_agency_list' , 'fetch_setting_agency_list']),
    dialogEvent(val){
      this.dialog = val;
    },
    // 데이터 테이블 더블클릭 과 클릭의 파라미터 요소가 틀리므로 주의 클릭으로 바꿀경우 event 자리에 item 이 온다.
    detailEvent( event , {item}){
      this.dialog = true;
      this.dialogTitle = '대리점확인&수정'
      this.dialogData = item;

      this.fetch_setting_agency_list({memberCode : this.GET_AUTH_MEMBER_CODE , agencyCode : item.bizCode});
    },
    regEvent(){
      this.dialog = true;
      this.fetch_setting_agency_list({memberCode : this.GET_AUTH_MEMBER_CODE  , agencyCode : '0000002'});
      this.dialogTitle = '대리점신규';
      // 초기선택값
      this.dialogData = {};
      this.dialogData.trgetBizCode = '0000002'; 
      this.dialogData.recommendBizCode = '0000002';
      this.dialogData.bizType = 'Y'; 
      this.dialogData.dealerKind = '32';
      this.dialogData.memberInputYn = '0';
      this.dialogData.useAt = 'Y'; 
      this.dialogData.pgVan = 'PG';
      this.dialogData.pgGb = 'KSNET';
      this.dialogData.limitOne = 300000; 
      this.dialogData.limitDay = 20000000; 
      this.dialogData.limitMonth = 20000000; 
      this.dialogData.limitYear = 24000000; 
      this.dialogData.installmentMonths = 5; 
      this.dialogData.bizLimitOne = 30000; 
      this.dialogData.bizLimitDay = 3000000; 
      this.dialogData.bizLimitMonth = 3000000; 
      this.dialogData.bizLimitYear = 36000000; 
      this.dialogData.bizInstallmentMonths = 12; 
      this.dialogData.payBprprr = '이규승'; 
      this.dialogData.payTelno = '1600-0174'; 
      this.dialogData.payCmpnm = '주)유니코아';
      this.dialogData.payBizrno = '820-81-00121';
      this.dialogData.payBplc = '서울시 구로구 디지털로30길28 1218호';

    },
    excelEvent1(){
      const wscols = [];
      const hedaers = [];
      this.$store.state.mainLoading = true;
      wscols.push({wpx:70});
      hedaers.push('대리점구분');
      wscols.push({wpx:120});
      hedaers.push('상호(이름)');
      wscols.push({wpx:100});
      hedaers.push('사업자번호');
      wscols.push({wpx:90});
      hedaers.push('사업장전화번호');
      wscols.push({wpx:90});
      hedaers.push('핸드폰번호');
      wscols.push({wpx:70});
      hedaers.push('아이디');
      wscols.push({wpx:90});
      hedaers.push('VAN터미널ID');
      wscols.push({wpx:90});
      hedaers.push('PG터미널ID');
      wscols.push({wpx:60});
      hedaers.push('VAN/PG');
      wscols.push({wpx:70});
      hedaers.push('활성회원수');
      wscols.push({wpx:120});
      hedaers.push('거래중지일');
      wscols.push({wpx:120});
      hedaers.push('총판');
      wscols.push({wpx:120});
      hedaers.push('대리점');

      const excelList = this.list.map(item =>{
        const retItem = [];
        if(item.hasOwnProperty("gubun")) {
          retItem.push(item.gubun);
        }
        if(item.hasOwnProperty("cmpnm")) {
          retItem.push(item.cmpnm);
        }
        if(item.hasOwnProperty("bizrno")) {
          retItem.push(item.bizrno);
        }
        if(item.hasOwnProperty("bizTelno")) {
          retItem.push(item.bizTelno);
        }
        
        if(item.hasOwnProperty("mtelno")) {
          retItem.push(item.mtelno);
        }

        if(item.hasOwnProperty("dealerId")) {
          retItem.push(item.dealerId);
        }
  
        if(item.hasOwnProperty("trmnlNo")) {
          retItem.push(item.trmnlNo);
        }
        if(item.hasOwnProperty("pgTrmnlNo")) {
          retItem.push(item.pgTrmnlNo);
        }
        if(item.hasOwnProperty("pgVan")) {
          retItem.push(item.pgVan);
        }
        if(item.hasOwnProperty("mberCnt")) {
          retItem.push(item.mberCnt);
        }
        if(item.hasOwnProperty("거래중지일")) {
          retItem.push(item.거래중지일);
        }
        if(item.hasOwnProperty("agent")) {
          retItem.push(item.agent);
        }
        if(item.hasOwnProperty("distributor")) {
          retItem.push(item.distributor);
        }
        return retItem;
      });
      
      this.excelArrayEvent(excelList ,`대리점목록-${this.payload.dateStart}` ,'대리점목록' , wscols , hedaers );
      this.$store.state.mainLoading = false;
    },

    excelEvent2(){
      const wscols = [];
      const hedaers = [];
      this.$store.state.mainLoading = true;
      wscols.push({wpx:70});
      hedaers.push('대리점구분');
      wscols.push({wpx:120});
      hedaers.push('상호(이름)');
      wscols.push({wpx:100});
      hedaers.push('사업자번호');
      wscols.push({wpx:90});
      hedaers.push('사업장전화번호');
      wscols.push({wpx:90});
      hedaers.push('핸드폰번호');
      wscols.push({wpx:70});
      hedaers.push('아이디');
      wscols.push({wpx:90});
      hedaers.push('은행명');
      wscols.push({wpx:90});
      hedaers.push('은행코드');
      wscols.push({wpx:110});

      hedaers.push('계좌번호');
      wscols.push({wpx:100});
      hedaers.push('이체금액');
      wscols.push({wpx:120});
      hedaers.push('예금주명');
      wscols.push({wpx:70});
      hedaers.push('입급계좌적요');


      const excelList = this.list.map(item =>{
        const retItem = [];
        if(item.hasOwnProperty("gubun")) retItem.push( item.gubun);
        if(item.hasOwnProperty("cmpnm")) retItem.push(item.cmpnm);
        if(item.hasOwnProperty("bizrno")) retItem.push(item.bizrno);
        if(item.hasOwnProperty("bizTelno")) retItem.push(item.bizTelno);
        if(item.hasOwnProperty("mtelno")) retItem.push(item.mtelno);
        
        if(item.hasOwnProperty("dealerId")) retItem.push(item.dealerId);
        if(item.hasOwnProperty("bankName")) {
          retItem.push(item.bankName); // 은행명
          retItem.push(this.getBankCode(item.bankName)); // 은행코드명
        }
        if(item.hasOwnProperty("bankSerial")) retItem.push(item.bankSerial);
        retItem.push(" "); // 이체금액
        if(item.hasOwnProperty("bprprr")) retItem.push(item.bprprr);
        retItem.push(" ");//입금계좌적요
        return retItem;
      });
      this.excelArrayEvent(excelList ,`계좌이체용엑셀-${this.payload.dateStart}` ,'대리점목록' , wscols , hedaers);
    },

    searchEvent(){
       this.payload.cmpnm = '';
       this.payload.dealerId= '';
       this.payload.bizNum= '';
      if(this.option1 === 'cmpnm') { 
        this.payload.cmpnm = this.textVal1;
      }
      else if(this.option1 === 'dealerId') {
        this.payload.dealerId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }

      if(this.option2 === 'cmpnm') {
        this.payload.cmpnm = this.textVal2;
      }
      else if(this.option2 === 'dealerId') {
        this.payload.dealerId = this.textVal2;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal2;
      }


      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_agency_list(retPayload);
    }
  },
}
</script>

<style>

</style>