<!--보증료관리 -->
<template>
  <v-container>
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">보증료관리</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
            보증료등록
          </v-btn>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=1>
          <div class="caption pl-3 pt-1">
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=2>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=1>
          <div class="caption pl-3 pt-1">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=2>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=12 xs=12 sm=6 md=6 lg=3 xl=3>
          <v-autocomplete
            class=" pl-2 pr-2 pt-1"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
        <v-col cols=12 xs=12 sm=6 md=6 lg=3 xl=3>
          <v-select
            class=" pl-2 pr-2 pt-1"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.target"
            :items="generalList"
            label="관리자_보증료관리_상태" 
            outlined

          >
          </v-select>
        </v-col>
        

        
      </v-row>
      <v-row dense>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <v-select
            class="caption pl-2"
            dense
            v-model="option1"
            :items="optionList"
            label="옵션" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
          <v-text-field
            class="caption pr-2"
            dense
            name="name"
            label="검색어입력"
            id="textVal1"
            v-model="textVal1"
          ></v-text-field>
        </v-col>
        <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
          <v-select
            class="caption pl-2"
            dense
            v-model="option2"
            :items="optionList"
            label="옵션" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
          <v-text-field
            class="caption pr-2"
            dense
            name="name"
            label="검색어입력"
            id="textVal2"
            v-model="textVal2"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions} from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject,handPhoneNum} from "@/util/filter.js";
import moment from "moment";
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '보증료관리';

  },
  computed : {
    list(){
      try {
        const list = this.$store.state.taxStore.guarantList.map(item=>{
          item.limitOne = currency(item.limitOne);
          item.limitDay = currency(item.limitDay);
          item.limitMonth = currency(item.limitMonth);
          item.limitYear = currency(item.limitYear);
          item.paymentAmount = currency(item.paymentAmount);
          item.mberMobile = handPhoneNum(item.mberMobile);
          
          // Invalid date 대응관련 자세한 사항 확인 필요 -- 여러 브라우저 확인이 필요한 항목이기도 한다.
          if(!!item.paymentDate && item.paymentDate.toString() !='Invalid date')item.paymentDate = moment(item.paymentDate ).format('YYYY-MM-DD');
          else item.paymentDate = "";
          if(!!item.smsSendDate&&  item.smsSendDate.toString() !='Invalid date')item.smsSendDate = moment(item.smsSendDate ).format('YYYY-MM-DD');
          else item.smsSendDate = "";
          
          if(!!item.nextDate && item.nextDate.toString() !='Invalid date') item.nextDate = moment(item.nextDate ).format('YYYY-MM-DD');
          else item.nextDate = "";
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }

    }
  },
  data() {
    return {
      headers : [
        {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자핸드폰', value:'mberMobile', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '1회한도', value:'limitOne', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '1일한도', value:'limitDay', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '월한도', value:'limitMonth', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '년한도', value:'limitYear', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '납부일', value:'paymentDate', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '납부금액', value:'paymentAmount', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '다음납부일', value:'nextDate', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '거래상황', value:'useAt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '만료알림일자', value:'smsSendDate', class :'d-flexcaption font-weight-bold align-center pt-1'},
       
      ],
      option1 : '',
      option2 : '',
      textVal1 : '',
      textVal2 : '',
      year : Number(new Date().toISOString().substr(0, 4)),
      bunki : 0,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        bizCode : "", // 대리점코드
        target : "GENERAL",
        memberBizeCode : "0000002",
        mberName : '',
        usid : '',
        bizNum : '',
      },

      //관리자_보증료관리_상태
      generalList : [
        {text : '전체' , val : 'GENERAL'},
        {text : '재납입대상자' , val : 'TODO'},
        {text : '미납자' , val : 'DEFAULTER'},
      ],
      // 옵션
      optionList : [ 
        '전체',
        '판매자성명',
        '아이디',
        '사업자번호',

      ],
    }
  },
  methods: {
    ...mapActions('taxStore',[
      'fetch_guarant_list',
    ]),

    excelDown(){
      const excelList = this.list.map(item =>{
        const retItem = {};
          if(item.hasOwnProperty("usid")) retItem.판매자ID = item.usid;
          if(item.hasOwnProperty("mberName")) retItem.판매자명 = item.mberName;
          if(item.hasOwnProperty("mberMobile")) retItem.판매자핸드폰 = item.mberMobile;
          if(item.hasOwnProperty("limitOne")) retItem.o1회한도 = item.limitOne;
          if(item.hasOwnProperty("limitDay")) retItem.o1일한도 = item.limitDay;
          if(item.hasOwnProperty("limitMonth")) retItem.월한도 = item.limitMonth ;
          if(item.hasOwnProperty("limitYear")) retItem.년한도 = item.limitYear;
          if(item.hasOwnProperty("paymentDate")) retItem.납부일 = item.paymentDate;
          if(item.hasOwnProperty("paymentAmount")) retItem.납부금액 = item.paymentAmount;
          if(item.hasOwnProperty("nextDate")) retItem.다음납부일 = item.nextDate;
          if(item.hasOwnProperty("useAt")) retItem.거래상황 = item.useAt;
          return retItem;
        
      });
      this.excelEvent(excelList ,`보증료관리`+new Date().toISOString().substr(0, 10) ,'세금계산서' );
   },
    searchEvent(){
      this.payload.mberName = '';
      this.payload.usid = '';
      this.payload.bizNum = '';

      if(this.option1 === '판매자성명') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === '아이디') { 
        this.payload.usid = this.textVal1;
      }
      else if(this.option1 === '사업자번호') { 
        this.payload.bizNum = this.textVal1;
      }

      if(this.option2 === '판매자성명') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === '아이디') { 
        this.payload.usid = this.textVal1;
      }
      else if(this.option2 === '사업자번호') { 
        this.payload.bizNum = this.textVal1;
      }
    
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);

      this.fetch_guarant_list(retPayload);
    },

  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>