import notice from "../api/notice";

const noticeStore = {
  namespaced: true,
  state : {
    noticeList : [],
    pgFeeList : []
  },
  getters : {},
  mutations : {
    set_notice_list(state , list){
      state.noticeList = list;
    },
    set_pg_fee_list(state , list){
      state.pgFeeList = list;
    },

  },
  actions : {
    async fetch_notice_list({commit}){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await notice.noticeList();
        await commit('set_notice_list' , list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    async fetch_pg_fee_list({commit}){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await notice.pgFeeList();
        await commit('set_pg_fee_list' , list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },


  }
}

export default noticeStore;