<!--세금계산서발급 -->
<template>
  <v-container class="mt-2">
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">세금계산서발급</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=12 xl=2>
          <v-autocomplete
            class=" pl-2 pr-2 pt-1"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=1>
          <div class="caption pl-2 pt-3">
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=1>
          <div class="caption pl-2 pt-3">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>

        
      </v-row>
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark

        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions} from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '세금계산서발급';
    this.$store.state.overLay = false;
  },
  computed : {
    list(){
      try {
        const list = this.$store.state.taxStore.taxIssueList.map(item=>{
          item.splpc1 = currency(item.splpc1);
          item.vat = currency(item.vat);
          item.tot = currency(item.tot);
          item.cnt = currency(item.cnt);
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }

    }
  },
  data() {
    return {
      headers : [
        {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '주민번호', value:'mberJumi', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '사업자번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '주소', value:'adres', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '이메일', value:'email', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '공급가액', value:'splpc1', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '세액', value:'vat', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '합계', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '건수', value:'cnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       
      ],

      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        bizCode : "", // 대리점코드
      },
    }
  },
  methods: {
    ...mapActions('taxStore',[
      'fetch_taxissue_list',
    ]),
    excelDown(){
      const excelList = this.list.map(item =>{
        const retItem = {};
        
          if(item.hasOwnProperty("usid")) retItem.판매자ID = item.usid;
          if(item.hasOwnProperty("mberJumi")) retItem.주민번호 = item.mberJumi;
          if(item.hasOwnProperty("mberName")) retItem.성명 = item.mberName;
          if(item.hasOwnProperty("bizNum")) retItem.사업자번호 = item.bizNum;
          if(item.hasOwnProperty("companyName")) retItem.상호 = item.companyName;
          if(item.hasOwnProperty("adres")) retItem.주소 = item.adres;
          if(item.hasOwnProperty("email")) retItem.이메일 = item.email ;
          if(item.hasOwnProperty("splpc1")) retItem.공급가액 = item.splpc1;
          if(item.hasOwnProperty("vat")) retItem.세액 = item.vat;
          if(item.hasOwnProperty("tot")) retItem.합계 = item.tot;
          if(item.hasOwnProperty("cnt")) retItem.건수 = item.cnt;
          return retItem;
        
      });
      const wscols = [{wpx:90},{wpx:100},{wpx:50},{wpx:80},{wpx:150},{wpx:200},{wpx:80},{wpx:60} ,{wpx:60} ,{wpx:50} ,{wpx:50} ]
      this.excelEvent(excelList ,`세금계산서발급-${this.payload.dateStart}-${this.payload.dateEnd}` ,'세금계산서' ,wscols);
   
   },
    searchEvent(){
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_taxissue_list(retPayload);
    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>