import trans from "../api/trans";

const transStore = {
  namespaced: true,
  state : {
    dayList : [],// 일자별 거래내역목록
    transList : [] // 거래 취소내역
  },
  getters : {},
  mutations : {
    set_day_list(state , list){
      state.dayList = list;
    },

    set_trans_list(state , list){
      state.transList = list;
    }
  },
  actions : {    
    // 달력용 전체 매출용
    async fetch_day_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await trans.transByDate(payload);
        await commit('set_day_list' ,list);

      } catch (error) {
        console.error(error);
        throw err;
      }finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 거래 / 취소내역좋로
    async fetch_trans_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        let list = await trans.transListSearch(payload);
        await commit('set_trans_list' , list);
        return;
      } catch (error) {
        console.error(error);
        await commit('SET_ALERT_DIALOG',{ flag : true , message : '조회중 문제가 발생했습니다.  고객센터에 문의바랍니다.' , title : '오류' , type : 'error'}, { root: true });
        throw err;
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
  }
}

export default transStore;