<template>
    <v-dialog
      v-model="dialog"
      max-width="600"
      persistent
    >

    <v-card >
      
      <v-app-bar
        color="deep-purple darken-3"
        dense
        dark
      
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>판매자일괄등록</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
        <v-sheet 
          class="overflow-y-auto"
          max-height="900">
        <v-card >
           <v-card-text>
             <v-container>
               <v-row> 
                <v-col cols=12>
                  <v-file-input
                    dense
                    v-model="fileData"
                    color="deep-purple accent-4"
                    class=""
                    label="판매자일괄등록"
                    @change="readFile" 
                    placeholder="등록 서식 파일에 내용을 작성후 업로드하세요"
                    prepend-icon="mdi-paperclip"
                    outlined

                  >
                    <template v-slot:selection="{ index, text }">
                      <v-chip
                        v-if="index < 2"
                        color="deep-purple accent-4"
                        dark
                        label
                        small
                      >
                        {{ text }}
                      </v-chip>

                      <span
                        v-else-if="index === 2"
                        class="overline grey--text text--darken-3 mx-2"
                      >
                        +{{ files.length - 2 }} File(s)
                      </span>
                    </template>
                  </v-file-input>
                </v-col>
                <v-col cols=12>
                  <v-btn block href="/download/bizpay_user.xls" download>
                    등록 서식파일 다운로드
                  </v-btn>
                </v-col>
               </v-row>
             </v-container>
           </v-card-text>

        </v-card>
        </v-sheet>
        <v-divider></v-divider>
        <v-card-actions>
        <v-btn
          color="deep-purple dark-2"
          :style="{'color':'#fff'}"       
          block
          @click="saveEvent"
        >
          입력
        </v-btn>
      </v-card-actions>
    </v-card>

  </v-dialog>

</template>

<script>
import { mapActions, mapMutations } from 'vuex';
import commInfo from "@/mixins/comInfo";
import excel from "@/mixins/comInfo";
import XLSX from 'xlsx';
import api from "@/api/agencyManage.js";
export default {
  mixins: [commInfo,excel],
  props : ['dialog'],

  created() {
    //this.$refs.bizrno.hint='';
  },
  computed: {
  },
  data() {
    return {
      fileData : null,
      payLoadArr : [],
    }
  },
  methods: {

    ...mapMutations(['SET_ALERT_DIALOG']),
    readFile(event){
      console.log(event);
      //const file = event.target.files[0];
    },
    saveEvent(){
      this.payLoadArr = [];
      let reader = new FileReader();
      reader.readAsBinaryString(this.fileData);
      reader.onload= (evt) =>{
        let data = evt.target.result;
        const workbook = XLSX.read(data, {
          type: 'binary'
        });
        
        workbook.SheetNames.forEach(sheet=>{  
          const rowArr = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
          rowArr.forEach(item =>{
            const retItem = {};
            for (const [key, value] of Object.entries(item)) {
              if(key === '1일한도') retItem.limitDay = value;
              if(key === '1회한도') retItem.limitOne = value;
              if(key === 'E-MAIL주소') retItem.email = value;
              if(key === '계좌번호') retItem.accountNo = value;
              if(key === '년한도') retItem.limitYear = value;
              if(key === '사업자구분') retItem.bizType = value;
              if(key === '사업자명') retItem.companyName = value;
              if(key === '사업자번호') retItem.bizNum = value;
              if(key === '사업자번호(주민번호)') retItem.mberJumi = value;
              if(key === '상호(성명)') retItem.mberName = value;
              if(key === '수수료율(%)') retItem.feeRate = value;
              if(key === '업종아이디') retItem.indutyId = value;
              if(key === '예금주') retItem.depositor = value;
              if(key === '월한도') retItem.limitMonth = value;
              if(key === '은행명') retItem.bankName = value;
              if(key === '주소') retItem.adres = value;
              if(key === '할부개월') retItem.installmentMonths = value;
              if(key === '휴대폰번호') retItem.mberMobile = value;
              if(!!retItem.mberMobile&&retItem.mberMobile.length >4){
                retItem.mbtlnum = String(retItem.memberMberCode.slice(-4));
              }else {
                retItem.mbtlnum = String("0000");
              }
              retItem.memberMberCode = this.GET_AUTH_MEMBER_CODE;
              retItem.memberBizeCode = this.GET_AUTH_BIZCODE;
            }
          this.payLoadArr.push(retItem);
          });
        })
        console.log(this.payLoadArr);
        api.insertSeller(this.payLoadArr)
        .then(()=>{
          this.$emit('dialogFileEvent' , false);
        })
        .catch(error=>{
          this.SET_ALERT_DIALOG({flag : true , message :'등록에 실패했습니다. 등록엑셀파일내용이 정확한지 확인해주세요.'});
        })
      }

  
      
    },

    closeEvent(){
      this.$emit('dialogFileEvent' , false);
    },
  }
}
</script>

<style>

</style>