<template>
  <div class="text-center">
    <v-pagination
      circle
      v-model="page"
      :length="totPage"
      :total-visible="5"
      @input="inputEvent"
    ></v-pagination>
  </div>
</template>

<script>
export default {
  props :['size' , 'total' , 'page'],

  computed: {
    totPage(){
      return Math.ceil(this.total / this.size)
    }
  },
  updated() {
    console.log('업데이트');

  },
  mounted() {
    console.log('마운트');
  },

  methods: {
    inputEvent(val){
      this.$emit('pageEvent' , val);
    }
  },
}
</script>

