import XLSX from 'xlsx';
import * as api from "../api/index";
export const excel = {
  methods: {
  /**
   * 엑셀파일생성 데이터는 배열일경우 - 헤더명을 객체 속성명이 아닌 별도이름을 사용하고자 할때는 배열을 이용해야 한다. 내부에서 데이터 속성이름과 데이터를 강제로 맟추는 기능이존재
   * 
   * @param {Array : string} list 데이터 
   * @param {Array : string} wscols 각 셀 넓이
   * @param {Array : string} headers 헤더 값
   * @returns {boolean} 성공 여부
   */
  excelArrayEvent(list , fileName ,sheetName ,wscols , headers){
    try {
      if(!!headers){
        list.unshift(headers);
      }

      let dataWs = XLSX.utils.aoa_to_sheet(list);
      const wb = XLSX.utils.book_new();
      if(!!wscols){
        dataWs["!cols"] = wscols;
      }
      
      XLSX.utils.book_append_sheet(wb, dataWs, sheetName);
      XLSX.writeFile(wb, `${fileName}.xlsx`);
      return true;
    } catch (error) {
      console.error(error);
      return false     
    }
  },

    /**
   * 엑셀파일 생성 1개의 시트만
   * @param {Array : string} list 데이터 
   * @param {string} fileName 엑셀파일이름
   * @param {string} sheetName 씨트이름
   * @param {Array : string} wscols 각 셀 넓이
   * @returns {boolean} 성공 여부
   */
    excelEvent(list , fileName , sheetName , wscols ){
      try {
        let dataWs = {};
        dataWs = XLSX.utils.json_to_sheet(list);
        dataWs["!cols"] = wscols;
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, dataWs, sheetName);
        XLSX.writeFile(wb, `${fileName}.xlsx`);
        return true
      } catch (error) {
        console.error(error);
        return false
      }
    },

  /**
   * 엑셀파일 1줄간격등을 변경해서
   * @param {Array : string} list 데이터 
   * @param {string} fileName 엑셀파일이름
   * @param {string} sheetName 씨트이름
   * @returns {boolean} 성공 여부
   */
  excelRowEvent(list , fileName , sheetName ){
    try {
      let dataWs = XLSX.utils.json_to_sheet(list);
      let wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, dataWs, sheetName);
      XLSX.writeFile(wb, `${fileName}.xlsx`);
      return true
    } catch (error) {
      console.error(error);
      return false
    }
  },


    /**
   * 각 페이지별 엑셀용 데이터 추출을 위한 호출부
   * @param {string} callType 필요로 하는 api 유형
   * @param {Object} payload 검색용파라미터
   * @returns {boolean} 성공 여부
   */
    async excelApiCallEvent(callType , payload){
      let list = [];
      payload.startIndex = 0;
      payload.endIndex = 0;   
      switch (callType) {
        case 'atm': // 입출력내역
          list = await api.InqireAtm.list(payload);
          break;
        case 'delng':// 거래조회
          list = await api.InqireDelng.list(payload);
        default:
          break;
      }
      return list;
    },
  },
}