import {login , reLogin} from "../api/auth";
import router from "../router"

const authStore = {
  namespaced: true,
  state:{
    memberInfo : null, // 사용자정보
    //dealerList : [{bizCode:"all",cmpnm:"전체",trgetBizCode:"",grade:""}], // 딜러목록
    dealerList : [], // 딜러목록
    bizInfo : null, // 사업자정보
  },
  getters:{
    // 사용자별 이름은 존재하지 않는다. 조심!!!! 구조문제
    GET_USERNAME(state){
      if(!!state.memberInfo)return state.memberInfo.idntfcNm;
      else return false;
    },
    GET_CMPNM(state){
      if(!!state.memberInfo)return state.memberInfo.cmpnm;
      else return false;
    },
    GET_AUTH_BIZCODE(state){
      if(!!state.memberInfo)return state.memberInfo.bizCode;
      else return '0000002';// 임시로 완성전에 사용중인 부분 !!!!! 꼭 null 로 변경필요
    },
    GET_AUTH_MEMBER_CODE(state){
      if(!!state.memberInfo)return state.memberInfo.mberCode;
      else return '1303130010';
    },
    GET_MEMBER_GRADE(state){
      if(!!state.memberInfo)return state.memberInfo.grade;
      else 'NORMAL';
    }

  },
  mutations:{
    SET_MEMEBER_INFO(state, info){
      state.memberInfo = info;
    },
    SET_DEALER_LIST(state, list){
      state.dealerList = list;
      // 전체를 표현하기 위해 임의이 값을 넣는것이다. 등급이다 타켓값은 무의미 bizCode ="" 은 전체를 뜻한다.
      const addObject = {
        bizCode: "",
        cmpnm: "전체",
        grade: "ALL",
        trgetBizCode: "0000000",
      }
      state.dealerList.unshift(addObject);
      //state.dealerList = state.dealerList.slice(0,1);
      //state.dealerList = state.dealerList.concat(list);
    },
    SET_BIZ_INFO(state, info){
      state.bizInfo = info;
    },
    LOG_OUT(state){
      state.memberInfo = null;
      state.dealerList = [];
      state.bizInfo = null;
    }
  },
  actions:{
    async FETCH_MEMBER_INFO({commit} , payload){
      try {
        const data = await login(payload);
        await commit('SET_MEMEBER_INFO', data.member);
        await commit('SET_DEALER_LIST', data.dealerList);
        await commit('SET_BIZ_INFO', data.biz);
        return data.authkey;
      } catch (err) {
        
        router.push("/main");
        commit('SET_ALERT_DIALOG',{ flag : true , message : err.message , title : '오류' , type : 'error'}, { root: true });
        throw err;

      }
    },
    FETCH_RELOGIN({commit} , payload ){
      reLogin(payload)
      .then(data=>{
        commit('SET_MEMEBER_INFO', data.member);
        commit('SET_DEALER_LIST', data.dealerList);
        commit('SET_BIZ_INFO', data.biz);
      }).then(()=>{
        if(!!localStorage.rtPath){
          router.push(localStorage.rtPath);
        }else router.push("/");
      })
    }
  }
}

export default authStore;