import axios from 'axios'
import { DOMAIN } from "../Constants";

//로그인
const login = (payload) =>{
  return axios.post(DOMAIN+'/auth/login',payload)
  .then((res)=>{
    return res.data;
  }) 
  .catch( res=>{
    throw res.response.data;
  });
}
// 재로그인 -- 인증키가 유효한지 검사후 유효하면 강제 로그인 시킨다.
const reLogin = (payload) =>{
  return axios.post(DOMAIN+'/auth/relogin',{},{
    headers : {
      userId : payload.userId,
      authkey : payload.authkey
    }
  }).then(res=>{
    return res.data;
  }).catch(res=>{
    throw res.response.data;
  });
}
// id 체크
const idChk = usid =>{
  return axios.get(DOMAIN+`/auth/idChk/${usid}`)
    .then(res=>{
      return res.data;
    }).catch(res=>{
    throw res.response.data;
  });
}
// 사업자 번호 체크
const bizrnoChk = bizno =>{
  return axios.get(DOMAIN+`/auth/biznoChk/${bizno}`)
    .then(res=>{
      return res.data;
    }).catch(res=>{
    throw res.response.data;
  });
}

export {
  login,
  reLogin,
  idChk,
  bizrnoChk
}