<template>
  <v-dialog
    v-model="dialog"
    persistent
    max-width="1200px"
  >
  <v-form ref="form" v-model="valid">
    <v-card class="overflow-hidden">
      <v-app-bar
        color="blue"
        dense
        dark
        absolute
        elevate-on-scroll
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn small  :disabled="!valid" color="primary" @click="saveEvent">
          <v-icon left>mdi-content-save</v-icon>
          저장
        </v-btn>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-sheet max-height="600" class="overflow-y-auto ma-10">
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">사업자정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="대리점ID"
                filled
                v-model="info.dealerId"
                clearable
                @click:append="dealerIdCheck"
                :hint="dealerValidMessage"
                :append-icon="dealerValid ? 'mdi-emoticon-excited-outline' : 'mdi-emoticon-sad-outline' "
              >
              
              </v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="소속대리점"
                :items="affilateDealerList"
                item-text="cmpnm"
                item-value="bizCode"
                v-model="info.trgetBizCode" 
                outlined
                class="pt-2"
                dense
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="추천대리점"
                :items="dealerList"
                item-text="cmpnm"
                item-value="bizCode"
                v-model="info.recommendBizCode" 
                outlined
                dense
                class="pt-2"
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="구분"
                :items="gubun"
                item-text="text"
                item-value="val"
                outlined
                class="pt-2"
                dense
                v-model="info.bizType"
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="대리점구분"
                :items="dealerGubun"
                item-text="text"
                item-value="val"
                v-model="info.dealerKind"
                outlined
                dense
                class="pt-2"
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="가맹비"
                filled
                :value= 'info.joinAmt|currency'
                @input="val=>info.joinAmt=val"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="상호"
                filled
                v-model="info.cmpnm"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자번호"
                filled
                :value= 'info.bizrno|bizNum'
                @input="value=>info.bizrno=value"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="대표자명"
                filled
                v-model="info.bprprr"
              ></v-text-field>
            </v-col>
            
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="전화번호"
                filled
                v-model="info.bizTelno"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="핸드폰번호"
                filled
                v-model="info.mTelno"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="E-MAIL"
                filled
                v-model="info.email"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field
                dense  
                label="소재지(주소)"
                filled
                v-model="info.bplc"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">계좌정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="은행명"
                filled
                v-model="info.bankName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="계좌번호"
                filled
                v-model="info.bankSerial"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="예금주"
                filled
                v-model="info.bankUser"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">설정</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="대리점수익률"
                filled
                v-model="info.dealerRate"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="판매자수수료율"
                filled
                v-model="info.memberRate"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="판매자등록허용"
                :items="memberInsert"
                item-text="text"
                item-value="val"
                v-model="info.memberInputYn"
                outlined
                class="pt-2"
                dense
              ></v-select>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">대리점정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="로그인ID"
                filled
                v-model="info.usid"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="로그인비밀번호"
                filled
                placeholder="입력시 비밀번호가 수정됩니다."
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="거래상황"
                :items="useAt"
                item-text="text"
                item-value="val"
                outlined
                class="pt-2"
                dense
                v-model="info.useAt"
              ></v-select>
            </v-col>
          </v-row>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-textarea
                dense  
                label="메모"
                filled
                background-color="lime lighten-4"
                v-model="info.memo"
              ></v-textarea>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-textarea
                dense  
                label="수정이력"
                filled
                background-color="lime lighten-4"
                v-model="info.historyMemo"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">터미널정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
               <v-text-field
                label="VAN터미널ID"
                v-model="info.trmnlNo"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
               <v-text-field
                label="PG터미널ID"
                filled
                dense
                v-model="info.pgTrmnlNo"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="PG/VAN"
                :items="pgVan"
                item-text="text"
                item-value="val"
                outlined
                class="pt-2"
                dense
                v-model="info.pgVan"
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-select
                label="PG사"
                :items="pgChoice"
                item-text="text"
                item-value="val"
                outlined
                class="pt-2"
                dense
                v-model="info.vanGb"
                
              ></v-select>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="최종수정일자"
                filled
                v-model="info.dt"
                readonly
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">결제사정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="결제사대표자명"
                filled
                v-model="info.payBprprr"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="결제사대표전화번호"
                filled
                v-model="info.payTelno"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="결제사회사명"
                filled
                v-model="info.payCmpnm"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="결제사사업자번호"
                filled
                v-model="info.payBizrno"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="결제사주소"
                filled
                v-model="info.payBplc"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">한도정보</span>
        </v-card-title>
        <v-card-text>
          <v-row dense>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="개인 1회 한도"
                filled
                :value= 'info.limitOne|currency'
                @input="value=>info.limitOne=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="개인 1일 한도"
                filled
                :value= 'info.limitDay|currency'
                @input="value=>info.limitDay=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="개인 월 한도"
                filled
                :value= 'info.limitMonth|currency'
                @input="value=>info.limitMonth=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="개인 년 한도"
                filled
                :value= 'info.limitYear|currency'
                @input="value=>info.limitYear=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="개인 할부개월"
                filled
                v-model="info.installmentMonths"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자 1회 한도"
                filled
                :value= 'info.bizLimitOne|currency'
                @input="value=>info.bizLimitOne=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자 1일 한도"
                filled
                :value= 'info.bizLimitDay|currency'
                @input="value=>info.bizLimitDay=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자 월 한도"
                filled
                :value= 'info.bizLimitMonth|currency'
                @input="value=>info.bizLimitMonth=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자 년 한도"
                filled
                :value= 'info.bizLimitYear|currency'
                @input="value=>info.bizLimitYear=value"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
            <v-col cols="12" xs="12" sm="6" md="4" >
              <v-text-field
                dense  
                label="사업자 할부개월"
                filled
                v-model="info.bizInstallmentMonths"
                :rules="currencyRule"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
      </v-sheet>
    </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
import { InqireDealer } from "../api/index";
import commInfo from "@/mixins/comInfo";
import { mapActions } from 'vuex'
export default {
props :['dialog' , 'item' , 'title'],
mixins: [commInfo],
data() {
  return {
    valid : true,
    items1 : [
      {text : '대리점1' , val : '112'},
      {text : '대리점2' , val : '1122'},
      {text : '대리점3' , val : '1132'},
    ],
    gubun : [
      {text : '개인' , val : 'N'},
      {text : '사업자' , val : 'Y'},    
    ],
    dealerGubun : [
      {text : '총판' , val : 32},
      {text : '대리점' , val : 33},
      {text : '딜러' , val : 34},
    ],
    memberInsert : [
      {text : '허용' , val : 1},
      {text : '불가' , val : 0},
    ],
    useAt : [
      {text : '거래중' , val : 'Y'},
      {text : '거래중지' , val : 'N'},
    ],
    pgVan : [
      {text : 'PG' , val : 'PG'},
      {text : 'VAN' , val : 'VAN'},
    ],
    pgChoice : [
      {text : 'KSNET' , val : 'KSNET'},
      {text : 'DANAL' , val : 'DANAL'},
      {text : 'LGU+' , val : 'LGU'},
    ],
    currencyRule : [
      val => {
        val = val+",";
        val = val.replace(/,/g,"");

        if(/[^0-9]/g.test(val)) return '숫자만 입력가능합니다.';
      }
    ],
    dealerRule : [
      val => this.dealerValid || this.dealerValidMessage
    ],
    dealerValid : false ,
    dealerValidMessage : '아이디중복유무 확인하세요'
  }
},
computed: {
  info(){
    console.log(1111);
    if(!!this.$store.state.bpwStore.dealerRegInfo) return this.$store.state.bpwStore.dealerRegInfo;
    return null
  },
  affilateDealerList(){
    let returnArr = [];
    
    if(this.$store.state.authStore.memberInfo.authorCode!=="AUTHOR_MNGR"){
      // 딜러로 로그인 된것만 계산한다.
      // returnArr = this.$store.state.authStore.dealerList.filter(item=>{
      //   return (item.grade == 'DISTRIBUTOR' || item.grade == 'AGENCY' ) && item.trgetBizCode;
      // });
      return returnArr;
    }
    if(this.$store.state.bpwStore.dealerRegInfo.dealerKind == 31 ) returnArr = [];
    if(this.$store.state.bpwStore.dealerRegInfo.dealerKind == 32) returnArr = [];
    if(this.$store.state.bpwStore.dealerRegInfo.dealerKind == 33) {
      returnArr = this.$store.state.authStore.dealerList.filter(item=>{
        return item.bizCode == this.$store.state.bpwStore.dealerRegInfo.bizCode && item.grade == 'DISTRIBUTOR';
      });
    }
    if(this.$store.state.bpwStore.dealerRegInfo.dealerKind == 34){
      returnArr = this.$store.state.authStore.dealerList.filter(item=>{
        return item.bizCode == this.$store.state.bpwStore.dealerRegInfo.bizCode&&(item.grade == 'DISTRIBUTOR' || item.grade == 'AGENCY') ;
      });
      
    }
    
    return returnArr;
  },

},
methods: {
  ...mapActions('bpwStore',['modify_dealer']),
  dealerIdCheck(){
    if(!this.info.dealerId) return;
    if(!!this.info.dealerId) {
      InqireDealer.dealerIdCheck(this.info.dealerId)
      .then(data =>{
        if(data==='ok'){
          this.dealerValidMessage = "사용가능한 아이디";
          return this.dealerValid = true;
        }else{
          this.dealerValidMessage = "이미 사용중인 아이디";
          return this.dealerValid = false;
        }
      })
      
    }
    else {
      this.dealerValidMessage = "중복아이디";
      return this.dealerValid = false;
    }
  },
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
    saveEvent(){
      this.$refs.form.validate();
      let saveInfo = {};
      // 필터처리후 콤파가 데이터 자체에 삽입되는 경우가 발생함
      if(typeof(this.info.joinAmt) === 'string'){
        this.info.joinAmt = this.info.joinAmt.replace(/,/g,"");
        this.info.joinAmt = Number(this.info.joinAmt);
      }
      if(typeof(this.info.limitOne) === 'string'){
        this.info.limitOne = this.info.limitOne.replace(/,/g,"");
        this.info.limitOne = Number(this.info.limitOne);
      }
      if(typeof(this.info.limitDay) === 'string'){
        this.info.limitDay = this.info.limitDay.replace(/,/g,"");
        this.info.limitDay = Number(this.info.limitDay);
      }
      if(typeof(this.info.limitMonth) === 'string'){
        this.info.limitMonth = this.info.limitMonth.limitMonth(/,/g,"");
        this.info.limitMonth = Number(this.limitMonth.limitMonth);
      }
      if(typeof(this.info.limitYear) === 'string'){
        this.info.limitYear = this.info.limitYear.replace(/,/g,"");
        this.info.limitYear = Number(this.info.limitYear);
      }
      if(typeof(this.info.installmentMonths) === 'string'){
        this.info.installmentMonths = this.info.installmentMonths.replace(/,/g,"");
        this.info.installmentMonths = Number(this.info.installmentMonths);
      }
      if(typeof(this.info.bizLimitOne) === 'string'){
        this.info.bizLimitOne = this.info.bizLimitOne.replace(/,/g,"");
        this.info.bizLimitOne = Number(this.info.bizLimitOne);
      }
      if(typeof(this.info.bizLimitDay) === 'string'){
        this.info.bizLimitDay = this.info.bizLimitDay.replace(/,/g,"");
        this.info.bizLimitDay = Number(this.info.bizLimitDay);
      }
      if(typeof(this.info.bizLimitMonth) === 'string'){
        this.info.bizLimitMonth = this.info.bizLimitMonth.replace(/,/g,"");
        this.info.bizLimitMonth = Number(this.info.bizLimitMonth);
      }
      if(typeof(this.info.bizLimitYear) === 'string'){
        this.info.bizLimitYear = this.info.bizLimitYear.replace(/,/g,"");
        this.info.bizLimitYear = Number(this.info.bizLimitYear);
      }
      if(typeof(this.info.bizInstallmentMonths) === 'string'){
        this.info.bizInstallmentMonths = this.info.bizInstallmentMonths.replace(/,/g,"");
        this.info.bizInstallmentMonths = Number(this.info.bizInstallmentMonths);
      }
      

      // 수정
      if(this.title == '대리점상세정보'){
        this.modify_dealer(this.info)
        .then(()=>{
          
          this.$emit('search')
        })
      }
      // 입력
