<template>
  <v-dialog
    v-model="infodialog"
    persistent
    max-width="1400"
    max-height="500"
  >
    <v-form
      ref="form"
      v-model="valid"
    > 
    <v-card>
      <v-app-bar
        color="deep-purple darken-3"
        dense
        dark
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
        <v-card-title>
          <v-icon>mdi-home-import-outline</v-icon>
          <span class="">판매자정보</span>
          </v-card-title>
          <v-divider></v-divider>
           <v-card-text>
             <v-container>
               <v-row dense> 
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="cmpnm"
                    item-value="bizCode"
                    v-model="item.bizCode"
                    :items="this.dealerAgencyList"
                    label="소속대리점" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.dealerId"
                    label="소속대리점ID"
                    filled
                    disabled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.indutyId"
                    label="로그인ID"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.calculateType"
                    :items="this.calculateList"
                    label="정산" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    v-model="item.pgVan"
                    :items="this.pgVanList"
                    label="PG/VAN" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="cmpnm"
                    item-value="bizCode"
                    v-model="item.pgGb"
                    :items="this.pgList"
                    label="PG사" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.pgTrmnlNo"
                    label="PG터미널ID"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.pgSugiTrmnlNo"
                    label="PG수기결제터미널ID"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.setDevice"
                    :items="this.deviceList"
                    label="장비설정" 
                    outlined
                  ></v-select>
                </v-col>
                <!-- 장비설정2 값확인 필요 존재해야 하는지부터 -->
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.setDevice2"
                    :items="this.deviceList"
                    label="장비설정2" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
