                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizNum"
                    label="사업자번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizTypeName"
                    label="업종"
