                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizItem"
                    label="취급품목"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.mberName"
                    label="성명"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.mberMobile"
                    label="휴대폰번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.mberPhone"
                    label="대표전화번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.email"
                    label="이메일주소"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.adres"
                    label="주소"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.mberJumi"
                    label="주민번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bankName"
                    label="저장된은행명"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="name"
                    item-value="code"
                    v-model="item.bankCode"
                    :items="this.bankCodeList"
                    label="새로운은행명선택" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.accountNo"
                    label="계좌번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.depositor"
                    label="예금주"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.atmStop"
                    :items="this.atmStopList"
                    label="출금정지" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.feeBank"
                    :items="this.feeBankList"
                    label="이체수수료" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <!-- 소스상 사용자의 등급에 따라 강제 하드코딩됨 입력 수정에는 존재하지만 불러올때는 하드코딩된 값으로 대체됨
                    HEADOFFICE : 3.52 그외 : 2.97
                   -->
                  <v-text-field
                    dense
                    
                    :value = "this.본사수수료"
                    label="본사수수료"
                    filled
                  >
                  
                  </v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="cmpnm"
                    item-value="bizCode"
                    v-model="item.recommendBizCode"
                    :items="this.dealerAgencyList"
                    label="추천대리점" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    disabled
                    v-model="item.recommendDealerRate"
                    label="추천대리점수수료"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.payType"
                    :items="this.회원수정_pay_type"
                    label="코드(정산방식)" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                  disabled
                    dense
                    v-model="회원수정_pay_type_수수료율"
                    label="정산수수료"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="oneLimitVal"
                    :items="oneLimit"
                    label="1회한도" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    disabled
                    v-model="회원수정1회한도수수료율"
                    label="1회한도수수료"
                    filled
                  ></v-text-field>
                </v-col>
                <!-- 수익률 수수료율 관련 부분 / 하드코딩이나 강제 값 제공 로직이 존재하므로 수정만들때 다시한다. -->
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    disabled
                    v-model="item.feeDistributor"
                    label="총판수익률"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.feeAgency"
                    label="대리점수익률"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.feeDealer"
                    label="딜러수익률"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.feeRate"
                    label="판매자수수료율"
                    filled
                  >
                    <template v-slot:append-outer>
                      <v-btn
                      
                      color="deep-purple dark-2"
                      :style="{'color':'#fff'}"       
                      block
                      @click="calEvent"
                      
                    >
                      계산하기
                    </v-btn>
                    </template>
                  </v-text-field>
                  
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.documentReg"
                    :items="this.회원등록서류제출"
                    label="서류제출" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitOne | currency "
                    @change="value=>item.joinlimitOnemt = value"
                    label="1회한도"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitDay | currency "
                    @change="value=>item.limitDay = value"
                    label="1일한도"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitMonth | currency "
                    @change="value=>item.limitMonth = value"
                    label="월한도"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitYear | currency "
                    @change="value=>item.limitYear = value"
                    label="년한도"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.installmentMonths"
                    label="할부개월"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.useAt"
                    :items="this.dealerGuraList"
                    label="거래상황" 
                    outlined
                  ></v-select>
                </v-col>

                </v-row>
                <v-row dense> 
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-textarea
                    name="input-7-1"
                    filled
                    label="메모"
                    auto-grow
                    v-model="item.memo" 
                  ></v-textarea>
                </v-col>
                <v-col cols="12" xs="12" sm="6" md="4" xl="3" >
                  <v-textarea
                    name="input-7-1"
                    filled
                    label="수정이력"
                    auto-grow
                    v-model="item.historyMemo" 
                  ></v-textarea>
                </v-col>                
               </v-row>
             </v-container>
           </v-card-text>
           <v-divider></v-divider>
        <v-card-actions>
        <v-btn
          color="deep-purple dark-2"
          :style="{'color':'#fff'}"       
          block
          @click="saveEvent"
          :disabled="!valid"
        >
          {{btnName}}
        </v-btn>
      </v-card-actions>
          </v-card>
     

  </v-form>
  </v-dialog>

</template>

<script>
import { mapActions , mapMutations} from 'vuex';
import commInfo from "@/mixins/comInfo";
import { idChk , bizrnoChk} from "../api/auth";
import {currency , removeCharObject , commaReplace} from "@/util/filter.js";
import api from "@/api/agencyManage.js";
export default {
  props :['infodialog' , 'item' , 'preItem' , 'title'],
  mixins: [commInfo],
  created() {
    //this.$refs.bizrno.hint='';
  },
  computed: {
    btnName(){
      if(this.title==='대리점신규') return '신규저장';
      else return '수정저장';
    },
    본사수수료(){
      if(!this.$store.state.authStore.memberInfo) return '2.97';
      else {
        if(this.$store.state.authStore.memberInfo.grade === 'HEADOFFICE') {
          //return '3.52';
          return '3.33';
        }
      }
      return '2.97';
    },
    회원수정_pay_type_수수료율(){
      if(this.item.payType === 'T' ||  this.item.payType === 'B'){
        if(this.item.dealerKind === 31) {
          return 1.1;
        }else {
          return 0.77;
        }
      }
      return 0;
    },
    회원수정1회한도수수료율(){
      if(this.oneLimitVal===500000) return 0;
      else return 0.55;
    },
  },
  data() {
    return {
      //recommendDealerRate : 0.33,
      valid: false,
      requireRules: [
        v => !!v || '위항목을 입력해주셔야 합니다.' ,    
      ],
      // 향후 벨리데이션 체크 상세화 할때 진행한다.
      bizrnoRules : [
        v => !!v || '위항목을 입력해주셔야 합니다.' ,
      ],
      // 사업자유무
      dealerbizList : [
        {text : '사업자' , val : 'Y'},
        {text : '개인' , val : 'N'}
      ],
      dealerKindList : [
        {text : '본사' , val : "31"},
        {text : '총판' , val : "32"},
        {text : '대리점' , val : "33"},
        {text : '딜러' , val : "34"},
      ],
      // 거래중
      dealerGuraList : [
        {text : '거래중' , val : "Y"},
        {text : '거래중지' , val : "N"},
      ],
      memberInputYnList : [
        {text : '등록불가능' , val : "0"},
        {text : '등록가능' , val : "1"},
      ],
      // 출금상태
      atmStopList : [
        {text : '출금정지' , val : '1'},
        {text : '출금가능' , val : '0'}],
      // 이체수수료 : 
      feeBankList :[
        {text : '무료' , val : 'F'},
        {text : '유료' , val : 'P'},
      ],
      // 직거래상태
      calculateList : [
        {text : '전체' , val : ''},
        {text : '유니코아' , val : 'U'},
        {text : 'KSNET직정산' , val : 'K'}],
      // 장비목록
      deviceList : [
        {text : '설정안함' , val : null},
        {text : 'SC500(이어폰)' , val : 'SC500'},
        {text : 'SC500B(블루투스)' , val : 'SC500B'},
        {text : '7201(다날)' , val : '7201'},
        {text : '7602(다날)' , val : '7602'},
        {text : '7203(다날)' , val : '7203'},
        {text : '7211(다날)' , val : '7211'},
        {text : 'Q2' , val : 'Q2'}],
        // 수기
      sugiList : [
        {text : '허용' , val : 'Y'},
        {text : '허용안함' , val : 'N'}],
      //각종설정유무
      suljungList : [
        {text : '설정함' , val : 'Y'},
        {text : '설정안함' , val : 'N'}],
      // pg /van
      pgVanList : [
        'VAN',
        'PG',
      ],
      // pg사
      pgList : [
        'KSNET',
        'LGU',
        'DANAL',
      ],
      oneLimit : [
        {text : '50만원까지' , val : 500000},
        {text : '150만원까지' , val : 1500000},
        {text : '300만원까지' , val : 3000000},
      ],
      oneLimitVal : 500000,
      회원수정_pay_type : [
        {text : '5일입금' , val : 'N'},
        {text : '익일입금' , val : 'T'},
        {text : '바로정산' , val : 'B'},
      ],
      회원등록서류제출 : [
        {text : '제출완료' , val : 'O'},
        {text : '미제출' , val : 'X'},
      ]
    }
  },
  methods: {
    ...mapActions('agencyManageStore',['modify_seller']),
    ...mapMutations(['SET_ALERT_DIALOG']),
    calEvent(){
      let val = 0.0;
      let 회원수정_추천대리점_수수료율 = 0.33;
      
      //let 회원수정_대리점_수수료율 = 0.0;
      //let 회원수정_딜러_수수료율 = 0.0;
      if(!!this.본사수수료) val = val + Number(this.본사수수료);
      if(!!회원수정_추천대리점_수수료율) val = val + Number(회원수정_추천대리점_수수료율);
      if(!!this.회원수정_pay_type_수수료율) val = val + Number(this.회원수정_pay_type_수수료율);
      if(!!this.회원수정1회한도수수료율) val = val + Number(this.회원수정1회한도수수료율);
      if(!!this.item.feeDistributor) val = val + Number(this.item.feeDistributor);
      if(!!this.item.feeAgency) val = val + Number(this.item.feeAgency);
      if(!!this.item.feeDealer) val = val + Number(this.item.feeDealer);
      val = val.toFixed(2);

      this.item.feeRate = val;
    },
    validate () {
      this.$refs.form.validate()
    },
    reset () {
      this.$refs.form.reset()
    },
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
    saveEvent(){
      this.item.prevTarget = this.preItem.bizCode;
      this.item.prevDealerKind = this.preItem.dealerKind;
      this.item.memberBizeCode = this.memberBizCode;
      this.item.memberMberCode = this.GET_AUTH_MEMBER_CODE; /// ~~~~~~@!@@@@@@@ 여기가 잘못됨
      //this.item.grade = this.GET_MEMBER_GRADE;
      this.item.grade = 'HEADOFFICE';
      this.item.unicoreRate = Number(this.본사수수료);
      //!!!!!!!!!!!!!!!!!!! recommendDealerRate -- 추천대리점 수수료 확인!!!!!!!!!!!!!!!!!!!
      this.item.codeRate = this.회원수정_pay_type_수수료율;
      this.item.oneLimitRate = this.회원수정1회한도수수료율;
      this.item.lv1Rate = this.item.feeDistributor;
      this.item.lv2Rate = this.item.feeAgency;
      this.item.lv3Rate = this.item.feeDealer;
      const retPayload = removeCharObject(this.item , ['joinAmt','limitOne','limitDay','limitMonth','limitYear','bizLimitOne','bizLimitDay','bizLimitMonth','bizLimitYear']);
      if(this.title==='대리점신규'){
        const arr = [];
        arr.push(retPayload);
        api.insertSeller(arr)
        .then(()=>this.$emit('dialogEvent' , false))
        .catch(error=>{
          this.SET_ALERT_DIALOG({flag : true , message :'등록에 실패했습니다.'});
        })
      }
      else {
        this.modify_seller(retPayload)
        .then(()=>this.$emit('dialogEvent' , false))
        .catch(error=>{
          this.SET_ALERT_DIALOG({flag : true , message :'등록에 실패했습니다.'});
        })
      }

    },
    async idChkEvent(){
      this.$refs.loginId.hint = '';
      let temp = await idChk(this.item.usid);
      if(temp >0) {
        this.valid = false;
        this.$refs.loginId.hint ="이미 사용중인 아이디입니다.";
      }
      else this.$refs.loginId.hint ="사용가능한 아이디 입니다.";
    },
    async biznoChkEvent(){
      try {
        this.$refs.bizrno.hint = '';
        if(!this.item.bizrno) return;
        let temp = await bizrnoChk(this.item.bizrno);

        if(temp >0) {
          this.valid = false;
          this.$refs.bizrno.hint ="이미사용중인 사업자번호입니다.";
        }
        else this.$refs.bizrno.hint ="사용가능한 아이디 입니다.";
      } catch (error) {
        console.error(error);
      }
      
    }
  },

  
}
</script>

