<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      persistent 
      max-width="700px"
    >
    <v-card
      class="mx-auto"
    >
      <v-app-bar
        color="blue"
        dense
        dark
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>대리점관리</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-container>
        <v-row dense>    
          <v-col cols="12" >
            <v-select
              label="거래상태"
              :items="items1"
              item-text="text"
              item-value="val"
              v-model="inoutCode"
              outlined
              class="mb-4"
            ></v-select>
            </v-col>
            <v-col class="d-flex" cols="12" xs="12" sm="12" md="6" lg="6" xl="6">
            <v-select  
              class="mr-4"
              :items="items2"
              item-text="text"
              item-value="val"
              v-model="selected1"
              label="조회조건선택"
              @change="selected1Change"
            ></v-select>
            <v-text-field
              outlined
    
              v-model="selected1Val"
              :readonly="selected1Read"
              ref="selected1Ref"
            ></v-text-field>
          </v-col>
          
          <v-col class="d-flex" cols="12" xs="12" sm="12" md="6" lg="6" xl="6">
            <v-select
              class="mr-4"
              :items="items2"
              item-text="text"
              item-value="val"
              v-model="selected2"
              label="조회조건선택"
                       
              @change="selected2Change"
     
            ></v-select>
            <v-text-field
              v-model="selected2Val"
              outlined
              
              :readonly="selected2Read"
              ref="selected2Ref"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row dense>
          <v-col cols="12">
            <v-card-actions >
              <v-spacer></v-spacer>
              <v-btn
                color="blue-grey"
                tile
                @click="searchEvent"
                dark
              >
                <v-icon left>mdi-magnify</v-icon> 조회
              </v-btn>
            
            </v-card-actions>
          </v-col>
        </v-row>
      </v-container>       
    </v-card>
    </v-dialog>
   </v-row>
</template>

<script>
import { mapMutations } from 'vuex'
import commInfo from "@/mixins/comInfo";
export default {
  props : ['dialog', 'startIndex', 'endIndex'],
  mixins: [commInfo],
  data() {
    return {
      payload : {},
      items1 : [
        {text : '전체' , val : ''},
        {text : '거래중' , val : 'Y'},
        {text : '거래중지' , val : 'N'}
      ],
      items2 : [
        {text : '전체' , val : ''},
        {text : '아이디' , val : 'dealerId'},
        {text : '상호' , val : 'cmpnm'},
        {text : '사업자번호' , val : 'bizrno'},
      ],
      selected1 : '',
      selected2 : '',
      selected1Val : '',
      selected2Val : '',
      selected1Read : true,
      selected2Read : true
    }
  },
  methods: {
    ...mapMutations([
       'SET_ALERT_DIALOG'
		]),
    searchEvent(){
      this.payload = {};


      this.payload.mberName = this.mberName;
      this.payload.indutyId = this.indutyId;
			this.payload.memberBizeCode = this.memberBizCode;
      this.payload.startIndex = 0;
      this.payload.endIndex = 20;
      this.payload.inoutCode = this.inoutCode;
      
      this.$emit('dialogEvent' , false);
      // 첫번째 검색항목
      if(this.selected1!=='전체' && this.selected1Val!==''){
        switch (this.selected1) {
          case 'dealerId':
            this.payload.dealerId = this.selected1Val;
            break;
          case 'cmpnm':
            this.payload.cmpnm = this.selected1Val;
            break;
          case 'bizrno':
            this.payload.bizrno = this.selected1Val;
            break;
          default:
            break;
        }
      }

      // 두번째 검색항목 첫번째 항목과 같은 경우 덮어쓰기 한다.
      if(this.selected2!=='전체' && this.selected2Val!==''){
        switch (this.selected2) {
          case 'dealerId':
            this.payload.dealerId = this.selected1Val;
            break;
          case 'cmpnm':
            this.payload.cmpnm = this.selected1Val;
            break;
          case 'bizrno':
            this.payload.bizrno = this.selected1Val;
            break;
          default:
            break;
        }
      }
      this.$emit('searchEvent' , this.payload);
    },
    closeEvent(){
      console.log("다이알로그창");
      this.$emit('dialogEvent' , false);
    },

    // 검색선택후 값입력 창으로 포커스 이동
    selected1Change(val){
      if(val!=='') {
        this.selected1Read=false;
        this.$refs.selected1Ref.focus();
      }
      else this.selected1Read=true;
    },
    selected2Change(val){
      if(val!=='') {
        this.selected2Read=false;
        this.$refs.selected2Ref.focus();
      }
      else this.selected2Read=true;
    }
  },
}
</script>

