<!--정산용엑셀 -->
<template>
  <v-container class="mt-3">
    
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">정산용엑셀</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
       <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=4 xs=6 sm=2 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=4 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=4 sm=2 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=4 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
       
      </v-row>
      <v-row>
        <v-col cols=4 xs=4 sm=2 md=2 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option1"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=4 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal1"
             v-model="textVal1"
           ></v-text-field>
         </v-col>
         <v-col cols=4 xs=4 md=2 sm=2 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option2"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=4 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal2"
             v-model="textVal2"
           ></v-text-field>
         </v-col>

         <v-col cols=12>
                <v-select
                  class="caption pl-2 pr-2"
                  dense
                  item-text="cmpnm"
                  item-value="bizCode"
                  v-model="payload.bizCode"
                  :items="this.dealerList"
                  label="대리점목록" 
                  outlined
                >
                </v-select>
              </v-col>
      </v-row>
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      <template v-slot:[`item.tot`]="{ item }">
        {{ item.tot | currency}} 원
      </template>
      <template v-slot:[`item.cardFee`]="{ item }">
        {{ item.cardFee | currency}} 원
      </template>
      <template v-slot:[`item.selerPymntamt`]="{ item }">
        {{ item.selerPymntamt | currency}} 원
      </template>
      <template v-slot:[`item.feeErn`]="{ item }">
        {{ item.feeErn | currency}} 원
      </template>
      <template v-slot:[`item.feeDistributor`]="{ item }">
        {{ item.feeDistributor | currency}} 원
      </template>
      <template v-slot:[`item.feeAgency`]="{ item }">
        {{ item.feeAgency | currency}} 원
      </template>
      <template v-slot:[`item.feeDealer`]="{ item }">
        {{ item.feeDealer | currency}} 원
      </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '정산용엑셀';
    this.$store.state.overLay = false;
  },
  computed : {
    list(){
      return this.$store.state.accountStore.accountExcelList.map( item =>{
        if(!!item.feeErn) {
          // 서버 쿼리문의  feeErn 의 값을 사용하는 것이 아니라 직접 계산한다.
          item.feeErn = item.tot - item.selerPymntamt - item.cardFee - item.feeDistributor - item.feeAgency - item.feeDealer;
        }
        return item;
      }

      )
      //return this.$store.state.accountStore.accountExcelList;
    }
  },
  data() {
    return {
      headers : [
        {text: '소속', value:'cmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '총판', value:'distributor', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '대리점', value:'agent', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '거래일', value:'confmDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '승인번호', value:'confmNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '코드', value:'payType', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자아이디', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '사업자등록번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: 'VAN터미널ID', value:'trmnlNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: 'PG터미널ID', value:'pgTrmnlNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: 'VAN/PG', value:'vanPgComp', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '카드사', value:'issueCmpnyNm', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '카드종류', value:'cardType', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '승인금액', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '카드수수료', value:'cardFee', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자지급액', value:'selerPymntamt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '수수료수익', value:'feeErn', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '총판지급액', value:'feeDistributor', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '대리점지급액', value:'feeAgency', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '딜러지급액', value:'feeDealer', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        inoutCode : "", // 거래구분
        indutyId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
      },
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mberName'},
        {text : '판매자아이디', val : 'indutyId'},
        {text : '사업자번호', val : 'bizNum'},
        {text : '상호', val : 'companyName'},
      ],
    }
  },
  methods: {
    ...mapActions('accountStore',[
      'fetch_account_excel_list',
    ]),
    excelDown(){
      const excelList = this.list.map(item =>{
        const retItem = {};
        if(item.hasOwnProperty("cmpnm")) retItem.소속 = item.cmpnm;
        if(item.hasOwnProperty("distributor")) retItem.총판 = item.distributor;
        if(item.hasOwnProperty("agent")) retItem.대리점 = item.agent;
        if(item.hasOwnProperty("confmDt")) retItem.거래일 = item.confmDt;
        if(item.hasOwnProperty("confmNo")) retItem.승인번호 = item.confmNo;
        if(item.hasOwnProperty("payType")) retItem.코드 = item.payType;
        if(item.hasOwnProperty("usid")) retItem.판매자아이디 = item.usid ;
        if(item.hasOwnProperty("mberName")) retItem.판매자성명 = item.mberName;
        if(item.hasOwnProperty("companyName")) retItem.상호 = item.companyName;
        if(item.hasOwnProperty("bizNum")) retItem.사업자등록번호 = item.bizNum;
        if(item.hasOwnProperty("trmnlNo")) retItem.VAN터미널ID = item.trmnlNo;
        if(item.hasOwnProperty("pgTrmnlNo")) retItem.PG터미널ID = item.pgTrmnlNo;
        if(item.hasOwnProperty("vanPgComp")) retItem.VANPG = item.vanPgComp;
        if(item.hasOwnProperty("issueCmpnyNm")) retItem.카드사 = item.issueCmpnyNm;
        if(item.hasOwnProperty("cardType")) retItem.카드종류 = item.cardType;
        if(item.hasOwnProperty("tot")) retItem.승인금액 = currency(item.tot);
        if(item.hasOwnProperty("cardFee")) retItem.카드수수료 = currency(item.cardFee);
        if(item.hasOwnProperty("selerPymntamt")) retItem.판매자지급액 = currency(item.selerPymntamt);
        if(item.hasOwnProperty("feeErn")) retItem.수수료수익 = currency(item.feeErn);
        if(item.hasOwnProperty("feeDistributor")) retItem.총판지급액 = currency(item.feeDistributor);
        if(item.hasOwnProperty("feeAgency")) retItem.대리점지급액 = currency(item.feeAgency);
        if(item.hasOwnProperty("feeDealer")) retItem.딜러지급액 = currency(item.feeDealer);
        
        return retItem;
      });

      const wscols = [];
      wscols.push({wpx:100});
      wscols.push({wpx:100});
      wscols.push({wpx:100});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:80});
      wscols.push({wpx:60});
      wscols.push({wpx:120});
      wscols.push({wpx:100});
      wscols.push({wpx:80});
      wscols.push({wpx:80});
      wscols.push({wpx:60});
      wscols.push({wpx:110});
      wscols.push({wpx:50});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});
      wscols.push({wpx:70});

      this.excelEvent(excelList ,`정산용엑셀-${this.payload.dateStart}-${this.payload.dateEnd}` , '정산용엑셀' ,wscols );
    },
    searchEvent(){
      this.payload.mberName = '';
      this.payload.indutyId= '';
      this.payload.bizNum= '';
      this.payload.companyName= '';
      if(this.option1 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'companyName') {
        this.payload.companyName = this.textVal1;
      }

      if(this.option2 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option2 === 'companyName') {
        this.payload.companyName = this.textVal1;
      }
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_account_excel_list(retPayload);
    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>