<!-- 입출력내역관리 -->
<template>
  <v-container>
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">입출력내역관리</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
       <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="dialogEvent(true)">
          출금정지
        </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=12 xs=12 sm=6 md=6 lg=3 xl=2>
          <v-autocomplete
            class=" pl-2 pr-2 pt-1"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
        <v-col cols=12 xs=12 sm=6 md=6 lg=3 xl=2>
          <v-select
            class=" pl-2 pr-2 pt-1"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.inoutCode"
            :items="this.inOutList"
            label="입출력내역관리" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option1"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=2>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal1"
             v-model="textVal1"
           ></v-text-field>
         </v-col>
         <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option2"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=2>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal2"
             v-model="textVal2"
           ></v-text-field>
         </v-col>
      </v-row >
      
    </v-card>
    <v-row>
      <v-col>
    
      <v-alert
        text
        prominent
        type="success"
        icon="mdi-math-compass"
        border="left"  
      >
          입금 : 총 {{sumInfo.inCnt | currency}} 건 {{sumInfo.inSum | currency}} 원
         , 출금 :  총 {{sumInfo.outCnt | currency}} 건 {{sumInfo.outSum | currency}} 원
         , 매출취소 : 총 {{sumInfo.cancelCnt | currency}} 건 {{sumInfo.cancelSum | currency}} 원
      </v-alert>

      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      <template v-slot:[`item.reqAmt`]="{ item }">
        {{ item.reqAmt | currency}} 원
      </template>
      <template v-slot:[`item.charge`]="{ item }">
        {{ item.charge | currency}} 원
      </template>
      <template v-slot:[`item.balance`]="{ item }">
        {{ item.balance | currency}} 원
      </template>
      </v-data-table>
      </v-col>
    </v-row>
    <InOutSetting :dialog='dialog' @dialogEvent='dialogEvent'></InOutSetting>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
import InOutSetting from "@/components/InOutSetting";
export default {
  created() {
    document.title = '입출력내역관리';
    this.$store.state.overLay = false;
  },
  components : {
    InOutSetting
  },


  mixins: [excel , commInfo],
  computed : {
    list(){
      const retList = this.$store.state.accountStore.accountList.map(item=>{
        let retItem = {...item};
        if(item.inoutCode.substr(0,2)==='OT') retItem.reqAmt = item.reqAmt *-1;
        if(item.inoutCode === 'IN') retItem.inoutCode = '입금';
        else if(item.inoutCode === 'IN_DAILY') retItem.inoutCode = '입금';
        else if(item.inoutCode === 'IN_PAID') retItem.inoutCode = '본사정산입금';
        else if(item.inoutCode === 'IN_PAID_M') itretItemem.inoutCode = '관리비대납완료';
        else if(item.inoutCode === 'IN_PAID_T') retItem.inoutCode = '통신비대납완료';
        else if(item.inoutCode === 'OT') retItem.inoutCode = '출금';
        else if(item.inoutCode === 'OT_SF')  retItem.inoutCode = '관리비출금';
        else if(item.inoutCode === 'OT_TF') retItem.inoutCode = '통신비';
        else if(item.inoutCode === 'OT_CANCEL') retItem.inoutCode = '매출취소';
        else if(item.inoutCode === 'OT_UC') retItem.inoutCode = '본사출금';
        else if(item.inoutCode.substr(0,2)==='IN') retItem.inoutCode = '입금';
        else if(item.inoutCode.substr(0,2)==='OT') retItem.inoutCode = '출금';
        return retItem;
      })

      return retList;
    },
    sumInfo(){
      let retObject = {
        inCnt : 0,
        inSum : 0 ,
        outCnt : 0,
        outSum :0,
        cancelCnt : 0,
        cancelSum : 0,
      }
      this.$store.state.accountStore.accountList.forEach(item => {
        console.log(item.inoutCode);
        if(item.inoutCode.substr(0,2)==='IN') {
          retObject.inCnt = Number(retObject.inCnt) + Number(1);
          retObject.inSum = Number(retObject.inSum) + Number(item.reqAmt);
        }
        if(item.inoutCode === 'OT') {
          retObject.outCnt = Number(retObject.outCnt) + Number(1);
          retObject.outSum = Number(retObject.outSum) + Number(item.reqAmt);

        } 
        else if(item.inoutCode === 'OT_SF') {
          retObject.outCnt = Number(retObject.outCnt) + Number(1);
          retObject.outSum = Number(retObject.outSum) + Number(item.reqAmt);

        }
        else if(item.inoutCode === 'OT_TF') {
          retObject.outCnt = Number(retObject.outCnt) + Number(1);
          retObject.outSum = Number(retObject.outSum) + Number(item.reqAmt);

        }
        else if(item.inoutCode === 'OT_UC') {
          retObject.outCnt = Number(retObject.outCnt) + Number(1);
          retObject.outSum = Number(retObject.outSum) + Number(item.reqAmt);

        }
        else if(item.inoutCode === 'OT_CANCEL') {
          retObject.cancelCnt = Number(retObject.cancelCnt) + Number(1);
          retObject.cancelSum = Number(retObject.cancelSum) + Number(item.reqAmt);

        }
        
    });

        return retObject
  }

  },
  data() {
    return {
      textVal1 : '',
      textVal2 : '',
      dateMenu1 : '',
      dateMenu2 : '',
      option1 : '',
      option2 :'',
      dialog : false,
      headers : [
        {text: '대리점', value:'cmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '사업자번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자ID', value:'indutyId', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자이름', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '거래일시', value:'reqDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '구분', value:'inoutCode', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '입출금액', value:'reqAmt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '출금수수료', value:'charge', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '잔액', value:'balance', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '은행', value:'bank', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '계좌번호', value:'account', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '예금주', value:'depositor', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        inoutCode : "", // 거래구분
        indutyId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
      },
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mberName'},
        {text : '판매자아이디', val : 'indutyId'},
        {text : '사업자번호', val : 'bizNum'},
        {text : '상호', val : 'companyName'},
      ],
      // 입출력내역관리 검색구분
      inOutList : [
        {val : '', text : '전체'},
        {val : 'IN', text : '입금'},
        {val : 'IN_PAID', text : '본사정산입금'},
        {val : 'OT', text : '출금'},
        {val : 'OT_CANCEL', text : '매출취소'},
        {val : 'OT_UC', text : '본사출금'},

      ]

    }
  },
  methods: {
    ...mapActions('accountStore',[
      'fetch_account_inout_list',
    ]),

    dialogEvent(val){
      this.dialog = val;
    },
    searchEvent(){
      this.payload.mberName = '';
      this.payload.indutyId= '';
      this.payload.bizNum= '';
      this.payload.companyName= '';
      if(this.option1 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'companyName') {
        this.payload.companyName = this.textVal1;
      }

      if(this.option2 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option2 === 'companyName') {
        this.payload.companyName = this.textVal1;
      }
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_account_inout_list(retPayload);
    },
    excelDown(){
      try {
        this.$store.state.mainLoading = true;
        const wscols = []
        const exList = this.list.map(item=>{
            const retItem = {};
            if(item.hasOwnProperty("cmpnm")) {
              retItem.대리점 = item.cmpnm;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("companyName")) {
              retItem.상호 = item.companyName;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("bizNum")) {
              retItem.사업자번호 = item.bizNum;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("indutyId")) {
              retItem.판매자ID = item.indutyId;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("mberName")) {
              retItem.판매자성명 = item.mberName;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("reqDt")) {
              retItem.거래일시 = item.reqDt;
              wscols.push({wpx:120});
            }
            if(item.hasOwnProperty("inoutCode")) {
              retItem.구분 = item.inoutCode;
              wscols.push({wpx:50});
            }
            if(item.hasOwnProperty("confmNo")) {
              retItem.승인번호 = item.confmNo;
              wscols.push({wpx:20});
            }
            if(item.hasOwnProperty("payType")) {
              retItem.코드 = item.payType;
              wscols.push({wpx:20});
            }
            if(item.hasOwnProperty("reqAmt")) {
              retItem.입출금액 = currency(item.reqAmt);
              wscols.push({wpx:70});
            }
            if(item.hasOwnProperty("charge")) {
              retItem.출금수수료 = currency(item.charge);
              wscols.push({wpx:70});
            }
            if(item.hasOwnProperty("balance")) {
              retItem.잔액 = currency(item.balance);
              wscols.push({wpx:70});
            }
            if(item.hasOwnProperty("bank")) {
              retItem.은행 = item.bank;
              wscols.push({wpx:70});
            }
            if(item.hasOwnProperty("account")) {
              retItem.계좌번호 = item.account;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("depositor")) {
              retItem.예금주 = item.depositor;
              wscols.push({wpx:70});
            }
            
           
            return retItem;
          });      
    
          this.excelEvent(exList ,`입출금내역관리-${this.payload.dateStart}-${this.payload.dateEnd}` ,'입출금' ,wscols);

      } catch (error) {
        console.error(error);
      } finally{
        this.$store.state.mainLoading = false;
      }
    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>