<template>
    <v-dialog
      v-model="infodialog"
      persistent
      max-width="1200"
    >
    <v-form
    ref="form"
    v-model="valid"
    
  >
    <v-card >
      <v-app-bar
        color="deep-purple darken-3"
        dense
        dark
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
        <v-sheet 
          class="overflow-y-auto"
          max-height="600"
          >
        <v-card >
          <v-card-title>
            <v-icon>mdi-home-import-outline</v-icon>
            <span class="">사업자정보</span>
          </v-card-title>
          <v-divider></v-divider>
           <v-card-text>
             <v-container>
               <v-row> 
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.dealerId"
                    label="대리점ID"
                    disabled
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="cmpnm"
                    item-value="bizCode"
                    v-model="item.trgetBizCode"
                    :items="settingAgencyList"
                    label="소속대리점" 
                    outlined
          
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="cmpnm"
                    item-value="bizCode"
                    v-model="item.recommendBizCode"
                    :items="this.dealerList"
                    label="추천대리점" 
                    outlined
                    
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.bizType"
                    :items="dealerbizList"
                    label="구분(개인/사업자)" 
                    outlined
                    required
                    :rules="requireRules"
                  ></v-select>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.dealerKind"
                    :items="dealerKindList"
                    label="대리점구분" 
                    outlined
                    required
                    :rules="requireRules"
                  ></v-select>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.joinAmt | currency "
                    @change="value=>item.joinAmt = value"
                    label="가맹비"
                    filled
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.cmpnm"
                    label="상호"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizrno"
                    ref="bizrno"
                    label="사업자번호"
                    filled
                    hint=""
                    persistent-hint
                    clearable
                    @click:append-outer="biznoChkEvent"
                    append-outer-icon="mdi-cursor-default-click"
 
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bprprr"
                    label="대표자명"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bplc"
                    label="소재지(주소)"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizTelno"
                    label="전화번호"
                    filled
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.mTelno"            
                    label="핸드폰번호"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.email"   
                    label="이메일"
                    filled
                  ></v-text-field>
                </v-col>
               </v-row>
             </v-container>
           </v-card-text>
          
    
          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">계좌정보</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bankName"  
                    label="은행명"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bankSerial" 
                    label="계좌번호"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bankUser" 
                    label="예금주"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>


          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">설정</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.dealerRate" 
                    label="대리점수익률"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.memberRate" 
                    label="판매자수수료율"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="item.memberInputYn"
                    :items="memberInputYnList"
                    label="판매자등록허옹" 
                    outlined
                    required
                    :rules="requireRules"
                  ></v-select>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>


          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">대리점정보</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    ref="loginId"
                    dense
                    type="text"
                    v-model="item.usid" 
                    label="로그인ID"
                    hint=""
                    persistent-hint
                    filled
                    clearable
                    required
                    :rules="requireRules"
                    @click:append-outer="idChkEvent"
                    :append-outer-icon="!item.mberId ? 'mdi-cursor-default-click' : ''"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.password" 
                    label="로그인비밀번호"
                    filled
                    required
                    :rules="requireRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    item-text="text"
                    item-value="val"
 
                    v-model="item.useAt"
                    :items="dealerGuraList"
                    label="거래상황" 
                    outlined
                    required
                    :rules="requireRules"
                  ></v-select>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3"  v-if="this.title!=='대리점신규'" >
                  <v-textarea
                    name="input-7-1"
                    filled
                    label="메모"
                    auto-grow
                    v-model="item.memo" 
                  ></v-textarea>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" v-if="this.title!=='대리점신규'">
                  <v-textarea
                    name="input-7-2"
                    filled
                    label="수정이력"
                    auto-grow
                    v-model="item.historyMemo" 
                  ></v-textarea>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>


          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">터미널정보</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.historyMemo" 
                    label="VA터미널ID"
                    filled
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.pgTrmnlNo" 
                    label="PG터미널ID"
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    v-model="item.pgVan"
                    :items="pgList"
                    label="PG/VAN" 
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-select
                    class="caption pt-2"
                    dense
                    v-model="item.pgGb" 
                    :items="vanList"
           
                    label="PG사"
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.dt" 
                    label="최종수정일자"
                    filled
                  ></v-text-field>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>


          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">결자사정보</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.payBprprr" 
                    label="결제사대표자명"
                    filled
           
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.payTelno" 
                    label="결제사대표전화번호"
                    filled
        
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.payCmpnm" 
                    label="결제사회사명"
                    filled
 
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.payBizrno" 
                    label="결제사사업자번호"
                    filled
            
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.payBplc" 
                    label="결제사주소"
                    filled
             
                  ></v-text-field>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>


          <v-card-title color="#26c6da">
            <v-icon>mdi-home-group</v-icon>
            <span class="title font-weight-light">한도정보</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
             <v-container>
               <v-row>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitOne | currency"
                    @change="value=>item.limitOne=value"
                    required
                    label="개인1회 한도"
                    filled
             
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitDay | currency"
                    @change="value=>item.limitDay=value"
                    label="개인1일 한도"
                    filled
                   
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitMonth | currency"
                    @change="value=>item.limitMonth=value"
                    label="개인 월 한도"
                    filled
              
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.limitYear | currency"
                    @change="value=>item.limitYear=value"
                    label="개인 년 한도"
                    filled
                
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.installmentMonths"
                    label="개인 할부개월"
                    filled
                 
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.bizLimitOne | currency"
                    @change="value=>item.bizLimitOne=value"
                    label="사업자 1회 한도"
                    filled
                
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.bizLimitDay | currency"
                    @change="value=>item.bizLimitDay=value"
                    label="사업자 1일 한도"
                    filled
             
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.bizLimitMonth | currency"
                    @change="value=>item.bizLimitMonth"
                    label="사업자 월 한도"
                    filled
                  
                  ></v-text-field>
                </v-col>
                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    :value="item.bizLimitYear | currency"
                    @change="value=>item.bizLimitYear=value"
                    label="사업자 년 한도"
                    filled
              
                  ></v-text-field>
                </v-col>

                <v-col cols="12" xs="12" sm="4" md="4" xl="3" >
                  <v-text-field
                    dense
                    v-model="item.bizInstallmentMonths"
                    label="사업자 할부개월"
                    filled
               
                  ></v-text-field>
                </v-col>
              </v-row>
             </v-container>
          </v-card-text>
        </v-card>
        </v-sheet>
        <v-divider></v-divider>
        <v-card-actions>
        <v-btn
          color="deep-purple dark-2"
          :style="{'color':'#fff'}"       
          block
          @click="saveEvent"
          v-if="title!=='대리점신규'"
 
        >
          수정
        </v-btn>
        <v-btn
          color="deep-purple dark-2"
          :style="{'color':'#fff'}"       
          block
          @click="saveEvent"
          v-else
        >
          입력
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-form>
  </v-dialog>

</template>

<script>
import { mapActions , mapState} from 'vuex';
import commInfo from "@/mixins/comInfo";
import { idChk , bizrnoChk} from "../api/auth";
import {currency , removeCharObject , commaReplace} from "@/util/filter.js";
export default {
  props :['infodialog' , 'item' , 'preItem' , 'title'],
  mixins: [commInfo],
  mounted() {
   // this.$refs.bizrno.hint='';
    
  },


  computed : {
    settingAgencyList(){
      if(this.title !== '대리점신규'){
        const retList = this.$store.state.agencyManageStore.settingAgencyList.map(item =>{
          let retItem = {cmpnm : '' , bizCode : ''};
          retItem.bizCode = item.BIZ_CODE;
          retItem.cmpnm = item.CMPNM;
          return retItem;
        })

        return retList
      }else return this.dealerList;
    
    }
      //...mapState('agencyManageStore',['agencyKindList' , 'settingAgencyList']),
    //return item.trgetBizCode
  },

  data() {
    return {

      valid: true,
      requireRules: [
        v => !!v || '위항목을 입력해주셔야 합니다.' ,
        
      ],
      // 향후 벨리데이션 체크 상세화 할때 진행한다.
      bizrnoRules : [
        v => !!v || '위항목을 입력해주셔야 합니다.' ,
      ],
      // 사업자유무
      dealerbizList : [
        {text : '사업자' , val : 'Y'},
        {text : '개인' , val : 'N'}
      ],
      dealerKindList : [
        {text : '본사' , val : "31"},
        {text : '총판' , val : "32"},
        {text : '대리점' , val : "33"},
        {text : '딜러' , val : "34"},
      ],
      dealerGuraList : [
        {text : '거래중' , val : "Y"},
        {text : '거래중지' , val : "N"},
      ],
      memberInputYnList : [
        {text : '등록불가능' , val : "0"},
        {text : '등록가능' , val : "1"},
      ],
      pgList : ['PG' , 'VAN' ],
      vanList : ['KSNET' , 'KIS', 'KOVAN' ],
      // 결제사 정보내역
      initLimitInfo : {

      }
    }
  },
  methods: {
    ...mapActions('agencyManageStore',
      ['modify_agency_list',
      'insert_agency']
    ),
    validate () {
      this.$refs.form.validate()
    },
    reset () {
      this.$refs.form.reset()
    },
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
    saveEvent(){
      this.item.prevTarget = this.preItem.bizCode;
      this.item.prevDealerKind = this.preItem.dealerKind;
      this.item.memberBizeCode = this.memberBizCode;
      this.item.creatMberCode = this.GET_AUTH_MEMBER_CODE;
      this.item.grade = this.GET_AUTH_MEMBER_CODE;

      const retPayload = removeCharObject(this.item , ['joinAmt','limitOne','limitDay','limitMonth','limitYear','bizLimitOne','bizLimitDay','bizLimitMonth','bizLimitYear']);
      if(this.title !== '대리점신규'){ 
        this.modify_agency_list(retPayload)
        .then((data)=>{
         console.log(data);
        this.$emit('reSearchEvent') 
        })

      }else this.insert_agency(retPayload)
      .then((data)=>{
        console.log(data);
        this.$emit('reSearchEvent') ;
      })
      .then(()=>this.closeEvent())
      

    },
    async idChkEvent(){
      this.$refs.loginId.hint = '';
      let temp = await idChk(this.item.usid);
      if(temp >0) {
        this.valid = false;
        this.$refs.loginId.hint ="이미 사용중인 아이디입니다.";
      }
      else this.$refs.loginId.hint ="사용가능한 아이디 입니다.";
    },
    async biznoChkEvent(){
      try {
        this.$refs.bizrno.hint = '';
        if(!this.item.bizrno) return;
        let temp = await bizrnoChk(this.item.bizrno);

        if(temp >0) {
          this.valid = false;
          this.$refs.bizrno.hint ="이미사용중 입니다.";
        }
        else this.$refs.bizrno.hint ="사용가능합니다.";
      } catch (error) {
        console.error(error);
      }
      
    }
  },

  
}
</script>

<style>

</style>