<template>
  <v-dialog
    v-model="dialog"
    max-width="500"
  >
    <v-card
      class="mx-auto"
    >
      <v-toolbar
        color="indigo darken-2"
        cards
        dark
        flat
      >
        <v-btn icon>
          <v-icon>mdi-alert-rhombus-outline</v-icon>
        </v-btn>
        <v-card-title class="title">출금정지설정</v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-form
        ref="form"
        v-model="form"
        class="pa-4 pt-6"
      >
      <v-textarea
        v-model="atmConfig.msg"
        auto-grow
        filled
        color="cyan"
        background-color="green lighten-5"
        label="판매자 전달 메세지"
        rows="1"
      ></v-textarea>
      </v-form>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-switch 
          dense
          v-model="atmConfigSwitchFlag" 
          inset 
          @change="changeEvent"
          :label="`${atmConfigSwitchInfo}`">
        </v-switch>
      </v-card-actions>
    </v-card> 
   </v-dialog>
</template>

<script>
import { mapActions , mapMutations ,mapGetters ,mapState } from 'vuex'
export default {
  props : ['dialog'],
  data() {
    return {
      message : "",
      switch1 : this.atmConfig.enableYn,
      form : ''
    }
  },
  computed: {
    ...mapGetters('bpwStore',[
      'atmConfigSwitchInfo',
      'atmConfigSwitchFlag'
    ]),
    ...mapState('bpwStore',[
      'atmConfig'
    ]),
  },
  updated() {
    if(!this.atmConfig.enableYn){
      this.fetch_atm_config_info();
    }
  },
  methods: {
    ...mapActions('bpwStore' , ['fetch_atm_config_info' , 'modify_atm_config_info']),

    changeEvent(){
      if(this.atmConfig.enableYn === 'N')this.atmConfig.enableYn = 'Y';
      else this.atmConfig.enableYn = 'N';
      this.atmConfig.dbCode = "000"
      
      this.modify_atm_config_info(this.atmConfig);
    },
    closeEvent(){
      this.$emit('atmStop' , false);
    }
  },

}
</script>

