import tax from "../api/tax";

const taxStore = {
  namespaced: true,
  state : {
    taxIssueList : [],
    taxReportList : [],
    guarantList : [],
  },
  getters : {},
  mutations : {
    set_taxissue_list(state , list){
      state.taxIssueList = list;
    },
    set_taxreport_List(state , list){
      state.taxReportList = list;
    },
    set_guarant_List(state , list){
      state.guarantList = list;
    },
  },
  actions : {
    async fetch_taxissue_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await tax.taxIssueList(payload);
        await commit('set_taxissue_list' , list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    async fetch_taxreport_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await tax.taxReportList(payload);
        await commit('set_taxreport_List' , list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    async fetch_guarant_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await tax.guarantList(payload);
        await commit('set_guarant_List' , list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },

  }
}

export default taxStore;