import { request } from "./index";
// 세금등 금융관련 부분
const tax = {
  taxIssueList(payload){
    return request('post' ,'/tax/taxIssueList' , payload);
  },
  // 세무소 목록
  taxReportList(payload){
    return request('post' ,'/tax/taxReportList' , payload);
  },
  // 보증료 목록
  guarantList(payload){
    return request('post' ,'/tax/guarantList' , payload);
  },
}

export default tax;