<!-- 대리점별매출화면 -->
<template>
    <v-container>
      <v-row dense>
        <v-col cols=12>
           <v-card dark color="deep-purple darken-3">
             <v-app-bar dense class="deep-purple lighten-1 white--text">
               <span class="subtitle-1 font-weight-black">{{title}}</span>
               <v-spacer></v-spacer>
               <v-divider light vertical inset></v-divider>
               <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
                조회
              </v-btn>
              <v-divider light vertical inset></v-divider>
              <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
                엑셀
              </v-btn>
             </v-app-bar>
             <v-row dense>
               <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색시작일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateStart" 
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateStart" 
                    locale="ko"
                    no-title
                    :max= 'this.dateStartMax'
                    @input="dateMenu1 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색종료일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateEnd"
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateEnd" 
                    locale="ko"
                    no-title
                    :max='new Date().toISOString().substr(0, 10)'
                    @change="dateEndEvent"
                    @input="dateMenu2 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  수익상태
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="payload.type"
                  :items="typeList"
                  label="대리점매출 수익" 
                  single-line
                  @change="typeChange"
                >
                </v-select>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  대리점목록
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                 <v-autocomplete
                  class="caption pr-2"
                  :items="this.dealerList"
                  label="직접입력 & 목록선택"
                  outlined
                  dense
                  item-text="cmpnm"
                  item-value="bizCode"
                  v-model="payload.bizCode"
                >
                  <template v-slot:no-data>
                    대리점이 존재하지 않습니다
                  </template>
                </v-autocomplete>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2 v-if='this.payload.type===1'>
                <div class="caption pl-3 pt-1">
                  딜러포함유무
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4 v-if='this.payload.type===1'>
                <v-select
                  class="caption pr-2"
                  :style="{fontSize:'5px'}"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="payload.widthMber"
                  :items="this.관리자_대리점별매출_소속포함"
                  
                  single-line
                >
                </v-select>
              </v-col>
             </v-row>
             <v-row dense>
                <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option1"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal1"
                    v-model="textVal1"
                  ></v-text-field>
                </v-col>
                <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                
                  ></v-text-field>
                </v-col>
              </v-row>
           </v-card>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols=12>
          <v-alert
            v-if="this.payload.type===1"
            text
            prominent
            color="#2A3B4D"
            icon="mdi-math-compass"
            border="left"
            
          >
              총건수 : {{sumInfo.totCount  | currency}} 건
            , 총판수익 : {{sumInfo.sum1  | currency}} 원 
            , 대리점수익 : {{sumInfo.sum2  | currency}} 원
            , 딜러수익 : {{sumInfo.sum3  | currency}} 원
          </v-alert>

          <v-alert
            v-if="this.payload.type===2"
            text
            prominent
            color="#2A3B4D"
            icon="mdi-math-compass"
            border="left"
          >
              수익합계 : {{sumInfo.sum1 | currency}} 건
            , 딜러수익합계 : {{sumInfo.sum2 | currency}} 원 
            , 총합계 : {{sumInfo.sum3 | currency}} 원
          </v-alert>
          <v-alert
            v-if="this.payload.type===3"
            text
            prominent
            color="#2A3B4D"
            icon="mdi-math-compass"
            border="left"
            
          >
            수익 총합계 : {{sumInfo.sum1  | currency}} 원
          </v-alert>
        </v-col>
        <v-col cols=12>
          <v-data-table
            :headers="headers"
            :items="list"
            dense
            dark
            no-results-text="조회된 데이터가 없습니다."
            no-data-text="데이터를 조회를 하세요"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-arrow-collapse-left',
              lastIcon: 'mdi-arrow-collapse-right',
              prevIcon: 'mdi-arrow-left',
              nextIcon: 'mdi-arrow-right',
              itemsPerPageText : '표시갯수 :',
              itemsPerPageOptions : [20 , 50 , 100 , -1]
            }"   
          >
          <template v-slot:[`item.cnt`]="{ item }">
            {{ item.cnt | currency}} 건
          </template>

          <template v-slot:[`item.tot`]="{ item }">
            {{ item.tot | currency}} 원
          </template>
          <template v-slot:[`item.sellerFee`]="{ item }">
            {{ item.sellerFee | currency}} 원
          </template>
          <template v-slot:[`item.sellerIncome`]="{ item }">
            {{ item.sellerIncome | currency}} 원
          </template>
          <template v-slot:[`item.soleAgencyIncome`]="{ item }">
            {{ item.soleAgencyIncome | currency}} 원
          </template>
          <template v-slot:[`item.agencyIncome`]="{ item }">
            {{ item.agencyIncome | currency}} 원
          </template>
          <template v-slot:[`item.dealerIncome`]="{ item }">
            {{ item.dealerIncome | currency}} 원
          </template>

          <template v-slot:[`item.recommendAgencyFee`]="{ item }">
            {{ item.recommendAgencyFee | currency}} 원
          </template>
          <template v-slot:[`item.recommendDealerFee`]="{ item }">
            {{ item.recommendDealerFee | currency}} 원
          </template>

          <template v-slot:[`item.dealerJoinAmt`]="{ item }">
            {{ item.dealerJoinAmt | currency}} 원
          </template>

          <template v-slot:[`item.agentJoinAmt`]="{ item }">
            {{ item.agentJoinAmt | currency}} 원
          </template>

          <template v-slot:[`item.feeRate`]="{ item }">
            {{ item.feeRate }} %
          </template>
          </v-data-table>
        </v-col>
      </v-row>

    </v-container>
</template>

<script>
import { mapActions , mapMutations ,mapGetters } from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
export default {
  created() {
    document.title = '대리점별매출화면';
    this.$store.state.overLay = false;
    this.headers = this.headers1;
  },
  mixins: [excel , commInfo],
  data() {
    return {
      option1 : '',
      option2 : '',
      textVal1 : '',
      textVal2 : '',
      dateMenu1 : false,
      dateMenu2 : false,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        confmNo : "", // 승인번호
        userId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
        widthMber : 1,
        type : 1, // 1 : 대리점매출수익 , 2 : 추천수수료 수익 , 3: 가맹비 수익
      },
      관리자_대리점별매출_소속포함 : [
        {text : '소속 대리점/딜러 포함' , val : 1},
        {text : '소속 대리점/딜러 미포함' , val : 2},
      ],
      typeList : [
        {text : '대리점매출 수익' , val : 1},
        {text : '추천수수료 수익' , val : 2},
        {text : '가맹비 수익' , val : 3},
      ],
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mberName'},
        {text : '판매자아이디', val : 'mberId'},
        {text : '사업자번호', val : 'bizNum'},
        {text : '상호', val : 'companyName'},
        
        
      ],
      headers : [],
      // 대리점매출수익 헤더
      headers1: [
       {text: '총판', value:'soleAgencyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '대리점' , value:'agencyName' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
       {text: '딜러', value:'dealerName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '건수', value:'cnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '총판매금액', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료율', value:'feeRate', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료금액', value:'sellerFee', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '지급금액', value:'sellerIncome', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '총판수익', value:'soleAgencyIncome', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '대리점수익', value:'agencyIncome', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '딜러수익', value:'dealerIncome', class :'d-flexcaption font-weight-bold align-center pt-1'},       
     ],

      // 추천수수료수익 헤더
      headers2: [
       {text: '추천대리점', value:'recommendCmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '대리점' , value:'titleCmpnm' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
       {text: '딜러', value:'titleDealer', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '건수', value:'cnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '총판매금액', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '추천수수료율', value:'recommendRate', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '추천대리점수익', value:'recommendAgencyFee', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '추천대리점딜러수익', value:'recommendDealerFee', class :'d-flexcaption font-weight-bold align-center pt-1'}, 
      ],
      // 가맹비수익 헤더
      headers3: [
       {text: '대리점', value:'trgetCmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '딜러', value:'dealerCmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '딜러가맹비', value:'dealerJoinAmt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '추천대리점', value:'agentCmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '추천대리점가맹비', value:'agentJoinAmt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '가맹비수익', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

    }
  },
  computed : {
    list(){
      try {
        if(this.payload.type===1){
          return this.$store.state.agencyStore.agency1List;
        } 
        if(this.payload.type===2){
          return this.$store.state.agencyStore.agency2List;
        }
        if(this.payload.type===3){
          return this.$store.state.agencyStore.agency3List;
        }
      } catch (error) {
        console.error(error);
        return [];
      }
    },
    title(){
      try {
        if(this.payload.type===1){
          return '대리점매출'
        } 
        if(this.payload.type===2){
          return '추천수수료'
        }
        if(this.payload.type===3){
          return '가맹비'
        }
      } catch (error) {
        return '대리점매출'
      }
    },
    sumInfo(){
      try {
        let temp = {totCount : 0, sum1: 0 , sum2:0 , sum3 :0 }
        if(this.payload.type===1){
          this.$store.state.agencyStore.agency1List.forEach(item=>{
          
            temp.totCount += item.cnt;
            temp.sum1 += item.soleAgencyIncome;
            temp.sum2 += item.agencyIncome;
            temp.sum3 += item.dealerIncome;
          })
        }
        if(this.payload.type===2){
          this.$store.state.agencyStore.agency2List.forEach(item=>{
            temp.sum1 += item.recommendAgencyFee;
            temp.sum2 += item.recommendDealerFee;
            temp.sum3 += (item.recommendAgencyFee + item.recommendDealerFee);
          })
        }
        if(this.payload.type===3){
          this.$store.state.agencyStore.agency3List.forEach(item=>{
            temp.sum1 += item.tot;
          })
        }
       
        return temp;
      } catch (error) {
        return temp;
      }

    }
  },

  methods: {
    ...mapActions('agencyStore' , 
      ['fetch_agency1_list' 
      ,'fetch_agency2_list' 
      ,'fetch_agency3_list']),

    typeChange(){
      if(this.payload.type===1) this.headers = this.headers1;
      if(this.payload.type===2) this.headers = this.headers2;
      if(this.payload.type===3) this.headers = this.headers3;
    },

    searchEvent(){
      // 옵션처리
      this.payload.mberName = '';
      this.payload.mberId = '';
      this.payload.bizNum = '';
      this.payload.companyName = '';
      if(this.option1 === 'mberName') {this.payload.mberName = this.textVal1}
      if(this.option1 === 'mberId') {this.payload.mberId = this.textVal1}
      if(this.option1 === 'bizNum') {this.payload.bizNum = this.textVal1}
      if(this.option1 === 'companyName') {this.payload.companyName = this.textVal1}

      if(this.option2 === 'mberName') {this.payload.mberName = this.textVal2}
      if(this.option2 === 'mberId') {this.payload.mberId = this.textVal2}
      if(this.option2 === 'bizNum') {this.payload.bizNum = this.textVal2}
      if(this.option2 === 'companyName') {this.payload.companyName = this.textVal2}

      // 대리점코드 처리 선택되지 않은 경우 로그인한 대리점코드를 가져간다. bizCode
      let retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      if(this.payload.type===1) {
        if(!retPayload.bizCode){
          retPayload.bizCode = this.GET_AUTH_BIZCODE;
        }
        this.fetch_agency1_list(retPayload);
      }
      if(this.payload.type===2) {
        this.fetch_agency2_list(retPayload);
      }
      if(this.payload.type===3) {
        this.fetch_agency3_list(retPayload);
      }
    },
    excelDown(){
      try {
        this.$store.state.mainLoading = true;
          let sheetName = "씨트";
          const excelList = this.list.map(item =>{
            const retItem = {};
            if(this.payload.type===1){
              if(item.hasOwnProperty("soleAgencyName")) retItem.총판 = item.soleAgencyName;
              if(item.hasOwnProperty("agencyName")) retItem.대리점 = item.agencyName;
              if(item.hasOwnProperty("dealerName")) retItem.딜러 = item.dealerName;
              if(item.hasOwnProperty("usid")) retItem.판매자ID = item.usid;
              if(item.hasOwnProperty("mberName")) retItem.판매자성명 = item.mberName;
              if(item.hasOwnProperty("companyName")) retItem.상호 = item.companyName;
              if(item.hasOwnProperty("cnt")) retItem.건수 = item.cnt ;
              if(item.hasOwnProperty("tot")) retItem.총판매금액 = item.tot;
              if(item.hasOwnProperty("feeRate")) retItem.수수료율 = item.feeRate;
              if(item.hasOwnProperty("sellerFee")) retItem.수수료금액 = item.sellerFee;
              if(item.hasOwnProperty("sellerIncome")) retItem.지급금액 = item.sellerIncome;
              if(item.hasOwnProperty("soleAgencyIncome")) retItem.총판수익 = item.soleAgencyIncome;
              if(item.hasOwnProperty("agencyIncome")) retItem.대리점수익 = item.agencyIncome;
              if(item.hasOwnProperty("dealerIncome")) retItem.딜러수익 = item.dealerIncome;
              sheetName = "대리점매출수익";
            }
            if(this.payload.type===2){
              if(item.hasOwnProperty("recommendCmpnm")) retItem.추천대리점 = item.recommendCmpnm;
              if(item.hasOwnProperty("titleCmpnm")) retItem.대리점 = item.titleCmpnm;
              if(item.hasOwnProperty("titleDealer")) retItem.딜러 = item.titleDealer;
              if(item.hasOwnProperty("usid")) retItem.판매자ID = item.usid;
              if(item.hasOwnProperty("mberName")) retItem.판매자성명 = item.mberName;
              if(item.hasOwnProperty("companyName")) retItem.상호 = item.companyName;
              if(item.hasOwnProperty("cnt")) retItem.건수 = item.cnt;
              if(item.hasOwnProperty("tot")) retItem.총판매금액 = item.tot;
              if(item.hasOwnProperty("recommendRate")) retItem.추천수수료율 = item.recommendRate;
              if(item.hasOwnProperty("recommendAgencyFee")) retItem.추천대리점수익 = item.recommendAgencyFee;
              if(item.hasOwnProperty("recommendDealerFee")) retItem.추천대리점딜러수익 = item.recommendDealerFee;
              sheetName = "추천수수료수익";
            }
            if(this.payload.type===3){
              if(item.hasOwnProperty("trgetCmpnm")) retItem.대리점 = item.trgetCmpnm;
              if(item.hasOwnProperty("dealerCmpnm")) retItem.딜러 = item.dealerCmpnm;
              if(item.hasOwnProperty("dealerJoinAmt")) retItem.딜러가맹비 = item.dealerJoinAmt;
              if(item.hasOwnProperty("agentCmpnm")) retItem.추천대리점 = item.agentCmpnm;
              if(item.hasOwnProperty("agentJoinAmt")) retItem.추천대리점가맹비 = item.agentJoinAmt;
              if(item.hasOwnProperty("tot")) retItem.가맹비수익 = item.tot;
              sheetName = "가맹점수익";
              
            }
            return retItem;
          
        });
        this.excelEvent(excelList ,`대리점매출-${this.payload.dateStart}-${this.payload.dateEnd}` ,sheetName );
    
      } catch (error) {
        console.error(error);
      } finally {
        this.$store.state.mainLoading = false;
      }
    }
  },
}
</script>

