<template>
  <v-container mx-auto>
    <v-row
      dense
      class="pt-2"
    >
      <v-col cols=12 xs=12 sm=4 md=4 lg=4 xl=2>
        <v-card
          class=""
          :style="{backgroundColor:'#E0F2F1'}"
          flat
        >
          <v-btn
            small
            :dark='this.todayBtnAble'
            @click="setToday"
            color="cyan darken-3"
            :disabled='!this.todayBtnAble'
            >
            오늘
            </v-btn>
            <v-btn
              fab
              text
              color="grey darken-2"
            @click="prev"
            small
          >
            <v-icon >
              mdi-chevron-left
            </v-icon>
          </v-btn>
          <span class="subtitle-2" >{{this.getMonthTitle()}}</span>
          <v-btn
            fab
            text
            color="grey darken-2"
            @click="next"
            small
          >
            <v-icon>
              mdi-chevron-right
            </v-icon>
          </v-btn>
        </v-card>
      </v-col>
      
      <v-col cols=12 xs="6" sm=4 md=4 lg=4 xl=2 class="justify-end" >
       
      <v-card
        flat
        :style="{backgroundColor:'#E0F2F1'}"
        class="pr-3 "
        justify-end
      >
      
        <v-chip-group 
          
            v-model="dataAll"
          >
            <v-chip
            class="caption"
              filter
              outlined
              small
              
            >
              전체
            </v-chip>
            <v-chip
              filter
              outlined
              small
              class="caption"
            >
              총합
            </v-chip>
            <v-chip
              filter
              small
              outlined
              class="caption"
            >
              카드
            </v-chip>
            <v-chip
              filter
              small
              outlined
              class="caption"
            >
              현금
            </v-chip>
          </v-chip-group>
      </v-card>
       </v-col>
      <v-col cols=12 xs="6" sm=4 md=4 lg=4 xl=2>
      <v-card
      :style="{backgroundColor:'#E0F2F1'}"
      
      flat
      >
        <v-chip-group 
            v-model="dataType"
          >
     
            <v-chip
              filter
              outlined
              class="caption"
              small
            >
              거래내역
            </v-chip>
            <v-chip
              filter
              outlined
              class="caption"
              small
            >
              취소내역
            </v-chip>
            <v-chip
              filter
              outlined
              class="caption"
              small
            >
              예비용
            </v-chip>
            
          </v-chip-group>
      </v-card>
        </v-col>
    </v-row>
   
  
  <v-row dense>
    <v-col cols=12 xl=10 lg=9 md=9 class="mt-1">
      <v-sheet height="755">
        <v-calendar
          ref="calendar"
          v-model="value"
          :weekdays="weekday"
          :type="type"
          :events="this.dayList"
          :event-height=17
          locale="kr"
          @click:event="showEvent"
          :event-color="getEventColor"
          :event-more=false
        >
          <template v-slot:day-label="{month,day}">
            <div class="day1" v-if="day!==1">
              {{day}}
            </div>
            <div class="day1" v-if="day===1">
              {{month}}/{{day}}
            </div>
          </template>
        </v-calendar>
      </v-sheet>
    </v-col>
    <v-col cols=12 xl=2 lg=3 md=3>
        <v-row dense >
          <!-- 배너돌리기 -->
          <!-- <v-col cols="12" md=12 sm=3>
            <v-card>
              <v-carousel 
                :show-arrows="false"
                continuous
                cycle
                :interval="6000"
                hide-delimiters
                height="100%"
              >
                <v-carousel-item
                  v-for="(item, index) in banList" :key="index"
                >
                  <v-img :src="item.src">
                  </v-img>
                </v-carousel-item>
              </v-carousel>
            </v-card>
          </v-col> -->
          <v-col cols="12" md=12 sm=3 >
            <v-card
              color="light-blue darken-2"
              dark
            >
              <v-card-title class="subtitle-1">
                {{this.getMonthTitle('side') + " " +this.sideInfo1.title}}
              </v-card-title>
              <v-card-subtitle class="caption">
               *
              </v-card-subtitle>
              <v-card-text class="caption">
                <div>총건수 : {{this.sideInfo1.cnt | currency}} 건</div>
                <div> {{this.sideInfo1.amount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>카드 : {{this.sideInfo1.cardCnt | currency}} 건</div>
                <div>카드액 : {{this.sideInfo1.cardAmount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>현금 : {{this.sideInfo1.cashCnt | currency}} 건</div>
                <div>현금액 : {{this.sideInfo1.cashAmount | currency}} 원</div>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" md=12 sm=3>
            <v-card
              color="deep-orange darken-1"
              dark
            >
              <v-card-title class="subtitle-1">
                {{this.getMonthTitle('side') + " " +this.sideInfo2.title}}
              </v-card-title >
              <v-card-subtitle class="caption">
                *거래날짜에 취소 적용
              </v-card-subtitle>
              <v-card-text class="caption">
                <div>총건수 : {{this.sideInfo2.cnt | currency}} 건</div>
                <div>총액 : {{this.sideInfo2.amount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>카드 : {{this.sideInfo2.cardCnt | currency}} 건</div>
                <div>카드액 : {{this.sideInfo2.cardAmount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>현금 : {{this.sideInfo2.cashCnt | currency}} 건</div>
                <div>현금액 : {{this.sideInfo2.cashAmount | currency}} 원</div>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col cols="12" md=12 sm=3>
            <v-card
              color="teal darken-4"
              dark
            >
              <v-card-title class="subtitle-1">
                {{this.getMonthTitle('side') + " " +this.sideInfo3.title}}
              </v-card-title>
              <v-card-subtitle class="caption">
                *(거래내역 - 취소내역)
              </v-card-subtitle>
              <v-card-text class="caption">
                <div>총건수 : {{this.sideInfo3.cnt | currency}} 건</div>
                <div>총액 : {{this.sideInfo3.amount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>카드 : {{this.sideInfo3.cardCnt | currency}} 건</div>
                <div>카드액 : {{this.sideInfo3.cardAmount | currency}} 원</div>
                <v-divider class="mt-2 mb-2" color="cyan darken-4"></v-divider>
                <div>현금 : {{this.sideInfo3.cashCnt | currency}} 건</div>
                <div>현금액 : {{this.sideInfo3.cashAmount | currency}} 원</div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
    </v-col>
  </v-row>
</v-container>
</template>

<script>
import moment from "moment";
import { mapState, mapActions } from "vuex";
import * as util from "@/util/filter.js";
export default {
  created() {    
    if(!this.value){
      this.setToday();
    }
    this.payload.dateStart = this.value.replace(/-/gi, "").substring(0,6) + "01";
    this.payload.dateEnd = this.value.replace(/-/gi, "");
    
    this.fetch_day_list(this.payload);
  },
  computed: {
    ...mapState('transStore',{
      dayList(state){
        let rtlist = [];
        // 거래내역
        let count = 0;
        let amount = 0;
        let cardCnt = 0;
        let cardAmount = 0;
        let cashCnt = 0;
        let cashAmount = 0;
        // 취소내역
        let cancelCount = 0;
        let cancelAmount = 0;
        let cancelCardCnt = 0;
        let cancelCardAmount = 0;
        let cancelCashCnt = 0;
        let cancelCashAmount = 0;

        // 달력표시 날짜
        let days = new Set();
        state.dayList.forEach(item => {
          days.add(item.confmDt)
        });

        days.forEach(item=>{
          // 거래내역용
          // 총건수
          let event1 = {name : '' , start: item , end : item, color : 'light-blue darken-1'};
          // 총액
          let event2 = {name : '' , start: item , end : item, color : 'light-blue darken-1'};
          // 카드건수
          let event3 = {name : '' , start: item , end : item, color : 'light-blue darken-2'};
          // 카드금액
          let event4 = {name : '' , start: item , end : item, color : 'light-blue darken-2'};
          // 현금건수
          let event5 = {name : '' , start: item , end : item, color : 'light-blue darken-3'};
          // 현금금액
          let event6 = {name : '' , start: item , end : item, color : 'light-blue darken-3'};

          // 취소내역용
          // 총건수
          let cancelEvent1 = {name : '' , start: item , end : item, color : 'amber darken-4'};
          // 총액
          let cancelEvent2 = {name : '' , start: item , end : item, color : 'amber darken-4'};
          // 카드건수
          let cancelEvent3 = {name : '' , start: item , end : item, color : 'amber darken-2'};
          // 카드금액
          let cancelEvent4 = {name : '' , start: item , end : item, color : 'amber darken-2'};
          // 현금건수
          let cancelEvent5 = {name : '' , start: item , end : item, color : 'amber darken-3'};
          // 현금금액
          let cancelEvent6 = {name : '' , start: item , end : item, color : 'amber darken-3'};

          // 거래내역정보
          let event1Cnt = 0;// 총건수
          let event2Sum = 0;// 총액
          let event3Cnt = 0;// 카드건수
          let event4Sum = 0;// 카드금액
          let evnet5Cnt = 0;// 현금건수
          let event6Sum = 0;// 현금금액
          // 취소내역정보
          let cancel1Cnt = 0;// 총건수
          let cancel2Sum = 0;// 총액
          let cancel3Cnt = 0;// 카드건수
          let cancel4Sum = 0;// 카드금액
          let cancel5Cnt = 0;// 현금건수
          let cancel6Sum = 0;// 현금금액

          // 1. 거래내역합 구하기
          this.sideInfo1.title = '거래정보';
          state.dayList.forEach(el => {
            if(item=== el.confmDt){
             
              
              //count += el.totCnt;
              //amount += el.totSum;
              // 카드취소
              if(el.delngSecode === 'CARD_CNCL' ){
                event3Cnt = event3Cnt - el.totCnt;
                event4Sum = event4Sum + el.totSum;
                //count -= el.totCnt;
                //cardCnt -= el.totCnt;
                //cardAmount += el.totSum;
              }
              // 카드거래
              if( el.delngSecode === 'CARD_ISSUE' ){
                event1Cnt = event1Cnt + el.totCnt;
                event2Sum = event2Sum + el.totSum;
                event3Cnt = event3Cnt + el.totCnt;
                event4Sum = event4Sum + el.totSum;
                count += el.totCnt;
                amount += el.totSum;
                cardCnt += el.totCnt;
                cardAmount += el.totSum;
              }
              // 현금거래
              if( el.delngSecode === 'CASH_RCIPT_ISSUE' ){
                event1Cnt = event1Cnt + el.totCnt;
                event2Sum = event2Sum + el.totSum;
                evnet5Cnt = evnet5Cnt + el.totCnt;
                event6Sum = event6Sum + el.totSum;
                count += el.totCnt;
                amount += el.totSum;
                cashCnt += el.totCnt;
                cashAmount += el.totSum;
              }
              // 현금거래취소
              if( el.delngSecode === 'CASH_RCIPT_CNCL' ){
                evnet5Cnt = evnet5Cnt - el.totCnt;
                event6Sum = event6Sum + el.totSum;
                //count -= el.totCnt;
                //cashCnt -= el.totCnt;
                //cashAmount += el.totSum;
              }              
            }
          });
          // 2. 취소내역합 구하기
          this.sideInfo2.title = '취소정보'
            state.dayList.forEach(el => {
              if(item=== el.confmDt){
                // 카드취소
                if( el.delngSecode === 'CARD_CNCL'){
                  cancelCount += el.totCnt;
                  cancelAmount += el.totSum;
                  cancelCardCnt += el.totCnt;
                  cancelCardAmount += el.totSum;
                  cancel1Cnt = cancel1Cnt + el.totCnt;
                  cancel2Sum = cancel2Sum + el.totSum;
                  cancel3Cnt = cancel3Cnt + el.totCnt;
                  cancel4Sum = cancel4Sum + el.totSum;
                }
                // 현금거래취소
                if( el.delngSecode === 'CASH_RCIPT_CNCL'){
                  cancelCount += el.totCnt;
                  cancelAmount += el.totSum;
                  cancelCashCnt += el.totCnt;
                  cancelCashAmount += el.totSum;
                  cancel1Cnt = cancel1Cnt + el.totCnt;
                  cancel2Sum = cancel2Sum + el.totSum;
                  cancel5Cnt = cancel5Cnt + el.totCnt;
                  cancel6Sum = cancel6Sum + el.totSum;
                }
              }
            });
          // 3 .달력표시정보
          if(this.dataAll!==0){
            let inColor = '';
            if(this.dataAll==1){
              if(this.dataType===0){
                inColor = this.getCalendarColor(event2Sum , 30000000 , 'trans');
                event1.color = inColor;
                event2.color = inColor;
                event1.name = util.currency(event1Cnt)+'건';
                event2.name = util.currency(event2Sum) + '원';
              }else{
                inColor = this.getCalendarColor(cancel2Sum , 500000 , 'cancel');
                cancelEvent1.color = inColor;
                cancelEvent2.color = inColor;
                cancelEvent1.name = util.currency(cancel1Cnt)+'건';
                cancelEvent2.name = util.currency(cancel2Sum) + '원';
              }
            }
            if(this.dataAll==2){
              if(this.dataType===0){
                inColor = this.getCalendarColor(event4Sum , 30000000 , 'trans');
                event3.color = inColor;
                event4.color = inColor;
                event3.name = util.currency(event3Cnt)+'건';
                event4.name = util.currency(event4Sum) + '원';
              }else{
                inColor = this.getCalendarColor(cancel4Sum , 500000 , 'cancel');
                cancelEvent3.color = inColor;
                cancelEvent4.color = inColor;
                cancelEvent3.name = util.currency(cancel3Cnt)+'건';
                cancelEvent4.name = util.currency(cancel4Sum)+'원';
              }
            }
            if(this.dataAll==3){
              if(this.dataType===0){
                inColor = this.getCalendarColor(event6Sum , 30000000 , 'trans');
                event5.color = inColor;
                event6.color = inColor;
                event5.name = util.currency(evnet5Cnt)+'건';
                event6.name = util.currency(event6Sum) + '원';
              }else{
                inColor = this.getCalendarColor(cancel6Sum , 500000 , 'cancel');
                cancelEvent5.color = inColor;
                cancelEvent6.color = inColor;
                cancelEvent5.name = util.currency(cancel5Cnt)+'건';
                cancelEvent6.name = util.currency(cancel6Sum) + '원';
              }
            }
          }else{
            if(this.dataType===0){
              event1.name = ' 총합: ' + util.currency(event1Cnt)+'건';
              event2.name = util.currency(event2Sum) + '원';
              event3.name = ' 카드: ' + util.currency(event3Cnt)+'건';
              event4.name = util.currency(event4Sum)+ '원';
              event5.name = ' 현금:' + util.currency(evnet5Cnt)+'건';
              event6.name = util.currency(event6Sum)+ '원';
            }else{
              cancelEvent1.name = ' 총합:' + util.currency(cancel1Cnt)+'건';
              cancelEvent2.name = util.currency(cancel2Sum) + '원';
              cancelEvent3.name = ' 카드: ' + util.currency(cancel3Cnt)+'건';
              cancelEvent4.name = util.currency(cancel4Sum)+ '원';
              cancelEvent5.name = ' 현금:' + util.currency(cancel5Cnt)+'건';
              cancelEvent6.name = util.currency(cancel6Sum)+ '원';
            }
          }
          if(this.dataType===0){
            if(!!event1.name) rtlist.push(event1);
            if(!!event2.name) rtlist.push(event2);
            if(!!event3.name) rtlist.push(event3);
            if(!!event4.name) rtlist.push(event4);
            if(!!event5.name) rtlist.push(event5);
            if(!!event6.name) rtlist.push(event6);
          }else{
            if(!!cancelEvent1.name)rtlist.push(cancelEvent1);
            if(!!cancelEvent2.name)rtlist.push(cancelEvent2);
            if(!!cancelEvent3.name)rtlist.push(cancelEvent3);
            if(!!cancelEvent4.name)rtlist.push(cancelEvent4);
            if(!!cancelEvent5.name)rtlist.push(cancelEvent5);
            if(!!cancelEvent6.name)rtlist.push(cancelEvent6);
          }
        });

        //캘린더 사용가능한 날짜 형태로 변경한다.
        rtlist.forEach(item=>{
          item.start = item.start.substring(0,4) + '-'+item.start.substring(4,6)+'-'+item.start.substring(6,8)
          item.end = item.end.substring(0,4) + '-'+item.end.substring(4,6)+'-'+item.end.substring(6,8)
        });

        // 거래내역
        this.sideInfo1.cnt = count;
        this.sideInfo1.amount = amount;
        this.sideInfo1.cardCnt = cardCnt;
        this.sideInfo1.cardAmount = cardAmount;
        this.sideInfo1.cashAmount = cashAmount;
        this.sideInfo1.cashCnt = cashCnt;

        // 취소내역
        this.sideInfo2.cnt = cancelCount;
        this.sideInfo2.amount = cancelAmount;
        this.sideInfo2.cardCnt = cancelCardCnt;
        this.sideInfo2.cardAmount = cancelCardAmount;
        this.sideInfo2.cashAmount = cancelCashAmount;
        this.sideInfo2.cashCnt = cancelCashCnt;

         // 전체 합계
        this.sideInfo3.cnt = count - cancelCount;
        this.sideInfo3.amount = amount - cancelAmount;
        this.sideInfo3.cardCnt = cardCnt - cancelCardCnt;
        this.sideInfo3.cardAmount = cardAmount - cancelCardAmount;
        this.sideInfo3.cashAmount = cashAmount - cancelCashAmount;
        this.sideInfo3.cashCnt = cashCnt - cancelCashCnt;
        return rtlist
      }
    }),
    todayBtnAble(){
      return moment().isAfter(this.value, 'month')
    }
  },
  data() {
    return {
      sideInfo1 : {
        title : '거래정보',
        cnt : 0,
        amount : 0,
        cardCnt : 0,
        cardAmount : 0,
        cashCnt : 0,
        cashAmount : 0
      },
      sideInfo2 : {
        title : '거래정보',
        cnt : 0,
        amount : 0,
        cardCnt : 0,
        cardAmount : 0,
        cashCnt : 0,
        cashAmount : 0
      },
      sideInfo3 : {
        title : '합계정보',
        cnt : 0,
        amount : 0,
        cardCnt : 0,
        cardAmount : 0,
        cashCnt : 0,
        cashAmount : 0
      },
      dataType : 0,
      dataAll : 0,
      eventColor : [
        '#1565C0' , '#1E88E5' , '#039BE5' , '#00ACC1' , '#4DD0E1',
        '#FFCC80' , '#66CC33' , '#FFA000' , '#FF7043' , '#FF1744',
      ],

      transColor : [
        '#E53935' , '#F4511E' , '#FB8C00' , '#FF8F00' , '#F9A825',
        '#FFCC80' , '#9E9D24' , '#2E7D32' , '#00796B' , '#0277BD',
      ],

      cancelColor : [
        '#7CB342' , '#689F38' , '#558B2F' , '#C0CA33' , '#AFB42B',
        '#9E9D24' , '#F9A825' , '#FF8F00' , '#EF6C00' , '#D84315',
      ],
      overlay : false,
      payload : {
        memberBizeCode:"0000002",
        dateStart : "20201101",
        dateEnd : "20201131",
      },
      publicPath: process.env.BASE_URL,
      imgsrc : 'pop/ban_02.png',
      banList1 : [
        "/pop/ban_01.png",
        "/pop/ban_02.png",
      ],
      banList : [
        {src :'/pop/ban_01.png',title : 'test'},
        {src :'/pop/ban_02.png',title : 'test2'},
      ],
      type: 'month', // ['month', 'week', 'day', '4day']
      mode: 'stack', // ['stack', 'column']
      
      weekday: [0, 1, 2, 3, 4, 5, 6],
      weekdays: [
        { text: 'Sun - Sat', value: [0, 1, 2, 3, 4, 5, 6] },
        { text: 'Mon - Sun', value: [1, 2, 3, 4, 5, 6, 0] },
        { text: 'Mon - Fri', value: [1, 2, 3, 4, 5] },
        { text: 'Mon, Wed, Fri', value: [1, 3, 5] },
      ],
      value: '',
    }
  },
  methods: {
    ...mapActions('transStore' , ['fetch_day_list']),
    // 캘린더 영역 표시 색
    getCalendarColor(val , unit , type){
      if(!unit||!val ) return;
      if(isNaN (unit)||isNaN(val)) return;
      let result = parseInt(val / unit);
      if(result > 9) result = 9;
      if(type==='cancel'){
        return this.cancelColor[result];
      }else{
        return this.transColor[result];
      }
    },
    getEventColor (event) {
      return event.color
    },
    getMonthTitle(type){
      if(!!this.value){
        if(type==='side'){
          return moment(this.value ).format('MM월');
        }
        return moment(this.value ).format('YY년MM월');
      } else {
        this.value = moment().format('YYYY-MM-DD');
        return moment().format('YY년MM월');
      }
    },
    setToday () {
      this.value = moment().format('YYYY-MM-DD'); // 현재 오늘날짜로변경됨
      this.payload.dateStart = this.value.replace(/-/gi, "").substring(0,6) + "01";;
      this.payload.dateEnd = this.value.replace(/-/gi, "").substring(0,6) + "31";
      this.fetch_day_list(this.payload);
    },
    prev () {
      this.$refs.calendar.prev();
      this.payload.dateStart = this.value.replace(/-/gi, "").substring(0,6) + "01";
      this.payload.dateEnd = this.value.replace(/-/gi, "");
      this.fetch_day_list(this.payload);
  
    },
    next () {
      if(!moment().isAfter(this.value, 'month')){
        return
      }
      this.$refs.calendar.next();
      
      this.payload.dateStart = this.value.replace(/-/gi, "");
      this.payload.dateEnd = this.value.replace(/-/gi, "").substring(0,6) + "31";
      this.fetch_day_list(this.payload);
    },
    tranType(val){
      console.log(`내역타입 : ${val}`);
    },
    showEvent(obj){
      console.log(`시작날짜 : ${obj.event.start} , 종료날짜 : ${obj.event.end}` );
      this.$router.push(
        {
          name : 'mainTrans' , 
          params : {
            dateStart : obj.event.start ,
            dateEnd : obj.event.end ,
            dataType : this.dataType
           }
        }
      );
    }
  },

}
</script>

