<template>
  <v-dialog
    v-model="dialog"
    persistent
    max-width="600px"
  >
  <v-form ref="form" v-model="valid">
    <v-card class="overflow-hidden">
      <v-app-bar
        color="blue"
        dense
        dark
        absolute
        elevate-on-scroll
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-sheet max-height="600" class="overflow-y-auto mt-10">
        <v-card-text>
          <v-row>
            <v-col cols=12  xs=12 sm=12 md=6 lg=6 xl=6>
              <v-text-field
                class="caption"
                label="PG사이름"
                v-model="dialogData.pgName"
              ></v-text-field>
            </v-col>
            <v-col cols=12  xs=12 sm=12 md=6 lg=6 xl=6>
              <v-text-field
                class="caption"
                label="기본 수수료율"
                v-model="dialogData.defaultFee"
              ></v-text-field>
            </v-col>
            <v-col cols=12  xs=12 sm=12 md=6 lg=6 xl=6>
              <v-text-field
                class="caption"
                label="익일 수수료율"
                v-model="dialogData.fee01"
              ></v-text-field>
            </v-col>
            <v-col cols=12  xs=12 sm=12 md=6 lg=6 xl=6>
              <v-text-field
                class="caption"
                label="5일 수수료율"
                v-model="dialogData.fee02"
              ></v-text-field>
            </v-col>

          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
            <v-btn block :disabled="!valid" color="primary" @click="saveEvent" >
              <v-icon left>mdi-content-save</v-icon>
              저장
            </v-btn>
        </v-card-actions>
      </v-sheet>
    </v-card>
    </v-form>
  </v-dialog>
</template>

<script>

import commInfo from "@/mixins/comInfo";
import { mapActions } from 'vuex'
import api from "@/api/notice.js";
export default {
props :['dialog' , 'dialogData' , 'title'],
mixins: [commInfo],
data() {
  return {
    valid : true,
  }
},

methods: {
  ...mapActions('noticeStore',[
    'fetch_pg_fee_list',
  ]),
  closeEvent(){
    this.$emit('dialogEvent' , false);
  },
  saveEvent(){
    if(this.title === 'PG사 수수료율 수정'){
      api.updatePgFee(this.dialogData)
      .then(_=>this.fetch_pg_fee_list())
      .then(_=>this.$emit('dialogEvent' , false) )
      .catch(error => console.log(error));
    }else {
      api.savePgFee(this.dialogData)
      .then(_=>this.fetch_pg_fee_list())
      .then(_=>this.$emit('dialogEvent' , false) )
      .catch(error => console.log(error));
     }    
  },
  },
}
</script>

