import * as api from "../api/index";

const bpwStore = {
  namespaced: true,
  state:{
    // 정산조회
    inqireDelngList : [],
    inqireDelngTotal : 0,
    inqireDelngSum : {},


    //대리점관리
    inqireDealerTotal : 0 ,
    inqireDealerList : [],
    dealerRegInfo : {},

    // 입출금내역관리 -- datatable 용
    inqireAtmList : [],
    inqireAtmSum :{},
    InqireAtmTotal : 0,
    atmConfig :{} , // 출금정지상태정보

  },
  getters:{
    atmConfigSwitchInfo : state =>{
      if(state.atmConfig && state.atmConfig.enableYn === 'Y') return "실행중";
      else return "해제";
    },
    atmConfigSwitchFlag : state =>{
      if(state.atmConfig && state.atmConfig.enableYn === 'Y') return true;
      else return false;
    },
    
  },
  mutations:{
    set_inqire_delng_list(state,list){
      state.inqireDelngList = list;
    },
    set_inqire_delng_sum(state,info){
      state.inqireDelngSum = info;
    },
    set_inqire_delng_total(state,count){
      state.inqireDelngTotal = count;
    },
    set_inqire_dealer_list(state ,list){
      state.inqireDealerList = list;
    },
    set_inqire_dealer_total(state ,count){
      state.inqireDealerTotal = count;
    },
    set_dealer_regInfo(state , info){
      state.dealerRegInfo = info;
    },

    set_inqire_atm_list(state , list){
      state.inqireAtmList = list;
    },
    set_inqire_atm_sum(state , info){
      state.inqireAtmSum = info;
    },
    set_inqire_atm_tot(state , totCount){
      state.InqireAtmTotal = totCount;
    },
    set_atm_confing(state , info){
      state.atmConfig = info;
    }

  },
  actions:{
    // 정산조회
    async fetch_inqire_delng_list({commit} , payload){
      try {
        const info = await api.InqireDelng.list(payload);
        await commit('set_inqire_delng_list' ,info.list );
        await commit('set_inqire_delng_sum' ,info.sum );
        await commit('set_inqire_delng_total' ,info.totCount );
      } catch (err) {
        if(!!err.message && err.message.length > 20){
          commit('SET_ALERT_DIALOG',{ flag : true , message : '데이터 조회중 문제가 발생했습니다. 잠시후 다시 조회해 주세요' , title : '오류' , type : 'error'}, { root: true });  
        }else commit('SET_ALERT_DIALOG',{ flag : true , message : err.message , title : '오류' , type : 'error'}, { root: true });
        
      }
    },
    // 대리점 관리 
    async fetch_inqire_dealer_list({commit} , payload){
      try {
        const info = await api.InqireDealer.list(payload);
        await commit('set_inqire_dealer_total' , info.totCount);
        await commit('set_inqire_dealer_list' , info.list);
      } catch (error) {
        console.error(error);
        throw error;
      }
    },

    // 개별대리점 상세정보
    fetch_dealer_info({commit} , userId){
      api.InqireDealer.get(userId)
      .then(info=>{
        return commit('set_dealer_regInfo' , info);
      })
      .catch(error=>{
        console.error(error);
        throw error;
      })
    },

    // 대리점정보 수정
    async modify_dealer(ctx , payload){
      try {
        return await api.InqireDealer.update(payload);
      } catch (error) {
        console.error(error);
        throw error;
      }
    },

    // 입출금내역관리
    async fetch_inqire_atm_list({commit} , payload){
      try {
        const info = await api.InqireAtm.list(payload);
        await commit('set_inqire_atm_list' , info.list);
        await commit('set_inqire_atm_sum' , info.sum);      
      } catch (error) {
        console.error(error);
        throw error;
      }
    },
    // 출금정지상태정보
    fetch_atm_config_info({commit}){
      
      api.atmConfig.info()
      .then((info)=>{
        return commit('set_atm_confing' , info);
      }).catch(error =>{
        console.error(error);
        throw error;
      })
    },

    async modify_atm_config_info({commit} , payload){
      try {
        const retMsg = await api.atmConfig.update(payload);
        if(retMsg == 'ok'){
    
         const info = await api.atmConfig.info();
         await commit('set_atm_confing' , info);
        } else {
          // 실패처안내 추가
          commit('SET_ALERT_DIALOG',{ flag : true , message : '출금상태변경에 문제가 있습니다..'}, { root: true });
        }
      } catch (error) {
        console.error(error);
        commit('SET_ALERT_DIALOG',{ flag : true , message : '출금상태변경시스템에 문제가 발생하였습니다.'}, { root: true });
      }
    }
  }
}

export default bpwStore;