<template>
  <v-card align="center" justify="center" class="mx-auto">
    <v-speed-dial
      absolute
      fixed
      v-model="fab"
      direction="left"
      bottom
      right
      open-on-hover
      transition="scale-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="blue darken-2"
          dark
          fab
          rounded
        >
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-magnify-plus-outline</v-icon>
        </v-btn>
      </template>
      <v-btn
        rounded
        dark
        small
        color="green"
        @click="dialog = true"
        
      >
        <v-icon small dark>mdi-magnify</v-icon>검색
      </v-btn>
      <v-btn
        rounded
        @click="excelDown(payload)"
        dark
        small
        color="indigo"
      >
        <v-icon small dark>mdi-cloud-download-outline</v-icon>
        엑셀다운
      </v-btn>
    </v-speed-dial>
    <v-simple-table  >
     <thead class="header">
       <tr>
         <th class="text-left" @click="orderByEvent('cmpnm')">대리점</th>
         <th class="text-left" @click="orderByEvent('confm_no')">승인번호</th>
         <th class="text-left" @click="orderByEvent('pay_type')">코드</th>
         <th class="text-left" @click="orderByEvent('usid')">판매자ID</th>
         <th class="text-left" @click="orderByEvent('mber_name')">판매자</th>
         <th class="text-left" @click="orderByEvent('company_name')">상호</th>
         <th class="text-left" @click="orderByEvent('splpc')">판매금액</th>
         <th class="text-left" @click="orderByEvent('delng_se_code')">구분</th>
         <th class="text-left" @click="orderByEvent('instlmt_month')">할부</th>
         <th class="text-left" @click="orderByEvent('issue_cmpny_nm')">발급자명</th>
         <th class="text-left" @click="orderByEvent('card_no')">카드번호</th>
         <th class="text-left" @click="orderByEvent('confm_dt')">승인일시</th>
         <th class="text-left" @click="orderByEvent('cnclConfmDt')">취소일자</th>
       </tr>
     </thead>
     <tbody v-if="list.length>0">
        <tr v-for="(item , index ) in list" :key="index" @dblclick="rowClickEvent(item)">
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.cmpnm }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.confmNo }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.payType }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.usid }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.mberName }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.companyName }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.splpc | currency}}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.delngSeCodeNm }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.instlmtMonth }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.issueCmpnyNm }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.cardNo }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.confmDt }}</td>
         <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.cnclConfmDt }}</td>
      </tr>
      <tr>
       <td v-if="sumInfo" colspan="13" style="text-align:center;background-color:gray;color:#fff">
         카드합계 : {{sumInfo.cardCount|currency}} 건 , {{sumInfo.cardSum |currency}} 원, 
         현금합계  {{sumInfo.cashCount|currency }} 건 ,{{sumInfo.cashSum | currency}} 원 ||
         총합계 : {{sumInfo.cardCount + sumInfo.cashCount}} 건
       {{sumInfo.sum |currency}} 원
       </td>
     </tr>
   </tbody>
   <tbody v-else>
     <tr>
       <td colspan="13" style="height : 300px; text-align:center; "> 조회된 데이터가 없습니다.</td>
     </tr>
   </tbody>
    </v-simple-table>
      <PagingNormal :size='this.pageSize' :total='this.listCount' @pageEvent="pageEvent" :page='this.page' />
      <InqireDelngSearch 
        :dialog='dialog' 
        @dialogEvent='delngDialogEvent' 
        @searchEvent="searchEvent"
       />
      <IngireDelngInfo 
        :infodialog='infodialog' 
        :item='clickItme' 
        @dialogEvent="delngDialogInfoEvent" 
      />
  </v-card>
</template>

<script>
import { mapActions , mapMutations } from 'vuex';
import PagingNormal from "@/components/PagingNormal";
import InqireDelngSearch from "@/components/InqireDelngSearch";
import IngireDelngInfo from "@/components/IngireDelngInfo";
import {excel} from "@/mixins/excel";
import {currency} from "@/util/filter.js";

export default {
  components : {
    PagingNormal,
    InqireDelngSearch,
    IngireDelngInfo
  },
  mixins: [excel],
  created() {
    document.title = '거래조회';
    this.set_inqire_delng_list([]);
    this.set_inqire_delng_sum(null);
    this.set_inqire_delng_total(0);
  },
  computed: {
    list(){
      try {
        if(this.$store.state.bpwStore.inqireDelngList) return this.$store.state.bpwStore.inqireDelngList;
      } catch (error) {
        console.error(error);
        return [];
      }
    },
    sumInfo(){
      try {
        return this.$store.state.bpwStore.inqireDelngSum
      } catch (error) {
        console.error(error);
        return {};
      }
    },
    listCount(){
      try {
        return this.$store.state.bpwStore.inqireDelngTotal;
      } catch (error) {
        return 0;
      }
    }
  },
  data() {
    return {
      fab : false,
      menu1 : false,
      menu2 : false,
      infodialog : false,
      dialog : false,
      startIndex : 0 , 
      endIndex : 0,
      pageSize : 20,
      clickItme : null,
      page : 1,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10).replace(/-/g,''),
        dateEnd : new Date().toISOString().substr(0, 10).replace(/-/g,''), 
			  memberBizeCode : this.$store.state.authStore.memberInfo.bizCode, // 로그인 세션 기능 만들어 진후 변경한다.
        startIndex : 0,
        endIndex :100,
        bizCode : 'all',
        calculateType : 'all',
        delngSeCode : 'all',
        orderbyColumn : '',
        orderby : '',
      },
    }
  },
  methods: {
    ...mapMutations(['SET_ALERT_DIALOG']),
    ...mapActions('bpwStore' , ['fetch_inqire_delng_list']),
    ...mapMutations('bpwStore' , 
      ['set_inqire_delng_list' 
      ,'set_inqire_delng_sum'
      ,'set_inqire_delng_total']),

    searchEvent(payload){
      this.$store.state.mainLoading = true;
      payload.startIndex = 0;
      payload.endIndex = this.pageSize;
      this.payload = payload;
      this.fetch_inqire_delng_list(payload)
      .then(()=>{
        this.page =1;
      })
      .finally(()=>{
        this.$store.state.mainLoading =false;
      });
    },
    delngDialogEvent(val){
      this.dialog = val;
    },
    delngDialogInfoEvent(val){
      this.infodialog = val;
    },   
    rowClickEvent(item){
      this.infodialog=false;
      this.infodialog=true;
      this.clickItme = item;
    },
    pageEvent(val){
      this.page=val;
      this.$store.state.mainLoading = true;
      this.payload.startIndex = this.pageSize * (val-1);
      this.payload.endIndex = this.pageSize * (val);
      this.fetch_inqire_delng_list(this.payload)
      .finally(()=>{
        this.$store.state.mainLoading =false;
      });
    },
    orderByEvent(val){
      if(this.list.length===0) return;
      if(!val){
        this.SET_ALERT_DIALOG({flag : true , message :'해당항목은 정렬기능이 지원되지 않습니다.' , title :"안내" , type : "info" });
        return
      }
      this.page =1;
      this.$store.state.mainLoading = true;
      if(this.payload.orderbyColumn === val){
        if(this.payload.orderby === "desc") this.payload.orderby = "asc";
        else this.payload.orderby = "desc";
      }else {
        this.payload.orderby = "desc";
        this.payload.orderbyColumn = val;
      }

      this.payload.startIndex = 0;
      this.payload.endIndex = this.pageSize;
      this.fetch_inqire_delng_list(this.payload)
      .finally(()=>{
        this.$store.state.mainLoading =false;
      });
    },
    excelDown(payload){
      try {
        this.$store.state.mainLoading = true;
        this.excelApiCallEvent('delng' , payload)
        .then((list)=>{
          list = list.list.map(item=>{
            const retItem = {};
            if(item.hasOwnProperty("cmpnm")) {
              retItem.대리점 = item.cmpnm;
            }
            if(item.hasOwnProperty("confmNo")) {
              retItem.승인번호 = item.confmNo;
            }
            if(item.hasOwnProperty("payType")) {
              retItem.코드 = item.payType;
            }
            if(item.hasOwnProperty("usid")) {
              retItem.판매자ID = item.usid;
            }
            if(item.hasOwnProperty("mberName")) {
              retItem.판매자성명 = item.mberName;
            }
            if(item.hasOwnProperty("companyName")) {
              retItem.상호 = item.companyName;
            }
            if(item.hasOwnProperty("bizNum")) {
              retItem.사업자등록 = item.bizNum;
            }
            if(item.hasOwnProperty("mberMobile")) {
              retItem.판매자휴대폰 = item.mberMobile;
            }
            if(item.hasOwnProperty("rciptNo")) {
              retItem.영수증번호 = item.rciptNo;
            }
            if(item.hasOwnProperty("mberPhone")) {
              retItem.구매자연락처 = item.mberPhone;
            }
            if(item.hasOwnProperty("splpc")) {
              retItem.판매금액 = currency(item.splpc);
            }
            if(item.hasOwnProperty("delngSeCodeNm")) {
              retItem.구분 = item.delngSeCodeNm;
            }
            if(item.hasOwnProperty("instlmtMonth")) {
              retItem.할부 = item.instlmtMonth;
            }
            if(item.hasOwnProperty("issueCmpnyNm")) {
              retItem.발급사명 = item.issueCmpnyNm;
            }
            if(item.hasOwnProperty("cardNo")) {
              retItem.카드번호 = item.cardNo;
            }
            if(item.hasOwnProperty("confmDt")) {
              retItem.승인일시 = item.confmDt;
            }
            if(item.hasOwnProperty("cnclConfmDt")) {
              retItem.취소일시 = item.cnclConfmDt;
            }
            return retItem;
          });      
          this.excelEvent(list ,`거래조회-${payload.dateStart}-${payload.dateEnd}` ,'거래조회' )
        })

      } catch (error) {
        console.error(error);
      } finally{
        this.$store.state.mainLoading = false;
      }
    }
  },
}
</script>

<style scoped>
  .cancel {
    background-color:#FF8A80;
    color: rgb(47, 61, 141);
    font-size: .5em;
  }
  .sumover {
    background-color:#827717;
    color: #fff;
    font-size: xx-large;
  }
  .header {
    background-color:#DCEDC8;
    font-size: xx-large;
    cursor: pointer;
  }
  
  
</style>