<template>
  <v-navigation-drawer
    v-model="drawer"
    color="cyan darken-4"
    app
    dark
    temporary
  > <v-list >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="title">
           {{this.GET_CMPNM}}
          </v-list-item-title>
          
          <v-list-item-subtitle>
            {{managerTypeName}} /
            <template v-if="!!this.$store.state.authStore.bizInfo &&this.$store.state.authStore.bizInfo.bizType==='Y'">
                사업자
            </template>
            <template v-else>
                비사업자
            </template>
          </v-list-item-subtitle>
          
        </v-list-item-content>
        <v-btn
          
            @click="closeEvent"
            color="cyan darken-3"
          >
            <v-icon>mdi-format-horizontal-align-left</v-icon>
          </v-btn>
      </v-list-item>
    </v-list>
    <v-divider color="white"></v-divider>
    <v-list dark>
        <div
          v-for="item in menuList"
          :key="item.url+item.title"
        >
        <v-list-group
          :value="(item.title === menuName) ?true : false"
          active-class="yellow--text"
      >
        <template v-slot:activator>
          <v-list-item-content    >
            <v-list-item-title>{{item.title}}</v-list-item-title>
          </v-list-item-content>
        </template>
        <v-list-item
          v-for="(subItem , index) in item.subList"
          :key="subItem.url+index"
          link
          :class="subItem.title===subMenuName && item.title===menuName? 'active2' : ''"
          @click="menuClickEvent(item.title, subItem.title , subItem.url )"
        >
          <v-list-item-subtitle class="text-center">{{subItem.title}}</v-list-item-subtitle>
        </v-list-item>
      </v-list-group>
      </div>
    </v-list>
    <template v-slot:append>
      <div class="pa-2">  
        <v-btn block color="indigo darken-4" @click="logoutEvent" v-if="!!GET_CMPNM">Logout</v-btn>
        <v-btn block color="indigo darken-4" @click="logoutEvent" v-else>로그인</v-btn>
      </div>
    </template>
  </v-navigation-drawer>
</template>

<script>
import {mapState} from 'vuex'
import commInfo from "@/mixins/comInfo";
export default {
  props : ['drawer'],
  mixins: [commInfo],
  methods: {
    closeEvent(){
      this.$emit('drawerEvent' , false);
    }
  },
}
</script>

<style>
  .active2{
    background-color: rgb(52, 119, 119);

  }
  .yellow--text{
    background-color: rgb(106, 158, 158);
  }
</style>