<template>
  <v-container>
    <!-- 검색영역추가부분 -->
    <v-speed-dial
      absolute
      fixed
      v-model="fab"
      direction="left"
      bottom
      right
      :open-on-hover="true"
      transition="scale-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="blue darken-2"
          dark
          fab
          rounded
        >
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-magnify-plus-outline</v-icon>
        </v-btn>
      </template>
      
      <v-btn
        rounded
        dark
        small
        color="green"
        @click="dialog = true"
        
      >
        <v-icon small dark>mdi-magnify</v-icon>검색
      </v-btn>
      <v-btn
        rounded
        @click="excelDown(payload)"
        dark
        small
        color="indigo"
      >
        <v-icon small dark>mdi-cloud-download-outline</v-icon>
        엑셀DOWN
      </v-btn>
      <v-btn
        rounded
        dark
        small
        color="red"
        @click="excelBankEvent"
      >
        <v-icon small dark>mdi-bank-outline</v-icon>
        계좌이체용엑셀DOWN
      </v-btn>
      <v-btn
        rounded
        dark
        small
        color="green"
        @click="newDealerEvent"
      >
        <v-icon small dark>mdi-home-plus</v-icon>대리점등록
      </v-btn>
    </v-speed-dial>

    <v-data-table
      dense
      :headers="headers"
      :items="list"
      class="elevation-1"
      no-data-text="조회된 데이터가 없습니다."

      
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-arrow-collapse-left',
        lastIcon: 'mdi-arrow-collapse-right',
        prevIcon: 'mdi-arrow-left',
        nextIcon: 'mdi-arrow-right',
        itemsPerPageText : '표시갯수 :',
        itemsPerPageOptions : [20 , 50 , 100 , -1]
      }"
    >
      <template  v-slot:body="{ items }">
        <tbody>
          <tr v-for="(item, index) in items" :key="index" @dblclick="rowClickEvent(item.usid)" class="text-sm-caption" >
            <td>{{ item.gubun }}</td>
            <td>{{ item.cmpnm }}</td>
            <td>{{ item.bizrno|bizNum }}</td>
            <td>{{ item.usid }}</td>
            <td>{{ item.mberCnt }}</td>
            <td>{{ item.regDate }}</td>
            <td>
              <v-switch
                disabled
                dense
                v-model="item.useAt"
                color="success"
                value="Y"
              ></v-switch>
            </td>
            <td>{{ item.stopTradeDate }}</td>
            <td>{{ item.distributor }}</td>
          </tr>
        </tbody>
      </template>
      </v-data-table>
    <InqireDealerSearch :dialog='dialog' @dialogEvent="dialogEvent" @searchEvent="searchEvent"/>
    <DealerRegister :dialog='regDialog'  @dialogEvent="reDialogEvent" :title="deakerRegTitle" @search="searchEvent"/>
  </v-container>
</template>

<script>
import { mapActions ,mapMutations } from 'vuex'
import InqireDealerSearch from "@/components/InqireDealerSearch";
import DealerRegister from "@/components/DealerRegister";
import {excel} from "@/mixins/excel";
export default {
  created() {
    document.title = '대리점관리';
    this.set_inqire_dealer_list([]);
    this.set_inqire_dealer_total(0)
  },
  components : {
    InqireDealerSearch,
    DealerRegister
  },
  mixins: [excel],
  data() {
    return {
      headers: [
         { text: '대리점구분', value: 'gubun' },
         { text: '상호(이름)', value: 'cmpnm' },
         { text: '사업자번호', value: 'bizrno' },
         { text: '아이디', value: 'usid' },
         { text: '활성회원수', value: 'mberCnt' },
         { text: '등록일', value: 'regDate' },
         { text: '거래상태', value: 'useAt' },
         { text: '거래중지일', value: 'stopTradeDate' },
         { text: '총판', value: 'distributor' }
      ],
      dialog : false,
      payload : {},
      fab : false,
      regDialog : false,
      deakerRegTitle : '대리점'
    }
  },
  computed: {
    list(){
      return this.$store.state.bpwStore.inqireDealerList;
    }
  },
  methods: {
    ...mapActions('bpwStore' , [
      'fetch_inqire_dealer_list' , 
      'fetch_dealer_info'
      ]),
    ...mapMutations('bpwStore' , [
      'set_inqire_dealer_list',
      'set_inqire_dealer_total',
      'set_dealer_regInfo'
    ]),
    rowClickEvent(val){
      this.$store.state.mainLoading = true;
      this.deakerRegTitle = "대리점상세정보";
      this.fetch_dealer_info(val)
      .then(()=>{
        this.regDialog =true;
        this.$store.state.mainLoading = false;
      });
    },
    newDealerEvent(){
     // debugger
      this.deakerRegTitle = "신규대리점등록";
      this.set_dealer_regInfo({});
      this.regDialog =true;
    },

    dialogEvent(val){
      this.dialog = val;
    },
    reDialogEvent(val){
      this.regDialog = val;
    },
    searchEvent(payload){
      if(!!payload){
        payload.endIndex = 0;
        this.payload = payload;
      }
      this.$store.state.mainLoading = true;
      this.fetch_inqire_dealer_list(this.payload)
      .finally(()=>{
        this.$store.state.mainLoading =false;
      });
    },
    excelBankEvent(){
      const exList = this.list.map(item => {
        let retItem = {};
        if(item.hasOwnProperty("gubun")) {
            retItem.대리점구분 = item.gubun;
        }
        if(item.hasOwnProperty("cmpnm")) {
            retItem.상호 = item.gubun;
        }
        if(item.hasOwnProperty("bizrno")) {
            retItem.사업자번호 = item.bizrno;
        }
        if(item.hasOwnProperty("bizTelno")) {
            retItem.사업장전화번호 = item.bizTelno;
        }
        if(item.hasOwnProperty("mtelno")) {
            retItem.핸드폰번호 = item.mtelno;
        }
        if(item.hasOwnProperty("usid")) {
            retItem.아이디 = item.usid;
        }
        if(item.hasOwnProperty("bankName")) {
            retItem.은행명 = item.bankName;
        } 
        if(item.hasOwnProperty("bankCode")) {
            retItem.은행코드 = item.bankCode;
        }
        if(item.hasOwnProperty("bankSerial")) {
            retItem.계좌번호 = item.bankSerial;
        }
        retItem.이체금액 = '';
        if(item.hasOwnProperty("bankUser")) {
          retItem.예금주명 = item.bankUser;
        }
        retItem.입금계좌적요 = '';
        return retItem;

      });
      this.excelEvent(exList , '대리점계좌이체목록' , '계좌이체목록');
    }
  },
}
</script>

