<!--공지사항 -->
<template>
  <v-container>
    <v-row dense>
      <v-col cols=12>


    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">공지사항</span>
        <v-spacer></v-spacer>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
             </v-card>
       </v-col>
      </v-row>
      <v-row align="center">
        <v-col>
          <v-btn block @click="deleteEvent">
            삭제
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="regEvent('N')">
            등록
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="regEvent('M')">
            무이자공지등록
          </v-btn>
       
        </v-col>
      </v-row>
      <v-data-table
        v-model="selected"
        @click:row="detailEvent"
        :headers="headers"
        :items="list"
        dense
        dark
        show-select
        item-key="idx"
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      <template v-slot:[`item.type`]="{ item }">
        {{item.type=='M' ? '무이자공지' : '일반' }}
      </template>
    </v-data-table>
    <NoticeEdit 
      :dialog='dialogFlag' 
      :dialogData='item' 
      :title='title' 
      @dialogEvent='dialogOFFEvent' 
      :type='type'
    />
  </v-container>
</template>

<script>
import {mapActions} from 'vuex'
import commInfo from "@/mixins/comInfo";
import NoticeEdit from "@/components/notice/NoticeEdit";
import api from "@/api/notice.js";
export default {
  mixins: [commInfo],
  created() {
    document.title = '공지사항';
    this.fetch_notice_list();

  },

  components : {
    NoticeEdit
  },
  computed : {
    list(){
      try {
        const list = this.$store.state.noticeStore.noticeList.map(item=>{
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }

    }
  },
  data() {
    return {
      title : '공지사항수정',
      dialogFlag : false,
      item : null,
      type : '',
      selected : [],
      headers : [
        {text: '번호', value:'idx', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '표시', value:'isactive', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '공지타입', value:'type', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '제목', value:'title', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

     

    }
  },
  methods: {
    ...mapActions('noticeStore',[
      'fetch_notice_list',
    ]),
    searchEvent(){
      this.fetch_notice_list();
    },
    detailEvent(val){
      this.dialogFlag=true;
      this.item= val;
      this.title = '공지사항 확인 수정';
    },
    dialogOFFEvent(){
      this.dialogFlag=false;
    },

    regEvent(val){
      if(val=='N'){
        this.title = '공지사항 등록';
      }
      if(val=='M'){
        this.title = '무이자 공지사항 등록';
      }
      this.item={
        content : '',
        idx : -1,
        isactive : 'N',
        title : '',
        type : val
      }
      this.dialogFlag=true;
    },
    deleteEvent(){
      let idxStr = '';
      if(this.selected.length <1) {
        return;
      }
      this.selected.forEach(element => {
        if(idxStr.length >0) idxStr = idxStr + ','+ element.idx;
        else idxStr = String(element.idx);
      });
      api.deleteNotice(idxStr)
      .then(_=>this.fetch_notice_list())

    }

  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  }
 
</style>