import { request } from "./index";

// 거래관련
const trans = {
  // 일자별 거래내역조회
  transByDate(payload){
    return request('post' , '/trans/transByDay' , payload);
  },

  // 거래(승인)내역 / 취소내역
  transListSearch(payload){
    return request('post' , '/trans/transListSearch' , payload);
  }
}

export default trans;