<template>
  <v-card align="center" justify="center" class="mx-auto">
    <v-speed-dial
      absolute
      fixed
      v-model="fab"
      direction="left"
      bottom
      right
      :open-on-hover="true"
      transition="scale-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="blue darken-2"
          dark
          fab
          rounded
        >
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-magnify-plus-outline</v-icon>
        </v-btn>
      </template>
      <v-btn
        rounded
        dark
        small
        color="green"
        @click="dialog = true"
        
      >
        <v-icon small dark>mdi-magnify</v-icon>검색
      </v-btn>
      <v-btn
        rounded
        @click="excelDown(payload)"
        dark
        small
        color="indigo"
      >
        <v-icon small dark>mdi-cloud-download-outline</v-icon>
        엑셀다운
      </v-btn>
    </v-speed-dial>

    <v-data-table
          
      :headers="headers"
      :items="list"
      class="elevation-1"
      dense
      :footer-props="{
        showFirstLastPage: true,
        firstIcon: 'mdi-arrow-collapse-left',
        lastIcon: 'mdi-arrow-collapse-right',
        prevIcon: 'mdi-arrow-left',
        nextIcon: 'mdi-arrow-right',
        itemsPerPageText : '표시갯수 :',
        itemsPerPageOptions : [20 , 50 , 100 , -1]
      }"
    >
      <template  v-slot:no-data>
        데이터를 조회하세요1
      </template>
      <template  v-slot:no-results>
        데이터를 조회하세요2
      </template>
      <template  v-slot:body="{ items }">
        <tbody>
          <tr v-for="(item, index) in items" :key="index" @dblclick="rowClickEvent(item)" class="text-sm-caption" >
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt ,cancel : item.cnclConfmDt }" >{{ item.cmpnm }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.confmNo }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.payType }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.usid }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.mberName }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.companyName }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.splpc | currency }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.delngSeCodeNm }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.instlmtMonth }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.issueCmpnyNm }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.cardNo }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.confmDt }}</td>
            <td :class="{sumover : item.splpc > 300000 && !item.cnclConfmDt,cancel : item.cnclConfmDt }">{{ item.cnclConfmDt }}</td>
          </tr>
        </tbody>
      </template>
      <template v-slot:footer>
        <v-divider/>
        <div v-if="sumInfo" style="background : #E8EAF6">
          카드합계 : {{sumInfo.cardCount}} 건 ,{{sumInfo.cardSum |currency}} 원, 
          현금합계  {{sumInfo.cashCount }} 건 ,{{sumInfo.cashSum | currency}} 원
          총합계 : {{ sumInfo.cardCount ? sumInfo.cardCount + sumInfo.cashCount : 0}} 건
          {{sumInfo.sum |currency}} 원
        </div>
      </template>
    </v-data-table>

    <InqireDelngSearch 
      :dialog='dialog' 
      @dialogEvent='delngDialogEvent' 
      @searchEvent="searchEvent"
    />
    <IngireDelngInfo 
      :infodialog='infodialog' :item='clickItme' 
      @dialogEvent="delngDialogInfoEvent" />
  </v-card>
</template>

<script>
import { mapActions , mapMutations } from 'vuex';
import InqireDelngSearch from "@/components/InqireDelngSearch";
import IngireDelngInfo from "@/components/IngireDelngInfo";
import {excel} from "@/mixins/excel";
import {currency} from "@/util/filter.js";

export default {
  components : {
    InqireDelngSearch,
    IngireDelngInfo
  },
  mixins: [excel],
  created() {
    document.title = '거래조회전체목록';
    this.set_inqire_delng_list([]);
    this.set_inqire_delng_sum(null);
    this.set_inqire_delng_total(0);
  },
  computed: {
    list(){
      try {
        if(this.$store.state.bpwStore.inqireDelngList) return this.$store.state.bpwStore.inqireDelngList;
        else return []
      } catch (error) {
        console.error(error);
      }
    },
    sumInfo(){
      try {
        return this.$store.state.bpwStore.inqireDelngSum
      } catch (error) {
        console.error(error);
        return;
      }
    }
  },
  data() {
    return {
      fab : false,
      menu1 : false,
      menu2 : false,
      dialog : false,
      infodialog : false,
      clickItme : null,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10).replace(/-/g,''),
        dateEnd : new Date().toISOString().substr(0, 10).replace(/-/g,''), 
			  memberBizeCode : '0000002', // 로그인 세션 기능 만들어 진후 변경한다.
        startIndex : 0,
        endIndex :100,
        bizCode : 'all',
        calculateType : 'all',
        delngSeCode : 'all'
      },
      headers : [
        {text : '대리점', value: 'cmpnm' , fixedHeader : true},
        {text : '승인번호', value: 'confmNo'},
        {text : '코드', value: 'payType'},
        {text : '판매자ID', value: 'usid'},
        {text : '판매자', value: 'cmpnm'},
        {text : '상호', value: 'companyName'},
        {text : '판매금액', value: 'splpc'},
        {text : '구분', value: 'delngSeCodeNm'},
        {text : '할부', value : 'instlmtMonth' },
        {text : '발급자명', value: 'issueCmpnyNm'},
        {text : '카드번호', value: 'cardNo'},
        {text : '승인일시', value: 'confmDt'},
        {text : '취소일자', value: 'cnclConfmDt'}
      ]
    }
  },
  methods: {
    ...mapActions('bpwStore' , ['fetch_inqire_delng_list']),
    ...mapMutations('bpwStore' , 
      ['set_inqire_delng_list' 
      ,'set_inqire_delng_sum'
      ,'set_inqire_delng_total']),
    searchEvent(payload){
      this.$store.state.mainLoading = true;
      payload.startIndex = 0;
      payload.endIndex = this.pageSize;
      this.payload = payload;
      this.fetch_inqire_delng_list(payload)
      .finally(()=>{
        this.$store.state.mainLoading =false;
      });
    },
    delngDialogEvent(val){
      this.dialog = val;
    },
    delngDialogInfoEvent(val){
      this.infodialog = val;
    },   
    rowClickEvent(item){
      this.infodialog=false;
      this.infodialog=true;
      this.clickItme = item;
    }
  },
}
</script>

<style scoped>
.cancel {
  background-color:#FF8A80;
  color: rgb(47, 61, 141);
  font-size: .1em;
}
.v-data-table-header {
  background-color: cadetblue
}

.sumover {
  background-color:#4DD0E1;
  color: rgb(89, 102, 14);
  font-size: xx-large;
}
</style>