<template>
  <v-snackbar
    outlined
    v-model="snackFlag" 
    max-width="350" 
    right 
    :timeout="-1" 
    text
    color="teal darken-3"
  >  
  <v-row no-gutters>
    <v-col cols=12>
      <div class="d-flex" >
        <v-icon
          color="teal darken-2"
          left
        >
          mdi-information-outline
        </v-icon>
      <span class="text-h6 font-weight-black">알림</span>
      </div>
    </v-col>
  </v-row>
  <v-row dense>
    <v-col cols=12>
      <span v-html="infoSnackObj.inHtml"></span>
    </v-col>
  </v-row>
  <v-divider></v-divider>
  <v-row no-gutters>
    <v-col cols=9>
      <v-checkbox
        v-model="chk"
        label="다시보지 않기"
        color="teal"
        hide-details
        dense
      ></v-checkbox>
    </v-col>
    <v-col cols=3>
      <v-btn 
        outlined 
        color="teal"
        @click="btnEvent"
        class="mt-3" dense>
          확인
        </v-btn>
    </v-col>
  </v-row>
  </v-snackbar>
</template>

<script>
import { mapState , mapMutations , mapActions } from "vuex";
export default {
  created() {
    this.FETCH_INFO_SNACLBAR();
  },
  data() {
    return {
      chk : false,
      infoHtml : '<b>알림내용이 여기에 온다</b><br/> +1 내용 ++<br/> +2 내용 ++'
    }
  },
  computed: {
    ...mapState(['infoSnackObj']),
    snackFlag(){
      if(!!this.infoSnackObj.snackFlag){
        console.log(this.infoSnackObj.snackFlag);
        return !this.infoSnackObj.reLoadFlag
      } 
      return false;
      
    }
  },

  methods: {
    ...mapActions(['FETCH_INFO_SNACLBAR']),
    ...mapMutations(['SET_INFO_SNACKBAR']),
    btnEvent(){
      this.infoSnackObj.reLoadFlag = this.chk;
      this.infoSnackObj.snackFlag = false;
      this.SET_INFO_SNACKBAR(this.infoSnackObj);
    }
  },
}
</script>

<style>
  .v-snack .v-label{
    font-size: 0.8em;
  }
</style>