<template>
  <v-dialog
    v-model="dialog"
    persistent
    max-width="600px"
  >
  <v-form ref="form">
    <v-card class="overflow-hidden">
      <v-app-bar
        :color=this.color
        dense
        dark
        absolute
        elevate-on-scroll
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-sheet max-height="600" class="overflow-y-auto mt-10">
        <v-card-text>
          <v-row>
            <v-col cols=12>
              <v-textarea
                class="caption pr-2"
                dense
                label="판매자 전달 메세지"
                v-model="info.MSG"
              ></v-textarea>
            </v-col>

          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
            <v-btn color="warning" @click="saveEvent(true)" >
              <v-icon left>mdi-content-save</v-icon>
              출금정지실행
            </v-btn>
            <v-btn   color="primary" @click="saveEvent(false)" >
              <v-icon left>mdi-content-save</v-icon>
              출금정지해제
            </v-btn>
        </v-card-actions>
      </v-sheet>
    </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
import { mapActions } from 'vuex'
import api from "@/api/account.js";
export default {
  props :['dialog'],
  mounted() {
    this.fetch_in_out_setting_info();
  },
  computed : {
    info(){
      return this.$store.state.accountStore.inOutSettingInfo;
    },
    title(){
      if(this.$store.state.accountStore.inOutSettingInfo.ENABLEYN === 'N') return '출금정지상태';
      else return '출금가능상태';
    },
    color(){
      if(this.$store.state.accountStore.inOutSettingInfo.ENABLEYN === 'N') return 'warning';
      else return 'primary';
    }
  } ,

  data() {
    return {
  
    }
  },
  methods: {
    ...mapActions('accountStore' , ['fetch_in_out_setting_info']),
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
    saveEvent(flag){
      const formData = new FormData();
      formData.append('msg' , this.info.MSG);
      if(flag){
        formData.append('enableYn' , 'N');
      }else{
        formData.append('enableYn' , 'Y');
      }

      api.inOutSetting(formData)
      .then( ()=> this.fetch_in_out_setting_info())

      

      
      },
    
    },
  }
</script>

