<!-- 판매자관리 -->
<template>
    <v-container>
      <v-row dense>
        <v-col cols=12>
           <v-card dark color="deep-purple darken-3">
             <v-app-bar dense class="deep-purple lighten-1 white--text">
               <span class="subtitle-1 font-weight-black">판매자관리</span>
               <v-spacer></v-spacer>
               <v-divider light vertical inset></v-divider>
               <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
                조회
              </v-btn>
             </v-app-bar>
             <v-container>
               <v-row dense>
              <v-col cols=4 xs=4 sm=2 md=2 lg=4 xl=2>
                <div class="caption pt-2">
                  검색시작일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=4 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateStart" 
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateStart" 
                    locale="ko"
                    no-title
                    :max= 'this.dateStartMax'
                    @input="dateMenu2 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols=4 xs=4 sm=2 md=2 lg=2 xl=2>
                <div class="caption pt-2">
                  검색종료일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=4 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateEnd"
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateEnd" 
                    locale="ko"
                    no-title
                    :max='new Date().toISOString().substr(0, 10)'
                    @change="dateEndEvent"
                    @input="dateMenu2 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols=12 xs=12 sm=3 md=3 lg=3 xl=3>
                <v-autocomplete
                  class="caption"
                  :items="this.dealerList"
                  label="직접입력 & 목록선택"
                  outlined
                  dense
                  item-text="cmpnm"
                  item-value="bizCode"
                  v-model="payload.bizCode"
                >
                  <template v-slot:no-data>
                    대리점이 존재하지 않습니다
                  </template>
                </v-autocomplete>
              </v-col>
              <v-col cols=12 xs=4 sm=3 md=3 lg=3 xl=3>
                  <v-select
                    class="caption"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="payload.useAt"
                    :items="optionList1"
                    label="거래/가입정보" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=12 xs=4 sm=3 md=3 lg=3 xl=3>
                  <v-select
                    class="caption"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="payload.calculateType"
                    :items="calculateList"
                    label="직거래" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=12 xs=4 sm=3 md=3 lg=3 xl=3>
                  <v-select
                    class="caption"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList2"
                    label="판매자잔액" 
                    outlined
                  >
                  </v-select>
                </v-col>
  
              </v-row >
              <v-row dense>
                <v-col cols=4 xs=4 sm=2 md=2 lg=2 xl=1>
                  <v-select
                    class="caption"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option1"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=4 md=4 sm=4 lg=4 xl=5>
                  <v-text-field
                    class="caption"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal1"
                    v-model="textVal1"
                  ></v-text-field>
                </v-col>
                <v-col cols=4 xs=2 md=2 sm=2 lg=2 xl=1>
                  <v-select
                    class="caption"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=4 md=4 sm=4 lg=4 xl=5>
                  <v-text-field
                    class="caption"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal2"
                    v-model="textVal2"
                  ></v-text-field>
                </v-col>
              </v-row>
             </v-container>

           </v-card>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col cols=12 xs=12 sm=4  md=4  lg=4 xl=4>
          <v-btn block @click="excelEvent1"  >
            엑셀다운로드
          </v-btn>
        </v-col>
        
        <v-col cols=12 xs=12 sm=4  md=4  lg=4 xl=4>
          
          <v-btn block @click="dialogFileEvent(true)" >
            일괄등록
          </v-btn>
        </v-col>
        <v-col cols=12 xs=12 sm=4  md=4  lg=4 xl=4>
          <v-btn block @click="regEvent" >
            핀매자등록
          </v-btn>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols=12>
          <v-data-table
            :headers="headers"
            :items="list"
            dense
            dark
            no-results-text="조회된 데이터가 없습니다."
            no-data-text="데이터를 조회를 하세요"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-arrow-collapse-left',
              lastIcon: 'mdi-arrow-collapse-right',
              prevIcon: 'mdi-arrow-left',
              nextIcon: 'mdi-arrow-right',
              itemsPerPageText : '표시갯수 :',
              itemsPerPageOptions : [20 , 50 , 100 , -1]
            }"
            @click:row="detailEvent"
          >
          <template v-slot:[`item.documentReg`]="{ item }">
            {{item.documentReg!=='O' ? '미제출' : '제출완료' }}
          </template>
          <template v-slot:[`item.useAt`]="{ item }">
            {{!item.useAt ? '미활성' : '활성' }}
          </template>
          <template v-slot:[`item.creatDtCan`]="{ item }">
            {{item.useAt === 'N'? item.creatDtCan : '' }}
          </template>
          <template v-slot:[`item.balance`]="{ item }">
            {{item.balance | currency}} 원
          </template>
          </v-data-table>
        </v-col>
      </v-row>
      <sellerEdit :infodialog='dialog' @dialogEvent='dialogEvent' :item='dialogData' :preItem='dialogData' :title='dialogTitle'></sellerEdit>
      <SellerFileEdit :dialog='dialogFile'  @dialogFileEvent='dialogFileEvent' ></SellerFileEdit>
    </v-container>
</template>
<script>
import { mapActions , mapMutations ,mapGetters } from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
import sellerEdit from "@/components/sellerEdit.vue";
import SellerFileEdit from "@/components/SellerFileEdit.vue";
export default {
  created() {
    document.title = '판매자관리';
    this.$store.state.overLay = false;

  },
  computed : {
    list(){
      try {
        return this.$store.state.agencyManageStore.sellerList;
      } catch (error) {
        return [];
      }
    }
  },
  components : {
    sellerEdit,
    SellerFileEdit
  },
  mixins: [excel , commInfo],
  data() {
    return {
      publicPath: process.env.BASE_URL,
      option1 : '',
      option2 : '',
      textVal1 : '',
      textVal2 : '',
      dateMenu1 : false,
      dateMenu2 : false,
      payload : {
        dateStart : null, // 검색시작일
        dateEnd : null, // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        memberMberCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        indutyId : "", // 판매자 아이디
        bizCode : "", // 대리점코드
        memberName : "",  // 판매자 성명
        bizNum : "", // 사업자 번호 
        useAt : 'Y',
        calculateType:'',
        depositor : "", // 승인번호
        nm : "", // 승인번호
        bizTypeName : "", // 승인번호
        bizItem : "", // 승인번호
      },

      // 옵션
      optionList1 : [
        {text : '전체', val : ''},
        {text : '거래중', val : 'Y'},
        {text : '거래중지', val : 'N'},    
        {text : '대리점가입', val : 'S'},
        {text : 'APP가입', val : 'T'},
        {text : '서류제출완료', val : 'U'},
        {text : '서류미제출', val : 'V'},
      ],
      // 잔액상태
      optionList2 : [
        {text : '전체', val : ''},
        {text : '잔액 - ', val : '1'},
        {text : '잔액 + ', val : '2'}
      ],

      // 직거래상태
      calculateList : [
        {text : '전체' , val : ''},
        {text : '유니코아' , val : 'U'},
        {text : 'KSNET직정산' , val : 'K'}],

      optionList : [
        {text : '전체', val : ''},
        {text : '판매자아이디', val : 'indutyId'},
        {text : '판매자성명', val : 'memberName'},    
        {text : '사업자번호', val : 'bizNum'},
        {text : '예금주', val : 'depositor'},
        {text : '상호', val : 'nm'},
        {text : '업종', val : 'bizTypeName'},
        {text : '취급품목', val : 'bizItem'},
      ],

      headers: [
        {text: '판매자아이디', value:'indutyId', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '코드' , value:'kpayType' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
        {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '사업자등록번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '주소', value:'adres', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '대표전화번호', value:'mberPhone', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '수수료율', value:'feeRate', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '활성여부', value:'useAt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '가입경로', value:'regCase', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '서류제출', value:'documentReg', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '등록일자', value:'creatDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '거래중지일자', value:'creatDtCan', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '소속', value:'cmpnm', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '잔액', value:'balance', class :'d-flexcaption font-weight-bold align-center pt-1'},
      ],

      dialog : false,
      
      dialogData : {},
      dialogTitle : '',
      dialogFile : false,
    }
  },


  methods: {
    ...mapActions('agencyManageStore' , 
      ['fetch_seller_list']),
    dialogEvent(val){
      this.dialog = val;
    },
    dialogFileEvent(val){
      this.dialogFile = val;
    },
    detailEvent(val){
      this.dialog = false;
      this.dialog = true;
      this.dialogTitle = '판매자확인'
      this.dialogData = val;
    },
    regEvent(){
      this.dialog = false;
      this.dialog = true;
      this.dialogTitle = '대리점신규'
      this.dialogData = {feeRate : 0 , bizCode:'0000002'}; // 하위 값연계위해 미리 구성해서 보내주어야 한다.
    },
    excelEvent1(){
      const excelList = this.list.map(item =>{
        const retItem = {};
        if(item.hasOwnProperty("indutyId")) retItem.판매자아이디 = item.indutyId;
        if(item.hasOwnProperty("kpayType")) retItem.코드 = item.kpayType;
        if(item.hasOwnProperty("mberName")) retItem.판매자성명 = item.mberName;
        if(item.hasOwnProperty("adres")) retItem.주소 = item.adres;
        if(item.hasOwnProperty("mberMobile")){
          if(!!item.mberMobile) retItem.핸드폰번호 = item.mberMobile;
        }
        if(item.hasOwnProperty("feeRate")) retItem.수수료율 = item.feeRate;
        if(!item.useAt){
          retItem.활성여부 = '미활성';
        } else retItem.활성여부 = '활성';
        if(item.hasOwnProperty("creatDt")) retItem.등록일자 = item.creatDt;
        if(item.hasOwnProperty("mberCnt")) retItem.활성회원수 = item.mberCnt;
        if(item.hasOwnProperty("useAt")){
          if(item.useAt === 'N') retItem.거래중지일자 = item.creatDtCan;
          else retItem.거래중지일자 = '';
        }
        if(item.hasOwnProperty("cmpnm")) retItem.소속 = item.cmpnm;
        if(item.hasOwnProperty("bizType")){
          if(item.bizType === ' Y') retItem.사업자구분 = '사업자';
        else retItem.사업자구분 = '개인';
        }
        if(item.hasOwnProperty("companyName")) retItem.사업자명 = item.companyName;
        if(item.hasOwnProperty("bizNum")) retItem.사업자번호 = item.bizNum;
        if(item.hasOwnProperty("trmnlNo")) retItem.VAN터미널ID = item.trmnlNo;
        if(item.hasOwnProperty("pgTrmnlNo")) retItem.PG터미널ID = item.pgTrmnlNo;
        if(item.hasOwnProperty("pgVan")) retItem.VANPG = item.pgVan;
        return retItem;
      });
      this.excelEvent(excelList ,`판매자목록` ,'대리점' );
    },
    downEvent(){

    },
    excelEvent2(){

    },

    searchEvent(){
      this.payload.indutyId = '';
      this.payload.memberName= '';
      this.payload.bizNum= '';
      this.payload.depositor= '';
      this.payload.nm= '';
      this.payload.bizTypeName= '';
      this.payload.bizItem= '';

      // 옵션
      if(this.option1 === 'indutyId') { 
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option1 === 'memberName') {
        this.payload.memberName = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'depositor') {
        this.payload.depositor = this.textVal1;
      }
      else if(this.option1 === 'nm') {
        this.payload.nm = this.textVal1;
      }
      else if(this.option1 === 'bizTypeName') {
        this.payload.bizTypeName = this.textVal1;
      }
      else if(this.option1 === 'bizItem') {
        this.payload.bizItem = this.textVal1;
      }

      if(this.option2 === 'indutyId') { 
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option2 === 'memberName') {
        this.payload.memberName = this.textVal1;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option2 === 'depositor') {
        this.payload.depositor = this.textVal1;
      }
      else if(this.option2 === 'nm') {
        this.payload.nm = this.textVal1;
      }
      else if(this.option2 === 'bizTypeName') {
        this.payload.bizTypeName = this.textVal1;
      }
      else if(this.option2 === 'bizItem') {
        this.payload.bizItem = this.textVal1;
      }

      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_seller_list(retPayload);
    }
  },
}
</script>

<style>

</style>