import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '../store';
import InqireDelng from "@/views/InqireDelng";
import InqireDelngAll from "@/views/InqireDelngAll2";
import InqireAtm from "@/views/InqireAtm";
import InqireAtmAll from "@/views/InqireAtmAll";
import InqireDealer from "@/views/InqireDealer";
import InqireDealerAll from "@/views/InqireDealerAll";

import MainLayout from "@/components/layout/MainLayout.vue";
import ExternalLayout from "@/components/layout/ExternalLayout.vue";
import Period from "@/views/transaction/Period";
import Trans from "@/views/transaction/Trans.vue";
import AgencySales from "@/views/transaction/AgencySales.vue";
import SellerSales from "@/views/transaction/SellerSales";
import SalesAdjustment from "@/views/transaction/SalesAdjustment";
import AgencyManagement from "@/views/agency/agencyManagement";
import SellerManagement from "@/views/agency/SellerManagement";
import inputoutputManagement from "@/views/account/inputoutputManagement";
import accountExcel from "@/views/account/accountExcel";
import Transfer from "@/views/account/Transfer";
import BankTrans from "@/views/account/BankTrans";
import TaxIssue from "@/views/tax/TaxIssue";
import TaxReport from "@/views/tax/TaxReport";
import Guarantee from "@/views/tax/Guarantee";
import Notice from "@/views/notice/Notice";
import PgList from "@/views/notice/PgList";
import QrPayment from "@/views/external/QrPayment";
import QrPayCall from "@/views/external/QrPayCall";

import Login from "@/views/Login";

Vue.use(VueRouter)

// 로그인 필요페이지 -- ? 로그인 페이지를 다시 한번 가는 부분 제거 -- relogin 과 역임
const isAuth = (to, from,next) => {
  if(!!store.state.authStore.memberInfo){
    localStorage.rtPath = to.path;
    next();
  } else next('/')
  //!!store.state.authStore.memberInfo ? next() : next('/login')
}

// 로그인페이지등 로그인 필요없는 페이지
const notAuth = (to , from , next) => {
  console.log(store.state.authStore.memberInfo);
  !!store.state.authStore.memberInfo ? next('/main/login') : next()
}



const routes = [

  {
    path: '/main',
    name: 'PeriodPage',
    component: MainLayout,
    children: [
      {
        path: 'period',
        component: Period,
        beforeEnter : isAuth
      },{
        name : 'mainTrans',
        path: 'trans',
        component: Trans,
        beforeEnter : isAuth
      },,{
        name : 'mainAgencySales',
        path: 'agencySales',
        component: AgencySales,
        beforeEnter : isAuth
      },{
        name : 'mainSellerSales',
        path: 'sellerSales',
        component: SellerSales,
        beforeEnter : isAuth
      },{
        name : 'mainSalesAdjustment',
        path: 'salesAdjustment',
        component: SalesAdjustment,
        beforeEnter : isAuth
      },{ // 대리점관리
        name : 'agencyManagement',
        path: 'agencyManagement',
        component: AgencyManagement,
        beforeEnter : isAuth
      },{ // 판매자관리
        name : 'sellerManagement',
        path: 'sellerManagement',
        component: SellerManagement,
        beforeEnter : isAuth
      },{ // 판매자관리
        name : 'inputoutputManagement',
        path: 'inputoutputManagement',
        component: inputoutputManagement,
        beforeEnter : isAuth
      },{ // 정산엑셀
        name : 'accountExcel',
        path: 'accountExcel',
        component: accountExcel,
        beforeEnter : isAuth
      },{ // 계좌이체조정
        name : 'Transfer',
        path: 'transfer',
        component: Transfer,
        beforeEnter : isAuth
      },{ // 은행계좌이체
        name : 'BankTrans',
        path: 'bankTrans',
        component: BankTrans,
        beforeEnter : isAuth
      },{ // 세금계산서발급
        name : 'TaxIssue',
        path: 'TaxIssue',
        component: TaxIssue,
        beforeEnter : isAuth
      },
      { // 세무서신고
        name : 'TaxReport',
        path: 'TaxReport',
        component: TaxReport,
        beforeEnter : isAuth
      },
      { // 보증료관리
        name : 'Guarantee',
        path: 'Guarantee',
        component: Guarantee,
        beforeEnter : isAuth
      },
      { // 공지사항
        name : 'Notice',
        path: 'Notice',
        component: Notice,
        beforeEnter : isAuth
      },
      { // pg사 수수료율
        name : 'PgList',
        path: 'PgList',
        component: PgList,
        beforeEnter : isAuth
      },
      { // 로그인페이지
        name : 'mainLogin',
        path: 'login',
        component: Login,
        beforeEnter : notAuth
      },
      {
        path: '/',
        redirect : '/main/login'
      },
      {
        path: '*',
        redirect : '/main/login'
      },
    ],
  },
  {
    path: '/external/qrpa/:orderNO',
        name: 'externalMainqrpay',
        component: QrPayment,
        beforeEnter : notAuth,
  },
  {
    path: '/external/qrpa',
        name: 'externalMainqrpay',
        component: QrPayment,
        beforeEnter : notAuth,
  },
  {
    path: '/external/qrExCall',
        name: 'qrExCall',
        component: QrPayCall,
        beforeEnter : notAuth,
  },
  // {
  //   path: '/',
  //   name: 'index',
  //   component: InqireDelngAll,
  //   beforeEnter : isAuth
  // },
  {
    path: '/InqireDelng',
    name: 'InqireDelng',
    component: InqireDelng,
    beforeEnter : notAuth
  },
  {
    path: '/InqireDelngAll',
    name: 'InqireDelngAll',
    component: InqireDelngAll,
    beforeEnter : notAuth
  },
  // {
  //   path: '/InqireDealerAll',
  //   name: 'InqireDealerAll',
  //   component: InqireDealerAll
  // },
  {
    path: '/InqireAtm',
    name: 'InqireAtm',
    component: InqireAtm,
    beforeEnter : isAuth
  },
  {
    path: '/InqireAtmAll',
    name: 'InqireAtmAll',
    beforeEnter : isAuth,
    component: InqireAtmAll,
  },
  {
    path: '/InqireDealer',
    name: 'InqireDealer',
    beforeEnter : isAuth,
    component: InqireDealer,
  },
  {
    path: '/InqireDealerAll',
    name: 'InqireDealerAll',
    beforeEnter : isAuth,
    component: InqireDealerAll,
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    beforeEnter : notAuth
  },
  // 기타 주소 대응
  {
    path: '*',
    redirect : '/main/login'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


export default router
