<template>
  <v-container fluid fill-height >
    <v-layout
      align-center
      justify-center>
      <v-flex
        xs12
        sm8
        md4>
        <v-card 
          class="elevation-12"
        >
          <v-toolbar
            color="light-green lighten-4"
            flat
            class="d-flex"
          >
            <v-img src='../assets/logo_icon.png' max-height="50" contain></v-img>
            <v-toolbar-title class="title">Biz Pay Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form
              ref="loginForm"
              v-model="valid"
              @submit.prevent="loginEvent"
            >
              <v-text-field
                label="아이디"
                name="userId"
                v-model="userId"
                prepend-icon="mdi-account"
                :rules="[v => !!v || '아이디를 입력하세요']"
                type="text"
                @keyup.enter="loginEvent"
              ></v-text-field>
              <v-text-field
                  id="password"
                  label="패스워드"
                  name="password"
                  v-model="password"
                  :rules="[v => !!v || '패스워드를 입력하세요']"
                  prepend-icon="mdi-lock"
                  type="password"
                  @keyup.enter="loginEvent"
                ></v-text-field>
                <v-checkbox
                  v-model="idCheck"
                  label="ID저장"
                  color="light-green lighten-2"
                ></v-checkbox>
              </v-form>
              <v-card-actions>
                <v-btn 
                  @click="loginEvent"
                  block
                  :disabled="!valid"
                  type="summit"
                  style="color:#fff;font-weight:bolder"
                  color="light-green lighten-2">
                  Login
                </v-btn>
            </v-card-actions>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions , mapMutations , mapGetters} from 'vuex'
export default {
  created() {
    // 아이디 저장 체크한경우
    const {userId} = localStorage;
    this.userId = userId;
    this.$store.state.overLay = false;
  },
  data() {
    return {
      userId : '',
      password : '',
      valid: false,
      idCheck : true
    }
  },
  methods: {
    ...mapActions('authStore',['FETCH_MEMBER_INFO']),
    ...mapMutations(['SET_MENU_LIST']),
    ...mapGetters('authStore',['GET_MEMBER_GRADE']),
    loginEvent(){
      this.FETCH_MEMBER_INFO({userId : this.userId ,password : this.password , idCheck : this.idCheck})
      .then((key)=>{

        // 아이디 체크한경우 아이디 저장
        if(!!this.idCheck){
          localStorage.userId = this.userId;
        }else {
          delete localStorage.userId;
          this.$router.push("/");
        }
        localStorage.authkey = key;
        // 메뉴정보 생성
        const menuList = [];
      })
      .then(()=>{
        
        if(!!localStorage.rtPath){
            this.$router.push(localStorage.rtPath);
        }else {

          this.$router.push("/main/period");
        }
      })
    }
  },
}
</script>

<style scoped>
  .title {
    color:#7CB342;
    font-weight:bolder
    
  }
</style>