import agency from "../api/agency";

const agencyStore = {
  namespaced: true,
  state : {
    agency1List : [],// 대리점별매출 수익
    agency2List : [],// 추천수수료 수익
    agency3List : [],// 가맹비 수익
    sellerList : [],// 판매자수익
    adjustList : [], // 매출조정목록
    dealerList : [] , // 전체 딜러목록
  },
  getters : {},
  mutations : {
    set_agency1_list(state , list){
      state.agency1List = list;
    },
    set_agency2_list(state , list){
      state.agency2List = list;
    },
    set_agency3_list(state , list){
      state.agency3List = list;
    },
    set_seller_list(state , list){
      state.sellerList = list;
    },
    set_adjust_list(state , list){
      state.adjustList = list;
    },
    set_dealer_list(state , list){
      state.dealerList = list;
    }
  },
  actions : {
    // 전체 대리점 목록
    fetch_dealer_list({commit}){
      agency.dealerList()
      .then(data => {
        commit('set_dealer_list' , data);
      })
      .catch(err => console.error(err));
    },

    // 대리점별매출
    async fetch_agency1_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agency.agency1(payload);
        await commit('set_agency1_list' ,list);
        await commit('SET_OVERLAY',false, { root: true });
      } catch (err) {
        await commit('SET_OVERLAY',false, { root: true });
        console.error(err);
        throw err;
      }
    },
    // 추천수수료 수익
    async fetch_agency2_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agency.agency2(payload);
        await commit('set_agency2_list' ,list);
        await commit('SET_OVERLAY',false, { root: true });
      } catch (err) {
        await commit('SET_OVERLAY',false, { root: true });
        console.error(err);
        throw err;
      }
    },

    // 가맹비 수익
    async fetch_agency3_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agency.agency3(payload);
        await commit('set_agency3_list' ,list);
        await commit('SET_OVERLAY',false, { root: true });
      } catch (err) {
        await commit('SET_OVERLAY',false, { root: true });
        console.error(err);
        throw err;
      }
    },

    // 대리점매출
    async fetch_seller_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agency.seller(payload);
        await commit('set_seller_list' ,list);
        await commit('SET_OVERLAY',false, { root: true });
      } catch (err) {
        await commit('SET_OVERLAY',false, { root: true });
        commit('SET_ALERT_DIALOG',{ flag : true , message : '잠시후 다시시도해주세요' , title : '오류' , type : 'error'}, { root: true });
        console.error(err);
        throw err;
      }
    },

    // 매출조정목록
    async fetch_adjust_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agency.adjustList(payload);
        await commit('set_adjust_list' ,list);
        await commit('SET_OVERLAY',false, { root: true });
      } catch (err) {
        await commit('SET_OVERLAY',false, { root: true });
        console.error(err);
        throw err;
      }
    },
  }
}

export default agencyStore;