import { request } from "./index";

// 공지사항등
const notice = {
  // 공지사항 목록
  noticeList(){
    return request('get' , '/notice/noticeList');
  },
  // 공지사항 삭제
  deleteNotice(idxList){
    return request('delete' , `/notice/deleteNotice/${idxList}`);
  },
  // 공지사항 저장
  saveNotice(item){
    return request('post' , '/notice/saveNotice' ,item);
  },
  // 공지사항 수정
  updateNotice(item){
    return request('post' , '/notice/updateNotice' ,item);
  },
  // 수수료율 목록
  pgFeeList(){
    return request('get' , '/notice/pgFeeList');
  },
  // 공지사항 삭제
  deletePgFee(idxList){
    return request('delete' , `/notice/deletePgFee/${idxList}`);
  },
  // 공지사항 저장
  savePgFee(item){
    return request('post' , '/notice/savePgFee' ,item);
  },
  // 공지사항 수정
  updatePgFee(item){
    return request('post' , '/notice/updatePgFee' ,item);
  },
}

export default notice;