<!--은행계좌이체 -->
<template>
  <v-container class="mt-3">
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">은행계좌이체</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
      <v-row class="pt-2 pr-2 pl-2" dense>
        <v-col cols=12 xs=4 sm=12 md=2 lg=2 xl=2>
          <v-select
            class="caption"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.dateType"
            :items="this.dayList"
            label="거래/지급일자" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3" >
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=3 lg=3 xl=3>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=2 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=4 sm=9 md=3 lg=3 xl=3>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
       
      </v-row>
      <v-row dense class="pl-1 pr-1">
        
        <v-col cols=12 xs=6 sm=6 md=6 lg=6 xl=6>
          <v-autocomplete
            class="caption"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
       <v-col cols=12 xs=6 sm=6 md=6 lg=6 xl=6>
          <v-select
            class="caption"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.pymntYn"
            :items="this.codeList"
            label="관리자_은행계좌이체_지급구분" 
            outlined
          >
          </v-select>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option1"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal1"
             v-model="textVal1"
           ></v-text-field>
         </v-col>
         <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option2"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal2"
             v-model="textVal2"
           ></v-text-field>
         </v-col>
      </v-row>
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark

        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      <template v-slot:[`item.splpc`]="{ item }">
        {{ item.splpc | currency}} 원
      </template>

      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions , mapMutations } from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '계좌이체조정';
    this.$store.state.overLay = false;
  },
  computed : {
    list(){
      try {
        const list = this.$store.state.accountStore.bankAcntTransList.map(item=>{
          // if(this.payload.dateType==='confirm'){
          //   item.confmDt = item.confmDtSub;
          // }else {
          //   item.confmDt = item.payDt;
          // }
          if(item.pymntYn==='Y'){
            item.pymntYn = '지급'
          }else item.pymntYn = '미지급'

          item.bankFee = currency(item.bankFee );
          item.payAmount = currency(item.payAmount );
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }

    }
  },
  data() {
    return {
      singleSelect : false,
      headers : [
        {text: '거래일자', value:'confmDtSub', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '지급일자', value:'fpayDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '코드', value:'payType', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '은행명', value:'bankName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '계좌번호', value:'accountNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '지급구분', value:'pymntYn', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '수수료', value:'bankFee', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '입금금액', value:'payAmount', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '예금주', value:'depositor', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '건수', value:'cnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        
      ],

      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        inoutCode : "", // 거래구분
        indutyId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
        dateType : "confirm",
        pymntYn : "",
      },
      // 코드 - 관리자 계좌이체조정 코드
      dayList : [
        {text : '거래일자', val : 'confirm'},
        {text : '지급일자', val : 'pay'},
      ],
      // 코드 - 관리자 계좌이체조정 코드
      codeList : [
        {text : '전체', val : ''},
        {text : '지급', val : 'Y'},
        {text : '미지급', val : 'N'},
      ],
   
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mberName'},
        {text : '판매자아이디', val : 'indutyId'},
        {text : '사업자번호', val : 'bizrno'},
  
      ],
    }
  },
  methods: {
    ...mapActions('accountStore',[
      'fetch_account_bank_trans_list',
    ]),
    ...mapMutations(['SET_ALERT_DIALOG']),

    excelDown(){
      this.SET_ALERT_DIALOG({flag : true , message :'미지급내역만 다운로드됩니다.' , title : '주의사항'});
      const excelList = this.list.map(item =>{
        const retItem = {};
        if(item.pymntYn === '미지급'){
          if(item.hasOwnProperty("confmDtSub")) retItem.거래일자 = item.confmDtSub;
          if(item.hasOwnProperty("fpayDt")) retItem.지급일자 = item.fpayDt;
          if(item.hasOwnProperty("payType")) retItem.코드 = item.payType;
          if(item.hasOwnProperty("bankName")) retItem.은행명 = item.bankName;
          if(item.hasOwnProperty("accountNo")) retItem.계좌번호 = item.accountNo;
          if(item.hasOwnProperty("payType")) retItem.코드 = item.payType;
          if(item.hasOwnProperty("bankFee")) retItem.수수료 = item.bankFee ;
          if(item.hasOwnProperty("payAmount")) retItem.입금금액 = item.payAmount;
          if(item.hasOwnProperty("cnt")) retItem.예금주 = item.cnt;
          return retItem;
        }
      });
      this.excelEvent(excelList ,`은행계좌이체-${this.payload.dateStart}-${this.payload.dateEnd} ` ,'미지급내역' );
   
   },
    searchEvent(){
      this.payload.mberName = '';
      this.payload.indutyId= '';
      this.payload.bizNum= '';
      this.payload.confmNo= '';
      if(this.option1 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option1 === 'bizrno') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'confmNo') {
        this.payload.confmNo = this.textVal1;
      }

      if(this.option2 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option2 === 'bizrno') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option2 === 'confmNo') {
        this.payload.confmNo = this.textVal1;
      }
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_account_bank_trans_list(retPayload);
    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>