<!--계좌이체조정 -->
<template>
  <v-container class="mt-3">
    
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">계좌이체조정</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="acnutTransfrYn('Y')">
          지급처리
        </v-btn>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="acnutTransfrYn('N')">
          미지급처리
        </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색시작일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu1"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateStart" 
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateStart" 
              locale="ko"
              no-title
              :max= 'this.dateStartMax'
              @input="dateMenu1 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <div class="caption pl-2 pt-3">
            검색종료일
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-menu
            v-model="dateMenu2"
            :close-on-content-click="false"
            :nudge-right="40"
            single-line
            min-width="290px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="payload.dateEnd"
                label="날짜선택"
                readonly
                v-bind="attrs"
                v-on="on"
                single-line
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="payload.dateEnd" 
              locale="ko"
              no-title
              :max='new Date().toISOString().substr(0, 10)'
              @change="dateEndEvent"
              @input="dateMenu2 = false">
            </v-date-picker>
          </v-menu>
        </v-col>
       
      </v-row>
      <v-row dense>
        <v-col cols=4 xs=12 sm=12 md=4 lg=4 xl=4>
          <v-autocomplete
            class="caption"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
        <v-col cols=4 xs=12 sm=12 md=4 lg=4 xl=4>
          <v-select
            class="caption"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.payType"
            :items="this.codeList"
            label="관리자_계좌이체조정" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=4 xs=12 sm=12 md=4 lg=4 xl=4>
          <v-select
            class="caption"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.pymntYn"
            :items="this.jigupList"
            label="관리자_계좌이체조정_지급구분" 
            outlined
          >
          </v-select>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option1"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal1"
             v-model="textVal1"
           ></v-text-field>
         </v-col>
         <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
           <v-select
             class="caption pl-2"
             dense
             item-text="text"
             item-value="val"
             v-model="option2"
             :items="optionList"
             label="옵션" 
             outlined
           >
           </v-select>
         </v-col>
         <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
           <v-text-field
             class="caption pr-2"
             dense
             name="name"
             label="검색어입력"
             id="textVal2"
             v-model="textVal2"
           ></v-text-field>
         </v-col>
      </v-row>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="list"
        :item-key='id'
        dense
        dark
        show-select
        :single-select="singleSelect"
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      <template v-slot:[`item.splpc`]="{ item }">
        {{ item.splpc | currency}} 원
      </template>

      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions, mapMutations } from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
import api from "../../api/account";
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '계좌이체조정';
    this.$store.state.overLay = false;
  },
  computed : {
    list(){
      let id=1;
      const list = this.$store.state.accountStore.accountTransList.map(item=>{
        let retObject = item;
        retObject.id = id;
        id++;
        return retObject;
      })

      return list;
    }
  },
  data() {
    return {
      selected : [],
      singleSelect : false,
      headers : [
        {text: '거래일자', value:'confmDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '영수증번호', value:'rciptNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '코드', value:'payType', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '은행명', value:'bankName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '계좌번호', value:'accountNO', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '지급구분', value:'pymntYn', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '지급액', value:'splpc', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '예금주', value:'depositor', class :'d-flexcaption font-weight-bold align-center pt-1'},
        
      ],

      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        inoutCode : "", // 거래구분
        indutyId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
      },
      
      // 코드 - 관리자 계좌이체조정 코드
      codeList : [
        {text : '전체', val : ''},
        {text : '일반', val : 'N'},
        {text : '익일입금', val : 'T'},
      ],
      // 코드 - 관리자_계좌이체조정_지급구분
      jigupList : [
        {text : '전체', val : ''},
        {text : '지급', val : 'Y'},
        {text : '미지급', val : 'N'},
      ],
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mberName'},
        {text : '판매자아이디', val : 'indutyId'},
        {text : '사업자번호', val : 'bizrno'},
        {text : '승인번호', val : 'confmNo'},
      ],
    }
  },
  methods: {
    ...mapActions('accountStore',[
      'fetch_account_trans_list',
    ]),
    ...mapMutations(['SET_OVERLAY']),
    acnutTransfrYn(val){
      const tempArr = [];
      this.SET_OVERLAY(true);
      this.selected.forEach(item => {
        let ynPayload = {
          mberCode : '',
          mberCodeSn : '', 
          rciptNo : '' , 
          pymntYn : ''
        } 
        ynPayload.mberCode = item.mberCode;
        ynPayload.mberCodeSn = item.mberCodeSn;
        ynPayload.rciptNo = item.rciptNo;
        ynPayload.pymntYn = val;
        tempArr.push(ynPayload);
      });
      
      api.acnutTransfrYn(tempArr)
      .then(()=>{
        this.searchEvent();
        this.SET_OVERLAY(false);
      }).catch(error => {
        console.error(error);
        this.SET_OVERLAY(false);
      })

    },
 
    searchEvent(){
      this.payload.mberName = '';
      this.payload.indutyId= '';
      this.payload.bizNum= '';
      this.payload.confmNo= '';
      if(this.option1 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option1 === 'bizrno') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'confmNo') {
        this.payload.confmNo = this.textVal1;
      }

      if(this.option2 === 'mberName') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === 'indutyId') {
        this.payload.indutyId = this.textVal1;
      }
      else if(this.option2 === 'bizrno') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option2 === 'confmNo') {
        this.payload.confmNo = this.textVal1;
      }
      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_account_trans_list(retPayload);
    }
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>