<!-- 매출조정 -->
<template>
    <v-container>
      <v-row dense>
        <v-col cols=12>
           <v-card dark color="deep-purple darken-3">
             <v-app-bar dense class="deep-purple lighten-1 white--text">
               <span class="subtitle-1 font-weight-black">매출조정</span>
               <v-spacer></v-spacer>
               <v-divider light vertical inset></v-divider>
               <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
                조회
              </v-btn>
             </v-app-bar>
             <v-row dense>
               <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색시작일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu1"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateStart" 
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateStart" 
                    locale="ko"
                    no-title
                    :max= 'this.dateStartMax'
                    @input="dateMenu1 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  검색종료일
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-menu
                  v-model="dateMenu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  single-line
                  min-width="290px"
                  >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      dense
                      v-model="payload.dateEnd"
                      label="날짜선택"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      single-line
                    ></v-text-field>
                  </template>
                  <v-date-picker 
                    v-model="payload.dateEnd" 
                    locale="ko"
                    no-title
                    :max='new Date().toISOString().substr(0, 10)'
                    @change="dateEndEvent"
                    @input="dateMenu2 = false">
                  </v-date-picker>
                </v-menu>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  대리점목록
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                 <v-autocomplete
                  class="caption pr-2"
                  :items="this.dealerList"
                  label="직접입력 & 목록선택"
                  outlined
                  dense
                  item-text="cmpnm"
                  item-value="bizCode"
                  v-model="payload.bizCode"
                >
                  <template v-slot:no-data>
                    대리점이 존재하지 않습니다
                  </template>
                </v-autocomplete>
              </v-col>

              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  직거래상태
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  :style="{fontSize:'5px'}"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="payload.calculateType"
                  :items="calculateList"
                  label="전체" 
                  single-line
                >
                </v-select>
              </v-col>
              <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                <div class="caption pl-3 pt-1">
                  매출구분
                </div>
              </v-col>
              <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
                <v-select
                  class="caption pr-2"
                  dense
                  item-text="text"
                  item-value="val"
                  v-model="payload.delngSeCode"
                  
                  :items="adjList"
                  label="전체" 
                  single-line
                >
                </v-select>
              </v-col>
             </v-row>
             <v-row dense>
                <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option1"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal1"
                    v-model="textVal1"
                  ></v-text-field>
                </v-col>
                <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
                  <v-select
                    class="caption pl-2"
                    dense
                    item-text="text"
                    item-value="val"
                    v-model="option2"
                    :items="optionList"
                    label="옵션" 
                    outlined
                  >
                  </v-select>
                </v-col>
                <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
                  <v-text-field
                    class="caption pr-2"
                    dense
                    name="name"
                    label="검색어입력"
                    id="textVal2"
                    v-model="textVal2"
                  ></v-text-field>
                </v-col>
              </v-row>
           </v-card>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col>
          <v-btn block @click="dialogInsert1Event(true)">
            카드매출등록
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="dialogInsert3Event(true,'매출일괄입력')">
            카드매출일괄등록
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="dialogInsert2Event(true)">
            현금매출등록
          </v-btn>
        </v-col>
        <v-col >
          <v-btn block @click="dialogInsert3Event(true,'현금매출일괄등록')">
            현금매출일괄등록
          </v-btn>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols=12>
          <v-data-table
            :headers="headers"
            :items="list"
            dense
            dark
            no-results-text="조회된 데이터가 없습니다."
            no-data-text="데이터를 조회를 하세요"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-arrow-collapse-left',
              lastIcon: 'mdi-arrow-collapse-right',
              prevIcon: 'mdi-arrow-left',
              nextIcon: 'mdi-arrow-right',
              itemsPerPageText : '표시갯수 :',
              itemsPerPageOptions : [20 , 50 , 100 , -1]
            }"
            @click:row="detailEvent"
          >
          <template v-slot:[`item.tot`]="{ item }">
            {{ item.tot | currency}} 원
          </template>
          <template v-slot:[`item.fee`]="{ item }">
            {{ item.fee | currency}} 원
          </template>
          <template v-slot:[`item.pay`]="{ item }">
            {{ item.pay | currency}} 원
          </template>
          </v-data-table>
        </v-col>
      </v-row>
      <AdjustEdit :dialog='dialog' :dialogDate='dialogDate' @dialogEvent="dialogEvent"/>
      <DelngCardInsert :dialog='dialogCardInsert' @dialogEvent="dialogInsert1Event"/>
      <DelngCashInsert :dialog='dialogCashInsert' @dialogEvent="dialogInsert2Event"/>
      <DelngListInsert :dialog='dialogListInsert' @dialogEvent="dialogInsert3Event" :title='dialogListTitle' />
    </v-container>
</template>
<script>
import { mapActions , mapMutations } from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject} from "@/util/filter.js";
import AdjustEdit from "@/components/AdjustEdit";
import DelngCardInsert from "@/components/DelngCardInsert";
import DelngCashInsert from "@/components/DelngCashInsert";
import DelngListInsert from "@/components/DelngListInsert";
export default {
  created() {
    document.title = '매출조정';
    this.$store.state.overLay = false;
  },
  mounted() {
    this.dialogCardInsert = false;
    this.dialogCashInsert = false;
    this.dialogListInsert = false;
    this.dialog = false;
  },
  updated() {
  },

  components : {
    AdjustEdit,
    DelngCardInsert,
    DelngCashInsert,
    DelngListInsert,
  },
  mixins: [excel , commInfo],
  data() {
    return {
      year : 0,
      banki : 0 ,
      bangi : 0,
      month : 0,
      payload : {
        dateStart : new Date().toISOString().substr(0, 10), // 검색시작일
        dateEnd : new Date().toISOString().substr(0, 10), // 검색종료일
        memberBizeCode : "0000002", // 로그인 세션 완성후 이걸로 변경
        bizCode : "", // 대리점코드
        mberName : "",  // 판매자 성명
        confmNo : "", // 승인번호
        userId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호 
        delngSeCode : '',
        calculateType:'',
      },
      adjList : [
        {text : '전체' , val : ''},
        {text : '카드' , val : 'card'},
        {text : '현금' , val : 'cash'},
      ],
      // 직거래상태
      calculateList : [
        {text : '전체' , val : ''},
        {text : '유니코아' , val : 'U'},
        {text : 'KSNET직정산' , val : 'K'}],

      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mber_name'},
        {text : '판매자아이디', val : 'mberId'},
        {text : '승인번호', val : 'issueCmpnyNm'},    
        {text : '사업자번호', val : 'bizNum'},
      ],

      headers: [
       {text: '거래일자', value:'confmdt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '대리점' , value:'cmpnm' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
       {text: '코드', value:'payType', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자성명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '구분', value:'delngGb', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '승인번호', value:'confmNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '총판매금액', value:'tot', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료율(%)', value:'feeRate', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '수수료', value:'fee', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '지급금액', value:'pay', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '결제여부', value:'cardDeleteNm', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '취소자', value:'cancelMber', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '취소일자', value:'cancelDt', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '취소사유', value:'bigo', class :'d-flexcaption font-weight-bold align-center pt-1'},       
       {text: '취소가능', value:'cardDeleteYn', class :'d-flexcaption font-weight-bold align-center pt-1'},       
     ],

      dialog : false,
      dialogCardInsert : false,
      dialogCashInsert : false,
      dialogListInsert : false,
      dialogListTitle : '매출일괄입력',
      dialogDate : {},
    }
  },
  computed : {
    list(){
      try {
        return this.$store.state.agencyStore.adjustList;
      } catch (error) {
        console.error(error);
        return [];
      }
    },
  },

  methods: {
    ...mapActions('agencyStore' , 
      ['fetch_adjust_list']),
    dialogEvent(val){
      this.dialog = val;
    },
    dialogInsert1Event(val){
      this.dialogCardInsert = val;
    },
    dialogInsert2Event(val){
      this.dialogCashInsert = val;
    },
    dialogInsert3Event(val , title){
      this.dialogListInsert = val;
      if(!!title) this.dialogListTitle = title;
      else this.dialogListTitle = '매출일괄입력';
    },
    detailEvent(val){
      this.dialog = true;
      this.dialogDate = val;

    },
    searchEvent(){
       this.payload.mberName = '';
       this.payload.confmNo= '';
       this.payload.userId= '';
       this.payload.bizNum= '';
      if(this.option1 === 'mber_name') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'issueCmpnyNm') {
        this.payload.confmNo = this.textVal1;
      }
      else if(this.option1 === 'mberId') {
        this.payload.mberId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }

      if(this.option2 === 'mber_name') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === 'issueCmpnyNm') {
        this.payload.confmNo = this.textVal1;
      }
      else if(this.option2 === 'mberId') {
        this.payload.mberId = this.textVal1;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }


      const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_adjust_list(retPayload);
    }
  },
}
</script>

<style>

</style>