<template>
  <v-navigation-drawer
    
    temporary
    absolute
    v-model="transdrawer"
    
    dark
    color="indigo darken-3"
  >
    <v-system-bar height=30 color="green darken-2" >
      <v-icon class="pr-2" >mdi-magnify</v-icon>
      <span class="subtitle-2">
      거래내역검색
      </span>
    </v-system-bar>
    <v-list color="blue-grey darken-1" dense>
      <v-list-item>
        <v-subheader class="subtitle-1 font-weight-black"><v-icon class="pr-2">mdi-handshake-outline</v-icon>대리점선택</v-subheader>
      </v-list-item>
    </v-list>
    <v-list dense>
      <v-list-item>
        <v-select
          class="pt-1"
          dense
          reverse
          smallChips
          item-text="cmpnm"
          item-value="bizCode"
          v-model="bizCode"
          :items="dealerList"
          label="대리점목록" 
        >
        </v-select>
      </v-list-item>
    </v-list>
    <v-list color="blue-grey darken-1" dense>
      <v-list-item>
        <v-subheader class="subtitle-1 font-weight-black"><v-icon class="pr-2">mdi-calendar-blank</v-icon>검색기간</v-subheader>
      </v-list-item>
    </v-list>
    <v-list>
      <v-list-item>
        <v-list-item-avatar>
          <v-icon class="mx-auto">mdi-calendar</v-icon>
        </v-list-item-avatar>
      
        <v-menu
          v-model="calMenu1"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              dense
              v-model="dateStart"
              label="검색시작일"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker 
            v-model="dateStart" 
            locale="ko"
            no-title
            @input="calMenu1 = false">
          </v-date-picker>
        </v-menu>
      </v-list-item>
      <v-list-item>
        <v-list-item-avatar>
          <v-icon>mdi-calendar</v-icon>
        </v-list-item-avatar>
          <v-menu
          v-model="calMenu2"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                dense
                v-model="dateEnd"
                label="검색종료일"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker 
              v-model="dateEnd" 
              locale="ko"
              no-title
              @input="calMenu2 = false">
            </v-date-picker>
          </v-menu>
       
        </v-list-item>
      </v-list>

      <v-list color="blue-grey darken-1" dense>
        <v-list-item>
          <v-subheader class="subtitle-1"><v-icon class="pr-2">mdi-calculator-variant-outline</v-icon> 정산옵션</v-subheader>
        </v-list-item>
      </v-list>
      <v-list>
         
      <v-list-item class="pt-4" color="red">

          <v-select
            dense
            reverse
            v-model="value"
            :items="items1"
            label="거래구분 - 카드/현금"
          ></v-select>

      </v-list-item>
      <v-list-item>

          <v-select
          dense
            reverse
            v-model="value"
            :items="items1"
            label="정산구분"
          ></v-select>
    
      </v-list-item>
      <v-list-item>
   
          <v-select
          dense
            reverse
            v-model="value"
            :items="items1"
            label="스와이프구분"
          ></v-select>
     
      </v-list-item>
      <v-list-item>

          <v-select
          dense
            reverse
            v-model="value"
            :items="items1"
            label="직정산구분"
          ></v-select>
  
      </v-list-item>
  </v-list>
  
 </v-navigation-drawer>
</template>

<script>
export default {
  data() {
    return {
      dateStart : '',
      dateEnd : '',
      value : '',
      calMenu1 : null,
      calMenu2 : null,
      transdrawer : false,
      bizCode : [],
      items1 : [
        {text : '전체이전선택' , val : 'all'},
        {text : '카드결제' , val : 'CARD_ISSUE'},
        {text : '현금영수증결제' , val : 'CASH_RCIPT_ISSUE'},
        {text : '카드취소' , val : 'CARD_CNCL'}],
      dealerList : [
        {cmpnm : '대리점전체' , bizCode : ''},
        {cmpnm : '대리점1' , bizCode : 'bizcode1'},
        {cmpnm : '대리점2' , bizCode : 'bizcode2'},
        {cmpnm : '대리점3' , bizCode : 'bizcode3'},
        {cmpnm : '대리점4' , bizCode : 'bizcode4'},
        {cmpnm : '대리점5' , bizCode : 'bizcode5'},
        {cmpnm : '대리점6' , bizCode : 'bizcode6'},
        {cmpnm : '대리점7' , bizCode : 'bizcode7'},
        {cmpnm : '대리점8' , bizCode : 'bizcode8'},
        {cmpnm : '대리점9' , bizCode : 'bizcode9'},
        {cmpnm : '대리점10' , bizCode : 'bizcode10'},
        {cmpnm : '대리점11' , bizCode : 'bizcode11'},
      ]
    }

  },
  computed: {
  },

  methods: {
  },
}
</script>

