  
<template>
  <v-dialog
    v-model="dialog"
    persistent
    scrollable
    max-width="500px"
  >
  <v-form ref="form" v-model="valid">
    <v-card class="overflow-hidden">
      <v-app-bar
        color="deep-purple darken-1"
        dense
        dark
        absolute
        elevate-on-scroll
      ><v-icon class="pr-3">mdi-credit-card-plus</v-icon>
        <v-toolbar-title>카드매출등록</v-toolbar-title>
        <v-spacer></v-spacer>
        
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>

        <v-card-text style="height: 400px;" class="pt-15" >
          <v-row>
            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="usid"
                label="판매자ID"
                v-model="payload.usid"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="issueCmpnyNm"
                label="발급사명"
                v-model="payload.issueCmpnyNm"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="cardNo"
                label="카드번호"
                
                v-model="payload.cardNo"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-select
                class="caption pt-2"
                dense
                :items="instlmtMonthList"
                item-text="text"
                item-value="val"
                v-model="payload.instlmtMonth"
                label="할부개월수"
                outlined
                required  
              ></v-select>
            </v-col>

            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="confmDt"
                label="승인일자"
                v-model="payload.confmDt"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="confmNo"
                label="승인번호"
                v-model="payload.confmNo"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-text-field
                class="caption pr-2"
                dense
                name="splpc"
                label="금액"
                v-model="payload.splpc"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-subtitle style="color:red;font-weight:700">
          * 승인일자와 승인번호를 정확히 입력해야합니다.
        </v-card-subtitle>
        <v-divider></v-divider>
         <v-card-actions>
           <v-spacer></v-spacer>
        <v-btn block :disabled="!valid" color="primary" @click="saveEvent">
          <v-icon left>mdi-content-save</v-icon>
          등록
          </v-btn>
        </v-card-actions>
    </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
// 매출등록
export default {
  props :['dialog'],
  data() {
    return {
      valid : true,
      // 할부개월수
      instlmtMonthList : [
        {text : '일시불' , val : 1},
        {text : '2개월' , val : 2},
        {text : '3개월' , val : 3},
        {text : '4개월' , val : 4},
        {text : '5개월' , val : 5},
        {text : '6개월' , val : 6},
        {text : '7개월' , val : 7},
        {text : '8개월' , val : 8},
        {text : '9개월' , val : 9},
        {text : '10개월' , val : 10},
        {text : '11개월' , val : 11},
        {text : '12개월' , val : 12},
        {text : '24개월' , val : 24},
      ],
      payload : {
        usid : '',
        issueCmpnyNm : '',
        cardNo : '',
        instlmtMonth : 0,
        confmDt : '',
        confmNo : '',
        splpc : 0
      }
    }
  },
  methods: {
    saveEvent(){
      console.log(111);
    },
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
  },
}
</script>

