<!--세무서신고 -->
<template>
  <v-container class="mt-2">
    <v-card dark color="deep-purple darken-3" >
      <v-app-bar dense class="deep-purple lighten-1 white--text">
        <span class="subtitle-1 font-weight-black">세무서신고</span>
        <v-spacer></v-spacer>
        <v-divider light vertical inset></v-divider>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="txtDown">
          파일생성
        </v-btn>
       <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
          엑셀
        </v-btn>
       <v-divider light vertical inset></v-divider>
        <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent">
         조회
       </v-btn>
      </v-app-bar>
      <v-row dense class="mx-auto" >
        <v-col cols=12 xs=12 sm=6 md=3 lg=3 xl=3>
          <v-autocomplete
            class=" pl-2 pr-2 pt-1"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
        </v-col>
        <v-col cols=12 xs=12 sm=6 md=3 lg=3 xl=3>
          <v-select
            class=" pl-2 pr-2 pt-1"
            dense
            item-text="text"
            item-value="val"
            v-model="payload.paymentType"
            :items="reportsList"
            label="결제종류" 
            outlined

          >
          </v-select>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=1 lg=1 xl=1>
          <div class="caption pl-3 pt-1">
            연도선택
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=2 lg=2 xl=2>
          <v-select
            class="caption pr-2"
            dense
            v-model="year"
            :items="yearList"
            label="전체" 
            single-line
            @change="yearChange('year')"
          >
          </v-select>
        </v-col>
        <v-col cols=4 xs=4 sm=3 md=1 lg=1 xl=1>
          <div class="caption pl-3 pt-1">
            분기선택
          </div>
        </v-col>
        <v-col cols=8 xs=8 sm=9 md=2 lg=2 xl=2>
          <v-select
            class="caption pr-2"
            dense
            item-text="text"
            item-value="val"
            v-model="bunki"
            :items="bunkiList"
            label="전체" 
            single-line
            @change="yearChange('bunki')"
          >
          </v-select>
        </v-col>

        
      </v-row>
      <v-row dense>
        <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
          <v-select
            class="caption pl-2"
            dense
            v-model="option1"
            :items="optionList"
            label="옵션" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
          <v-text-field
            class="caption pr-2"
            dense
            name="name"
            label="검색어입력"
            id="textVal1"
            v-model="textVal1"
          ></v-text-field>
        </v-col>
        <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
          <v-select
            class="caption pl-2"
            dense
            v-model="option2"
            :items="optionList"
            label="옵션" 
            outlined
          >
          </v-select>
        </v-col>
        <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
          <v-text-field
            class="caption pr-2"
            dense
            name="name"
            label="검색어입력"
            id="textVal2"
            v-model="textVal2"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"   
      >
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions} from 'vuex'
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import {currency , removeCharObject , getTextLength} from "@/util/filter.js";
import moment from 'moment';
export default {
  mixins: [excel , commInfo],
  created() {
    document.title = '세무서신고';
    this.$store.state.overLay = false;
    this.getYearList();
    this.yearChange('year');
  },
  computed : {
    list(){
      try {
        const list = this.$store.state.taxStore.taxReportList.map(item=>{
          item.splpc = currency(item.splpc);
          item.cnt = currency(item.cnt);
          return item;
        })
        return list;
      } catch (error) {
        return [];
      }
    },
    sumInfo(){
      let retInfo = {cnt : 0 , cntsum : 0 , splpcsum : 0};
      try {
        this.$store.state.taxStore.taxReportList.forEach(item=>{
          retInfo.splpcsum =  Number(retInfo.splpcsum) +  Number(item.splpc.replaceAll(',','')) ;
          retInfo.cntsum =  Number(retInfo.cntsum) + Number(item.cnt);
          retInfo.cnt =  Number(retInfo.cnt) + 1;
        });

        return retInfo;
      } catch (error) {
        return retInfo;
      }
    }
  },
  data() {
    return {
      headers : [
        {text: '판매자ID', value:'indutyId', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '판매자명', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '사업자번호', value:'bizNum', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '핸드폰번호', value:'mberMobile', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '결제구분', value:'delngSeCode', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '결제월', value:'confmDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '금액', value:'splpc', class :'d-flexcaption font-weight-bold align-center pt-1'},
        {text: '건수', value:'cnt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       
      ],
      option1 : '',
      option2 : '',
      textVal1 : '',
      textVal2 : '',
      year : Number(new Date().toISOString().substr(0, 4)),
      bunki : 0,
      payload : {
        dateStart : "",
        dateEnd : "",
        bizCode : "", // 대리점코드
        paymentType : "CARD_ISSUE",
      },
      // 년도목록
      yearList : [],
      // 분기리스트
      bunkiList : [
        {text : '분기선택' , val : 0},
        {text : '1분기' , val : 1},
        {text : '2분기' , val : 2},
        {text : '3분기' , val : 3},
        {text : '4분기' , val : 4},
      ],
      // 관리자세무서신고 결제종류
      reportsList : [
        {text : '전체' , val : ''},
        {text : '카드결제' , val : 'CARD_ISSUE'},
        {text : '현금영수증결제' , val : 'CASH_RCIPT_ISSUE'},
      ],
      // 옵션
      optionList : [ 
        '전체',
        '판매자성명',
        '판매자아이디',
        '사업자번호',

      ],
    }
  },
  methods: {
    ...mapActions('taxStore',[
      'fetch_taxreport_list',
    ]),
    // 현재 년도 구하기및 이전 년도 목록만들기
    getYearList(){
      this.yearList = [];
      const today = new Date();
      let nowYear = today.getFullYear();
      this.year = nowYear;
      this.yearList.push(nowYear);
      while (nowYear > 2015 ) {
        nowYear--;
        this.yearList.push(nowYear);
      }
    },
    txtDown(){
      const data = JSON.stringify(this.list)
      // 헤더영역데이터
      let hdText = 'HD'+ this.year + this.bunki + '113';
      hdText += new Date().toISOString().substr(0, 10).replaceAll('-', '');
      hdText += this.$store.state.authStore.bizInfo.bizrno;
      hdText += this.$store.state.authStore.bizInfo.cmpnm;
      // 공백추가 
      for (let index = getTextLength(this.$store.state.authStore.bizInfo.cmpnm); index < 40; index++) {
        hdText +=  " "; 
      }
      hdText += this.payload.dateStart.replaceAll('-', '');
      hdText += this.payload.dateEnd.replaceAll('-', '');
      for (let index = 0; index < 126; index++) {
        hdText +=  " ";
      }
        hdText += '\n';

      // 중간 데이터 영역      
      let rdTextR = '';
      let rdNum = 0;
      let limitChar = "";

      let preBizNum = "";
      let preIndutyId = "";
      let preMberJumi = "";
      let preMberMobile = "";

      this.list.forEach(item => {
        rdNum++;
        rdTextR = rdTextR +'RD' + this.year + this.bunki + this.$store.state.authStore.bizInfo.bizrno; // 레코드구분 + 결제년도 + 분기구분 + 사업자등록번호
        limitChar = getTextLength(rdNum);
        if(limitChar > 6)limitChar=6;
        for (let index = 0; index < 6- limitChar; index++) {
          rdTextR = rdTextR + "0";
        }
        rdTextR = rdTextR + rdNum;

        let bizNum = "";
        if(!!item.bizNum) {
          preBizNum = item.bizNum;
        }else {
          bizNum =   preBizNum;
        }
        
        bizNum = bizNum.replaceAll(" " , "").replaceAll("-" , "").substring(0,10);
        limitChar = getTextLength(bizNum);
        if(limitChar>10)limitChar=10
        for (let index = 0; index < 10-limitChar; index++) {
          rdTextR = rdTextR + "*";
          
        }
        rdTextR += bizNum;
        let mberJumi = "";
        if(!!item.mberJumi){
          mberJumi = item.mberJumi;
          preMberJumi = item.mberJumi;
        }else {
          mberJumi = preMberJumi;
        }

        if(!!mberJumi) mberJumi = mberJumi.replaceAll(" " , "").replaceAll("-" , "")
        limitChar = getTextLength(mberJumi);
        if(limitChar >13) limitChar =13;
        for (let index = 0; index < 13-limitChar; index++) {
          mberJumi =  mberJumi + " ";
        }
        rdTextR = rdTextR +mberJumi;

        let indutyId="";
        if(!!item.indutyId) {
          indutyId = item.indutyId;
          preIndutyId = item.indutyId;
        }else {
          indutyId = preIndutyId;
        }
        limitChar = getTextLength(indutyId);
        if(limitChar >12) limitChar =12;
        for (let index = 0; index < 12-limitChar; index++) {
          indutyId =  indutyId + " ";
        }
        rdTextR = rdTextR +indutyId;
        if(!!item.confmDt)rdTextR += item.confmDt.replaceAll(" " , "").replaceAll("-" , "");
        else rdTextR += item.confmDt;
        rdTextR += " "; // 결제구분
        // 결제건수
        let cnt = item.cnt;
        limitChar = getTextLength(cnt);
        if(limitChar > 6)  limitChar = 6;
        for (let index = 0; index < 6- limitChar ; index++) {
          rdTextR += '0';  
        }
        rdTextR += item.cnt;
        rdTextR += '0'; //봉사료금액음수표시
        rdTextR += "000000000000000";	//봉사료금액
        rdTextR += "0";	//봉사료제외금액음수표시
        //봉사료제외금액

        let splpc = 0;
        if(typeof(item.splpc) === 'string'  ) item.splpc.replaceAll("," , "");
        limitChar = getTextLength(splpc);
        if(limitChar > 15)  limitChar = 15;
        for (let index = 0; index < 15- limitChar ; index++) {
          rdTextR += '0';  
        }
        rdTextR += splpc;
        rdTextR += '0';  
        for (let index = 0; index < 15- limitChar ; index++) {
          rdTextR += '0';  
        }
        rdTextR += splpc;
        for (let index = 0; index < 20; index++) {
          rdTextR = rdTextR + " ";
        }
        rdTextR += "00";  
          // 의뢰업체 전화번호
        for (let index = 0; index < 11 ; index++) {
          rdTextR += " ";  
        }
        //의뢰업체 휴대폰번호
        let mberMobile = "";
        if(item.mberMobile){
          mberMobile = item.mberMobile;
          preMberMobile = item.mberMobile;
        }else {
          mberMobile = preMberMobile;
        }
        mberMobile = mberMobile.replaceAll('-', '').substring(0,11);
        limitChar = getTextLength(mberMobile);
        if(limitChar > 11)  limitChar = 11;
        for (let index = 0; index < 11- limitChar ; index++) {
          rdTextR += " ";  
        }
        rdTextR += mberMobile;

        for (let index = 0; index < 40 ; index++) {
          rdTextR += " ";  
        }
        if(item.delngSeCode == 'CARD_ISSUE'){
          rdTextR += "8";
        }else {
          rdTextR += "6";
        }
        rdTextR += "     "; 
        rdTextR += '\n';
      });
      // tail 정보 생성
      let tdText = 'TD' + this.year + this.bunki + this.$store.state.authStore.bizInfo.bizrno; 
      limitChar = getTextLength(this.sumInfo.cnt);
      for (let index = 0; index < 9- limitChar ; index++) {
        tdText += "0";  
      }
      tdText = tdText + this.sumInfo.cnt;
      limitChar = getTextLength(this.sumInfo.cntsum);
      for (let index = 0; index < 9- limitChar ; index++) {
        tdText += "0";  
      }
      tdText = tdText + this.sumInfo.cntsum;
      tdText += "0";//봉사료금액 계 음수표시
      tdText += String("000000000000000");	//봉사료금액 계
      tdText += "0";	//봉사료제외결제금액 계 음수표시
      limitChar = getTextLength(this.sumInfo.splpcsum);
      for (let index = 0; index < 15- limitChar ; index++) {
        tdText += "0";  
      }
      tdText += this.sumInfo.splpcsum;
      tdText += "0";
      for (let index = 0; index < 15- limitChar ; index++) {
        tdText += "0";  
      }
      tdText += this.sumInfo.splpcsum;
      for (let index = 0; index < 127 ; index++) {
        tdText += " ";  
      }
      const fileName = 'E_' + this.$store.state.authStore.bizInfo.bizrno + this.year + this.bunki+ "_1" + ".txt"

      const blob = new Blob([hdText,rdTextR,tdText], {type: 'text/plain'})
      const e = document.createEvent('MouseEvents'),
      a = document.createElement('a');
      a.download = fileName;
      a.href = window.URL.createObjectURL(blob);
      a.dataset.downloadurl = ['text/json', a.download, a.href].join(':');
      e.initEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
      a.dispatchEvent(e);
    },

    excelDown(){
      const excelList = this.list.map(item =>{
        const retItem = {};
        
          if(item.hasOwnProperty("indutyId")) retItem.판매자ID = item.indutyId;
          if(item.hasOwnProperty("mberName")) retItem.판매자명 = item.mberName;
          if(item.hasOwnProperty("mberJumi")) retItem.주민번호 = item.mberJumi;
          if(item.hasOwnProperty("bizNum")) retItem.사업자번호 = item.bizNum;
          if(item.hasOwnProperty("companyName")) retItem.상호 = item.companyName;
          if(item.hasOwnProperty("mberMobile")) retItem.핸드폰번호 = item.mberMobile;
          if(item.hasOwnProperty("delngSeCode")) retItem.결제구분 = item.delngSeCode ;
          if(item.hasOwnProperty("confmDt")) retItem.결제월 = item.confmDt;
          if(item.hasOwnProperty("splpc")) retItem.금액 = item.splpc;
          if(item.hasOwnProperty("cnt")) retItem.건수 = item.cnt;
          return retItem;
        
      });
      this.excelEvent(excelList ,`세무서신고`+new Date().toISOString().substr(0, 10) ,'세금계산서' );
   },
    searchEvent(){
      this.payload.mberName = '';
      this.payload.usid = '';
      this.payload.bizNum = '';

      if(this.option1 === '판매자성명') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === '판매자아이디') { 
        this.payload.usid = this.textVal1;
      }
      else if(this.option1 === '사업자번호') { 
        this.payload.bizNum = this.textVal1;
      }

      if(this.option2 === '판매자성명') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option2 === '판매자아이디') { 
        this.payload.usid = this.textVal1;
      }
      else if(this.option2 === '사업자번호') { 
        this.payload.bizNum = this.textVal1;
      }
    
    const retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);

      this.fetch_taxreport_list(retPayload);
    },
    // 년도를 수정한경우
    yearChange(val){
      
      let tempDate = moment(String(this.year) );
      let tempNow = moment().format('YYYY-MM-DD');
      if(val === 'year'){
        this.banki=0;
        this.bunki=0;
        this.month=0;
        tempDate = moment(String(this.year) );
        this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
        this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
      }
      // 반기수정
      if(val==='ban'){
        this.bunki=0;
        this.month=0;
        if(this.banki===1){
          tempDate = moment(String(this.year+"01") );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          tempDate = moment(String(this.year+"06") );
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        }
        if(this.banki===2){
          tempDate = moment(String(this.year+"07") );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          tempDate = moment(String(this.year+"12") );
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        }
        if(this.banki===0){
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }
      // 분기수정
      if(val==='bunki'){
        this.banki=0;
        this.month=0;
        tempDate = moment(String(this.year))
        if(this.bunki >0 && this.bunki <5){
          tempDate = tempDate.quarter(this.bunki) ;
          this.payload.dateStart = tempDate.startOf('quarter').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('quarter').format('YYYY-MM-DD');
        } 
        else {
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }
      // 월수정
      if(val==='month'){
        this.banki=0;
        this.bunki=0;
        tempDate = moment(String(this.year));
        if(this.month >0 && this.month <13){
          if(this.month <10 )tempDate = moment(String(this.year)+ "0"+String(this.month) );
          else tempDate = moment(String(this.year)+String(this.month) );
          this.payload.dateStart = tempDate.startOf('month').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('month').format('YYYY-MM-DD');
        } 
        else {
          this.payload.dateStart = tempDate.startOf('year').format('YYYY-MM-DD');
          this.payload.dateEnd = tempDate.endOf('year').format('YYYY-MM-DD');
        }
      }
      
      if(this.payload.dateStart >  tempNow )this.payload.dateStart = tempNow;
      if(this.payload.dateEnd >  tempNow )this.payload.dateEnd = tempNow;
    },
  },
}
</script>

<style scoped>
  .confirm {
    background-color: chartreuse;
  } 
</style>