<template>
    <v-dialog
      v-model="dialog"
      max-width="600"
      persistent
    >
    <v-card > 
      <v-app-bar
        color="deep-purple darken-3"
        dense
        dark
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
        <v-sheet 
          class="overflow-y-auto"
          max-height="900">
        <v-card >
           <v-card-text>
             <v-container>
               <v-row> 
                <v-col cols=12>
                  <v-file-input
                    dense
                    v-model="fileData"
                    color="deep-purple accent-4"
                    class=""
                    label="매출파일등록"
                    @change="readFile" 
                    placeholder="등록 서식 파일에 내용을 작성후 업로드하세요"
                    prepend-icon="mdi-paperclip"
                    outlined

                  >
                    <template v-slot:selection="{ index, text }">
                      <v-chip
                        v-if="index < 2"
                        color="deep-purple accent-4"
                        dark
                        label
                        small
                      >
                        {{ text }}
                      </v-chip>

                      <span
                        v-else-if="index === 2"
                        class="overline grey--text text--darken-3 mx-2"
                      >
                        +{{ files.length - 2 }} File(s)
                      </span>
                    </template>
                  </v-file-input>
                </v-col>
                <v-col cols=12>
                  <v-btn block href="/download/매출일괄입력.xlsx" download v-if="this.title=='매출일괄입력'">
                    매출등록 서식파일 다운로드
                  </v-btn>
                  <v-btn block href="/download/현금매출일괄입력.xlsx" download v-if="this.title=='현금매출일괄등록'" >
                    현금매출 서식파일 다운로드
                  </v-btn>
                </v-col>
               </v-row>
             </v-container>
           </v-card-text>

        </v-card>
        </v-sheet>
        <v-divider></v-divider>
        <v-card-actions>
        <v-btn
          color="deep-purple dark-2"
          :style="{'color':'#fff'}"       
          block
          @click="saveEvent"
        >
          입력
        </v-btn>
      </v-card-actions>
    </v-card>

  </v-dialog>

</template>

<script>
import { mapActions} from 'vuex';
import commInfo from "@/mixins/comInfo";
import excel from "@/mixins/comInfo";
import XLSX from 'xlsx';
import api from "@/api/agencyManage.js";
export default {
  mixins: [commInfo,excel],
  props : ['dialog' , 'title'],

  created() {
    //this.$refs.bizrno.hint='';
  },
  computed: {
  },
  data() {
    return {
      fileData : null,
      payLoadArr : [],
    }
  },
  methods: {
    ...mapActions('',['']),
    readFile(event){
      // saveEvent 에서 파일입력 이벤트 잡아서 처리함
      console.log(event);
      //const file = event.target.files[0];
    },
    saveEvent(){
      this.payLoadArr = [];
      let reader = new FileReader();
      reader.readAsBinaryString(this.fileData);
      reader.onload= (evt) =>{
        let data = evt.target.result;
        const workbook = XLSX.read(data, {
          type: 'binary'
        });
        
        // real 씨트에 있는 내용만 처리한다.
        if(!workbook.Sheets['real']) {
          this.SET_ALERT_DIALOG({flag : true , message :'real 명의 시트가 없습니다.' , title : '시트' , type : 'error'});
          return;
        }
        if(!!workbook.Sheets['real']){
          const rowArr = XLSX.utils.sheet_to_json(workbook.Sheets['real']);
          rowArr.forEach(item =>{
            const retItem = {};
              // !!! 강조 백엔드 인증키 세션 인증 관련 기능 마무리후 삭제 예정
            retItem.memberMberCode = this.GET_AUTH_MEMBER_CODE;
            retItem.grade = this.GET_MEMBER_GRADE;
            if(this.title === '현금매출일괄등록'){
              for (const [key, value] of Object.entries(item)) {
                if(key === '판매자ID') retItem.usid = value;
                if(key === '승인번호') retItem.confmNo = value;
                if(key === '승인일자') retItem.confmDt = value;
                if(key === '승인시간') retItem.confmTime = value;
                if(key === '금액') retItem.splpc = value;
              }
            }else {
              for (const [key, value] of Object.entries(item)) {
                if(key === '발급사명') retItem.issueCmpnyNm = value;
                if(key === '카드번호') retItem.cardNo = value;
                if(key === '할부개월') retItem.instlmtMonth = value;
                if(key === '승인일시') retItem.confmDt = value;
                if(key === '승인번호') retItem.confmNo = value;
                if(key === '금액') retItem.splpc = value;
              }
            }

          this.payLoadArr.push(retItem);
          });
        }

        console.log(this.payLoadArr);
        //api.insertSeller(this.payLoadArr);
      }

  
      
    },

    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
  }
}
</script>

