<template>
  <v-dialog
    v-model="dialog"
    persistent
    max-width="600px"
  >
  <v-form ref="form" v-model="valid">
    <v-card class="overflow-hidden">
      <v-app-bar
        color="blue"
        dense
        dark
        absolute
        elevate-on-scroll
      >
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-spacer></v-spacer>
        
        <v-btn icon @click="closeEvent">
          <v-icon>mdi-close-circle</v-icon>
        </v-btn>
      </v-app-bar>
      <v-sheet max-height="600" class="overflow-y-auto mt-10">
        <v-card-text>
          <v-row>

            <v-col cols=12>
              <v-text-field
                class="caption"
                label="제목"
                v-model="dialogData.title"
              ></v-text-field>
            </v-col>
            <v-col cols=12>
              <v-select
                class="caption pt-2"
                dense
                v-model="dialogData.isactive"
                :items="viewList"
                item-text="text"
                item-value="val"
                label="표시" 
                outlined
              ></v-select>
            </v-col>

             <v-col cols=12>
              <v-textarea
                class="caption pr-2"
                dense
                label="내용"
                v-model="dialogData.content"
              ></v-textarea>
            </v-col>

          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
            <v-btn block :disabled="!valid" color="primary" @click="saveEvent" >
              <v-icon left>mdi-content-save</v-icon>
              저장
            </v-btn>
        </v-card-actions>
      </v-sheet>
    </v-card>
    </v-form>
  </v-dialog>
</template>

<script>

import commInfo from "@/mixins/comInfo";
import { mapActions } from 'vuex'
import api from "@/api/notice.js";
export default {
props :['dialog' , 'dialogData' , 'title'],
mixins: [commInfo],
data() {
  return {
    valid : true,
    viewList : [
      {text : '보이기' , val : 'Y'},
      {text : '숨기기' , val : 'N'}
    ]
  
  }
},

methods: {
    ...mapActions('noticeStore',[
      'fetch_notice_list',
    ]),
    closeEvent(){
      this.$emit('dialogEvent' , false);
    },
     saveEvent(){
      if(this.title === '공지사항 확인 수정'){
        api.updateNotice(this.dialogData)
        .then(_=>this.fetch_notice_list())
        .then(_=>this.$emit('dialogEvent' , false) )
        .catch(error => console.log(error));
      }else {
        api.saveNotice(this.dialogData)
        .then(_=>this.fetch_notice_list())
        .then(_=>this.$emit('dialogEvent' , false) )
        .catch(error => console.log(error));
       }

      
    },
    
  },
}
</script>

