import account from "../api/account";

const accountStore = {
  namespaced: true,
  state : {
    accountList : [],
    accountExcelList : [],
    accountTransList : [],
    bankAcntTransList :[],
    inOutSettingInfo : {},
  },
  getters : {},
  mutations : {
    set_account_list(state , list){
      state.accountList = list;
    },
    set_account_excel_list(state , list){
      state.accountExcelList = list;
    },
    set_account_trans_list(state , list){
      state.accountTransList = list;
    },
    set_bankacnt_trans_list(state , list){
      state.bankAcntTransList = list;
    },
    set_inout_setting_info(state , info){
      state.inOutSettingInfo = info;
    },
  },
  actions : {
    // 입출력내역목록
    async fetch_account_inout_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await account.accountList(payload);
        await commit('set_account_list',list);
      } catch (error) {
        console.error(error);
        // 에러이후 처리는 깊은 설계후 만든다.
      } finally{
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 정산용 엑셀목록
    async fetch_account_excel_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await account.accountExcelList(payload);
        await commit('set_account_excel_list',list);
      } catch (error) {
        console.error(error);
      } finally{
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 계좌이체목록
    async fetch_account_trans_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await account.accountTransList(payload);
        await commit('set_account_trans_list',list);
      } catch (error) {
        console.error(error);
      } finally{
        await commit('SET_OVERLAY',false, { root: true });
      }
    },

    // 은행계좌이체목록
    async fetch_account_bank_trans_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await account.bankAcntTransList(payload);
        await commit('set_bankacnt_trans_list',list);
      } catch (error) {
        console.error(error);
      } finally{
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 입출금정지정보조회
    async fetch_in_out_setting_info({commit}){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const info = await account.inOutSettingInfo();
        await commit('set_inout_setting_info',info);
      } catch (error) {
        console.error(error);
      } finally{
        await commit('SET_OVERLAY',false, { root: true });
      }
    },

  } 
}

export default accountStore;