import { request } from "./index";

// 대리점 / 판매자 관리
const agencyManage = {
  // 대리점 목록
  agencyList(payload){
    return request('post' , '/manage/agencyList' , payload);
  },
  // 대리점 정보 수정
  updateAgency(payload){
    return request('post' , '/manage/updateAgency' , payload);
  },
  // 대리점 정보 신규 저장
  insertAgency(payload){
    return request('post' , '/manage/insertAgency' , payload);
  },
  // 판매자 목록
  sellerList(payload){
    return request('post' , '/manage/sellerList' , payload);
  },
  // 기준대리점 정보 
  settingAgencyList(memberCode , agencyCode ){
    return request('get' , `/manage/settingAgencyList/${memberCode}/${agencyCode}`);
  },
  // 판매자 정보 입력 - 배열로 전달되어야 함
  insertSeller(payloadArr){
    return request('post' , '/manage/insertSeller' , payloadArr);
  },
  // 판매자 정보 수정 - 단건
  updateSeller(payload){
    return request('post' , '/manage/updateSeller' , payload);
  }
}

export default agencyManage;