<template>  
<v-container>
  <v-row dense>
    <v-col cols=12  >
      <v-card dark color="deep-purple darken-3">
        <v-app-bar dense class="deep-purple lighten-1 white--text">
          <span class="subtitle-1 font-weight-black">{{searchTitle}}</span>
          <v-spacer></v-spacer>
          <v-divider light vertical inset></v-divider>
            <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchChangeEvent(searchList[0].title)">
              {{ searchList[0].title }}
            </v-btn>
          <v-divider light vertical inset></v-divider>
          <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="searchEvent()">
            조회
          </v-btn>
          <v-divider light vertical inset></v-divider>
          <v-btn text color="teal lighten-4" class="subtitle-1 font-weight-black" @click="excelDown">
            엑셀
          </v-btn>
          <v-menu
            bottom
            left
            color="teal lighten-4"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                dark
                icon
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </template>

            <v-list>
              <v-list-item
                v-for="(item, i) in searchList"
                :key="i"
                @click="searchChangeEvent(item.title)"
              >
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-app-bar>
        
        <v-divider></v-divider>
        <v-row dense>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              검색시작일
            </div>
          </v-col>
          <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
            <v-menu
              v-model="dateMenu1"
              :close-on-content-click="false"
              :nudge-right="40"
              single-line
              min-width="290px"
              >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  dense
                  v-model="payload.dateStart" 
                  label="날짜선택"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  single-line
                ></v-text-field>
              </template>
              <v-date-picker 
                v-model="payload.dateStart" 
                locale="ko"
                no-title
                :max= 'this.dateStartMax'
                @input="dateMenu1 = false">
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              검색종료일
            </div>
          </v-col>
          <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
            <v-menu
              v-model="dateMenu2"
              :close-on-content-click="false"
              :nudge-right="40"
              single-line
              min-width="290px"
              >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  dense
                  v-model="payload.dateEnd"
                  label="날짜선택"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  single-line
                ></v-text-field>
              </template>
              <v-date-picker 
                v-model="payload.dateEnd" 
                locale="ko"
                no-title
                :max='new Date().toISOString().substr(0, 10)'
                @change="dateEndEvent"
                @input="dateMenu2 = false">
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              대리점목록
            </div>
          </v-col>
          <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
          <v-autocomplete
            class="caption pr-2"
            :items="this.dealerList"
            label="직접입력 & 목록선택"
            outlined
            dense
            item-text="cmpnm"
            item-value="bizCode"
            v-model="payload.bizCode"
          >
            <template v-slot:no-data>
              대리점이 존재하지 않습니다
            </template>
          </v-autocomplete>
          </v-col>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              결제상태
            </div>
          </v-col>
          <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
            <v-select
              class="caption pr-2"
              dense
              item-text="text"
              item-value="val"
              v-model="payload.delngSeCode"
              :items="delngSeCodeList"
              label="전체" 
              single-line
            >
            </v-select>
          </v-col>

          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              정산상태
            </div>
          </v-col>
          <v-col cols=8 xs=8 sm=9 md=4 lg=4 xl=4>
            <v-select
              class="caption pr-2"
              dense
              item-text="text"
              item-value="val"
              v-model="payload.delngPayType"
              :items="delngPayList"
              label="전체" 
              single-line
            >
            </v-select>
          </v-col>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              스와이프
            </div>
          </v-col>
          <v-col cols=8 xs=6 md=4 sm=9 lg=4 xl=4>
            <v-select
              class="caption pr-2"
              dense
              item-text="text"
              item-value="val"
              v-model="payload.tid"
              :items="tidList"
              label="전체" 
              single-line
            >
            </v-select>
          </v-col>
          <v-col cols=4 xs=6 md=2 sm=3 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              직거래상태
            </div>
          </v-col>
          <v-col cols=8 xs=6 md=4 sm=9 lg=4 xl=4>
            <v-select
              class=" caption pr-2"
              dense
              item-text="text"
              item-value="val"
              v-model="payload.calculateType"
              :items="calculateList"
              label="전체" 
              single-line
            >
            </v-select>
          </v-col>
          <v-col cols=4 xs=6 md=2 sm=3 lg=2 xl=2>
            <div class="caption pl-3 pt-1">
              동일카드
            </div>
          </v-col>
          <v-col cols=8 xs=6 md=4 sm=9 lg=4 xl=4>
            <v-select
              class="caption pr-2"
              dense
              item-text="text"
              item-value="val"
              v-model="payload.badCardSearch"
              :items="badCardSearchList"
              label="동일카드무시" 
              single-line
            >
            </v-select>
          </v-col>
          
        </v-row>
        <v-row dense>
          <v-col cols=4 xs=4 sm=3 md=2 lg=2 xl=2>
            <v-select
              class="caption pl-2"
              dense
              item-text="text"
              item-value="val"
              v-model="option1"
              :items="optionList"
              label="옵션" 
              outlined
            >
            </v-select>
          </v-col>
          <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
            <v-text-field
              class="caption pr-2"
              dense
              name="name"
              label="검색어입력"
              id="textVal1"
              v-model="textVal1"
            ></v-text-field>
          </v-col>
          <v-col cols=4 xs=4 md=2 sm=3 lg=2 xl=2>
            <v-select
              class="caption pl-2"
              dense
              item-text="text"
              item-value="val"
              v-model="option2"
              :items="optionList"
              label="옵션" 
              outlined
            >
            </v-select>
          </v-col>
          <v-col cols=8 xs=8 md=4 sm=9 lg=4 xl=4>
            <v-text-field
              class="caption pr-2"
              dense
              name="name"
              label="검색어입력"
              id="textVal2"
              v-model="textVal2"
            ></v-text-field>
          </v-col>
        </v-row>

      </v-card>
    </v-col>
    <v-col cols=12 xs=12 sm=12 md=12 lg=12 xl=12>
      <v-alert
        v-if="sumInfo.totCount!=0"
        text
        prominent
        color="#2A3B4D"
        icon="mdi-math-compass"
        border="left"
        
      >
          총매출 : {{sumInfo.totSum -cancelInfo.totSum | currency}} 원 
         , 카드총매출 : {{sumInfo.cardSum - cancelInfo.cardSum | currency}} 원 
         , 현금총매출 : {{sumInfo.cashSum - cancelInfo.cashSum| currency}} 원
      </v-alert>

      <v-alert
        v-if="sumInfo.totCount!=0"
        text
        prominent
        type="success"
        icon="mdi-tray-plus"
        border="left"
      >
         총건수 : {{sumInfo.totCount  | currency}} 건 , 총매출 : {{sumInfo.totSum | currency}} 원 
         , 카드총건수 : {{sumInfo.cardCount | currency}} 건 , 카드총매출 : {{sumInfo.cardSum | currency}} 원 
         , 현금총건수 : {{sumInfo.cashCount | currency}} 건 , 현금총매출 : {{sumInfo.cashSum | currency}} 원
      </v-alert>

      <v-alert
        v-if="cancelInfo.totCount!=0"
        text
        prominent
        type="error"
        icon="mdi-tray-minus"
        border="left"
      >
         총건수 : {{cancelInfo.totCount | currency}} 건 , 총매출 : {{cancelInfo.totSum | currency}} 원 
         , 카드총건수 : {{cancelInfo.cardCount | currency}} 건 , 카드총매출 : {{cancelInfo.cardSum | currency}} 원 
         , 현금총건수 : {{cancelInfo.cashCount | currency}} 건 , 현금총매출 : {{cancelInfo.cashSum | currency}} 원
      </v-alert>  
    </v-col>

    <v-col cols=12 >
      <v-data-table
        :headers="headers"
        :items="list"
        dense
        dark
        no-results-text="조회된 데이터가 없습니다."
        no-data-text="데이터를 조회를 하세요"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          itemsPerPageText : '표시갯수 :',
          itemsPerPageOptions : [20 , 50 , 100 , -1]
        }"
        @click:row="detailEvent"
        
      >
        <template v-slot:[`item.amount`]="{ item }">
            {{ item.amount | currency}} 원
        </template>

      </v-data-table>
    </v-col>
   
  </v-row>
  <IngireDelngInfo 
    :infodialog='infodialog' 
    :item='clickItem' 
    @dialogEvent="delngDialogInfoEvent" 
  />
</v-container>

</template>

<script>
import { mapActions , mapMutations ,mapGetters } from 'vuex';
import {excel} from "@/mixins/excel";
import commInfo from "@/mixins/comInfo";
import PagingNormal from "@/components/PagingNormal";
import TransNav from "@/components/navigation/TransNav.vue";
import IngireDelngInfo from "@/components/IngireDelngInfo";
import {currency , removeCharObject} from "@/util/filter.js";
export default {
  components : {
    PagingNormal,
    TransNav,
    IngireDelngInfo,
  },
  mixins: [excel , commInfo],
  created() {
    document.title = '거래조회';
    // 초기데이터 
    this.$store.state.overLay = false;
    // 검색날짜 // 검색구분값
    if(!this.$route.params.dateStart) {
      this.payload.dateStart = new Date().toISOString().substr(0, 10);
      this.payload.dateEnd = new Date().toISOString().substr(0, 10);
    }else {
      this.payload.dateStart = this.$route.params.dateStart;
      this.payload.dateEnd = this.$route.params.dateEnd;
    }
      this.dateStartMax = this.payload.dateEnd

    //if(!this.$route.params.searchOption) this.dateStart = this.$route.params.searchOption;

    
  },

  mounted() {
    if(!this.$store.state.transStore.transList){      
      this.searchEvent();
      return;
    }

    if(!!this.$store.state.transStore.transList && this.$store.state.transStore.transList.length <1){
      this.searchEvent();
      return;
    }
  },

  data() {
    return {
      // 검색조건 설정
      dataType : '', // 검색 날짜 타입 - 승인 / 취소
     
      dateMenu1 : false,
      dateMenu2 : false,
      textVal1 : '',
      textVal2 : '',

      payload : {
        dateStart : '', // 검색시작일
        dateEnd : '', // 검색종료일
        memberBizeCode : "", // 로그인 세션 완성후 이걸로 변경
        delngSeCode : "transCard", // 결제상태
        delngPayType : "" , // 일반 익익 바로정산구분
        tid : "", // 스와이프
        bizCode : "", // 대리점코드
        calculateType : "U", // 직거래 상태
        mberName : "",  // 판매자 성명
        confmNo : "", // 승인번호
        userId : "", // 판매자 아이디
        bizNum : "", // 사업자 번호
        issueCmpnyNm : "", // 발급자명
        sellPrice : "", // 판매금액
        endIndex : 0,
        startIndex : 0,

      },
      infodialog : false,
      clickItem : null, // 상세정보컴포넌트에 보낼 정보
      option1 : '',
      option2 : '',

      searchList : [
        {title : '합계정보-안보기'},
      ],
      searchTitle : '거래내역조회',
      
      headers: [
       {text: '구분', value:'gubun', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '대리점' , value:'cmpnm' ,align : 'center', width : '5%' , class :'d-flexcaption font-weight-bold pt-1 '},
       {text: '승인번호', value:'confmNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '코드', value:'payType', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자ID', value:'usid', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매자', value:'mberName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '상호', value:'companyName', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '판매금액', value:'amount', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '할부', value:'instlmtMonth', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '발급자명', value:'issueCmpnyNm', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '카드번호', value:'cardNo', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '승인일시', value:'confmDt', class :'d-flexcaption font-weight-bold align-center pt-1'},
       {text: '취소일시', value:'cancelDt', class :'d-flexcaption font-weight-bold align-center pt-1'},       
     ],

      thObject : {backgroundColor:'#F1F8E9' , fontSize : '0.9em' },
      
      // 걸제상태
      delngSeCodeList : [
        {text : '전체(승인건, 취소건 모두표시)' , val : 'all'},

        {text : '결제내역-전체' , val : 'transAll'},
        {text : '결제내역만-카드결제' , val : 'transCard'},
        {text : '결제내역만-현금결제' , val : 'transCash'},

        {text : '취소내역-전체' , val : 'cancelAll'},
        {text : '취소내역-카드' , val : 'cancelCard'},
        {text : '취소내역-현금' , val : 'cancelCash'},
      ],

      // 정산상태
      delngPayList : [
        {text : '전체' , val : ''},
        {text : '일반' , val : 'N'},
        {text : '익일' , val : 'T'},
        {text : '바로정산' , val : 'B'}
      ],
      
      // 옵션
      optionList : [
        {text : '전체', val : ''},
        {text : '판매자성명', val : 'mber_name'},
        {text : '승인번호', val : 'confmNo'},
        {text : '아이디', val : 'mberId'},
        {text : '사업자번호', val : 'bizNum'},
        {text : '카드사', val : 'issueCmpnyNm'},
        {text : '판매금액', val : 'sellPrice'},

      ],

      // 스와이프
      tidList : [
        {text : '전체' , val : ''},
        {text : '순간' , val : 'SP01'},
        {text : 'D+2' , val : 'SP02'}],
      // 직거래상태
      calculateList : [
        {text : '전체' , val : ''},
        {text : '유니코아' , val : 'U'},
        {text : 'KSNET직정산' , val : 'K'}],
      // 동일거래
      badCardSearchList : [
        {text : '확인' , val : 'Y'},
        {text : '무시' , val : 'N'}],
      // 전체합영역
      sumInfo : {
        totSum : 0 ,
        totCount : 0,
        cardSum  : 0,
        cardCount : 0,
        cashSum : 0,
        cashCount : 0,
      },

      cancelInfo : {
        totSum : 0 ,
        totCount : 0,
        cardSum  : 0,
        cardCount : 0,
        cashSum : 0,
        cashCount : 0,
      },
    }
  },

  computed: {
    ...mapGetters('authStore',[
      'GET_AUTH_BIZCODE' // 로그인한 사용자의 비즈코드
    ]),
    list(){
      try {
        if(!!this.$store.state.transStore.transList) return this.$store.state.transStore.transList;
      } catch (error) {
        console.error(error);
        return [];
      }
    },
    // sumInfo(){
    //   try {
    //     if(!!this.$store.state.bpwStore.inqireDelngSum) return this.$store.state.bpwStore.inqireDelngSum
    //     return {}
    //   } catch (error) {
    //     console.error(error);
    //     return {};
    //   }
    // },
    listCount(){
      try {
        return this.$store.state.bpwStore.inqireDelngTotal;
      } catch (error) {
        return 0;
      }
    }
  },

  methods: {
    ...mapMutations(['SET_ALERT_DIALOG']),
    ...mapActions('transStore' , ['fetch_trans_list']),
    
    searchEvent(){

      this.payload.memberBizeCode = this.GET_AUTH_BIZCODE; 

      // 옵션별 선택 값 구하기

      this.payload.mberName = '';
      this.payload.confmNo = '';
      this.payload.mberId = '';
      this.payload.bizNum = '';
      this.payload.issueCmpnyNm = '';
      this.payload.sellPrice = '';
      if(this.option1 === 'mber_name') { 
        this.payload.mberName = this.textVal1;
      }
      else if(this.option1 === 'confmNo') {
        this.payload.confmNo = this.textVal1;
      }
      else if(this.option1 === 'mberId') {
        this.payload.mberId = this.textVal1;
      }
      else if(this.option1 === 'bizNum') {
        this.payload.bizNum = this.textVal1;
      }
      else if(this.option1 === 'issueCmpnyNm') {
        this.payload.issueCmpnyNm = this.textVal1;
      }
      else if(this.option1 === 'sellPrice') {
        this.payload.sellPrice = this.textVal1;
      }

      if(this.option2 === 'mber_name') { 
        this.payload.mberName = this.textVal2;
      }
      else if(this.option2 === 'confmNo') {
        this.payload.confmNo = this.textVal2;
      }
      else if(this.option2 === 'mberId') {
        this.payload.mberId = this.textVal2;
      }
      else if(this.option2 === 'bizNum') {
        this.payload.bizNum = this.textVal2;
      }
      else if(this.option2 === 'issueCmpnyNm') {
        this.payload.issueCmpnyNm = this.textVal2;
      }
      else if(this.option2 === 'sellPrice') {
        this.payload.sellPrice = this.textVal2;
      }

    
      let retPayload = removeCharObject(this.payload , ['dateStart' , 'dateEnd']);
      this.fetch_trans_list(retPayload)
      .then(()=>{
        this.sumEvent(true);
      })
    },
    searchChangeEvent(val){
      if(val === '합계정보-보기') {
        this.searchList[0].title = '합계정보-안보기';
        this.sumEvent(true);
      }else {
        this.searchList[0].title = '합계정보-보기';
        this.sumEvent(false);
      }

    },
    sumEvent(val){
      this.sumInfo = {
        totSum : 0 ,
        totCount : 0,
        cardSum  : 0,
        cardCount : 0,
        cashSum : 0,
        cashCount : 0,
      }

      this.cancelInfo = {
        totSum : 0 ,
        totCount : 0,
        cardSum  : 0,
        cardCount : 0,
        cashSum : 0,
        cashCount : 0,
      }
    
      if(val){
      this.list.forEach(el => {
        // 결제내역 합계
        if(el.gubun==='승인') {
          this.sumInfo.totSum = Number(this.sumInfo.totSum) + Number(el.amount);
          this.sumInfo.totCount = Number(this.sumInfo.totCount) + 1;
          // 카드결제엔 할부정보가 없다는 것을 이용함
          if(el.delngsecodestring === '카드'){
            this.sumInfo.cardSum = Number(this.sumInfo.cardSum) + Number(el.amount);
            this.sumInfo.cardCount = Number(this.sumInfo.cardCount) + 1;
          }else {
            this.sumInfo.cashSum = Number(this.sumInfo.cashSum) + Number(el.amount);
            this.sumInfo.cashCount = Number(this.sumInfo.cashCount) + 1;
          }
        }
        if(el.gubun==='승인후취소') {
          this.sumInfo.totSum = Number(this.sumInfo.totSum) + Number(el.amount);
          this.sumInfo.totCount = Number(this.sumInfo.totCount) + 1;
          if(el.delngsecodestring === '카드'){
            this.sumInfo.cardSum = Number(this.sumInfo.cardSum) + Number(el.amount);
            this.sumInfo.cardCount = Number(this.sumInfo.cardCount) + 1;
          } else {
            this.sumInfo.cashSum = Number(this.sumInfo.cashSum) + Number(el.amount);
            this.sumInfo.cashCount = Number(this.sumInfo.cashCount) + 1;
          }
        }

        // 취소내역 합계
        if(el.gubun==='취소') {
          this.cancelInfo.totSum = Number(this.cancelInfo.totSum) + Number(el.amount);
          this.cancelInfo.totCount = Number(this.cancelInfo.totCount) + 1;
          // 카드결제엔 할부정보가 없다는 것을 이용함
          if(!!el.instlmtMonth){
            this.cancelInfo.cardSum = Number(this.cancelInfo.cardSum) + Number(el.amount);
            this.cancelInfo.cardCount = Number(this.cancelInfo.cardCount) + 1;
          }else {
            this.cancelInfo.cashSum = Number(this.cancelInfo.cashSum) + Number(el.amount);
            this.cancelInfo.cashCount = Number(this.cancelInfo.cashCount) + 1;
          }
        }

      });
      }
    },
    detailEvent(val){
      
      this.infodialog = !this.infodialog;
      this.clickItem = val;
    },

    delngDialogInfoEvent(val){
      this.infodialog = val;
    },
    excelDown(){
      try {
        this.$store.state.mainLoading = true;
        const wscols = []
        const exList = this.list.map(item=>{
            const retItem = {};
            if(item.hasOwnProperty("gubun")) {
              retItem.승인여부 = item.gubun;
              wscols.push({wpx:70});
            }
            if(item.hasOwnProperty("cmpnm")) {
              retItem.대리점 = item.cmpnm;
              wscols.push({wpx:110});
            }
            if(item.hasOwnProperty("confmNo")) {
              retItem.승인번호 = item.confmNo;
              wscols.push({wpx:80});
            }
            if(item.hasOwnProperty("payType")) {
              retItem.코드 = item.payType;
              wscols.push({wpx:60});
            }
            if(item.hasOwnProperty("usid")) {
              retItem.판매자ID = item.usid;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("mberName")) {
              retItem.판매자성명 = item.mberName;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("companyName")) {
              retItem.상호 = item.companyName;
              wscols.push({wpx:120});
            }
            if(item.hasOwnProperty("bizrno")) {
              retItem.사업자등록 = item.bizrno;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("mberMobile")) {
              retItem.판매자휴대폰 = item.mberMobile;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("rciptNo")) {
              retItem.영수증번호 = item.rciptNo;
              wscols.push({wpx:90});
            }
            if(item.hasOwnProperty("bizTelno")) {
              retItem.구매자연락처 = item.bizTelno;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("amount")) {
              retItem.판매금액 = Number(removeCharObject(item.amount));
              wscols.push({wpx:60});
            }
            if(item.hasOwnProperty("delngsecodestring")) {
              retItem.구분 = item.delngsecodestring;
              wscols.push({wpx:40});
            }
            if(item.hasOwnProperty("instlmtMonth")) {
              retItem.할부 = item.instlmtMonth;
              wscols.push({wpx:40});
            }
            if(item.hasOwnProperty("issueCmpnyNm")) {
              retItem.발급사명 = item.issueCmpnyNm;
              wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("cardNo")) {
              retItem.카드번호 = item.cardNo;
               wscols.push({wpx:130});
            }
            if(item.hasOwnProperty("confmDt")) {
              retItem.승인일시 = item.confmDt;
               wscols.push({wpx:100});
            }
            if(item.hasOwnProperty("cancelDt")) {
              retItem.취소일시 = item.cancelDt;
               wscols.push({wpx:100});
            }
            return retItem;
          });      

          this.excelEvent(exList ,`거래조회-${this.payload.dateStart}-${this.payload.dateEnd}` ,'거래조회' , wscols);

      } catch (error) {
        console.error(error);
      } finally{
        this.$store.state.mainLoading = false;
      }
    }
  },

}
</script>

