import { request } from "./index";

// 계좌 정산관련 부분
const account = {
  accountList(payload){
    return request('post' , '/account/accountList' , payload);
  },
  // 정산용엑셀
  accountExcelList(payload){
    return request('post' , '/account/accountExcelList' , payload);
  },
  // 계좌이체목록
  accountTransList(payload){
    return request('post' , '/account/accountTransList' , payload);
  },
  // 은행계좌이체목록
  bankAcntTransList(payload){
    return request('post' , '/account/bankAcntTransList' , payload);
  },
  // 출금정지 설정
  inOutSetting(fromData){
    return request('post' , '/account/inOutSetting' , fromData);
  },
  // 출금정지 상태
  inOutSettingInfo(){
    return request('get' , '/account/inOutSettingInfo');
  },
  // 계좌이체 지급/미지급처리
  acnutTransfrYn(payload){
    
    return request('post' , '/account/acnutTransfrYn' , payload);
  }
}
export default account;