import { request } from "./index";

// 거래관련
const agency = {
  // 대리점매출 수익
  agency1(payload){
    return request('post' , '/agency/agency1' , payload);
  },
  // 추천수수료 수익
  agency2(payload){
    return request('post' , '/agency/agency2' , payload);
  },
  // 가맹비 수익
  agency3(payload){
    return request('post' , '/agency/agency3' , payload);
  },
  // 대리점매출
  seller(payload){
    return request('post' , '/agency/seller' , payload);
  },
  // 매출조정목록
  adjustList(payload){
    return request('post' , '/agency/salesAdjustment' , payload);
  },
  // 전체 대리점 목록
  dealerList(){
    return request('post' , '/agency/dealerList');
  }
}

export default agency;