<template>
  <div >
    <v-system-bar 
      app
      dark 
      height="37"
      class="d-flex"
      color="teal darken-4">
      <v-img src='@/assets/logo_icon.png' max-height="30" max-width="30" contain></v-img>
      <span class="title font-weight-black">Biz Pay</span>
      <v-spacer></v-spacer>
      <!-- <v-chip v-if="!this.responsive" small class="ma-1" color="red darken-4">정보1</v-chip>
      <v-chip v-if="!this.responsive" small class="ma-1" color="yellow darken-4">정보2</v-chip>
      <v-chip v-if="!this.responsive" small class="ma-1" color="indigo darken-1">정보3</v-chip>
      <v-chip v-if="!this.responsive" small class="ma-1" color="purple darken-1">정보4</v-chip> -->
      <template v-if="!!this.GET_CMPNM">
      <v-chip small class="ma-1" color="pink darken-1" >{{this.GET_CMPNM}}</v-chip>
      <v-btn x-small class="ma-1" color="indigo darken-1" @click="logoutEvent">로그아웃</v-btn> 
      </template>
      <v-chip small class="ma-1" color="pink darken-1" to="/" v-else >로그인하세요</v-chip>
    </v-system-bar>
    <v-app-bar 
      color="teal darken-3"
      app
      dense
      dark
      flat
    >
      <v-app-bar-nav-icon v-if="this.responsive" @click="drawerEvent"></v-app-bar-nav-icon>
      <!--<v-spacer></v-spacer> -->
      <!-- <v-chip v-if="this.responsive" small class="ma-1" color="red darken-4">수수료 : 20,000</v-chip>
      <v-chip v-if="this.responsive" small class="ma-1" color="yellow darken-4">사용자수 : 10</v-chip> -->
      <template v-if="!this.responsive">
        <v-menu 
          v-for="item in menuList" :key="item.url+item.title"
          offset-y 
          open-on-hover
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              text
              large
              open-on-hover
              v-bind="attrs"
              v-on="on"
              :class="menuName==item.title ? 'menuBtn active' : 'menuBtn'"
            
            >
              {{item.title}}
            </v-btn>
          </template>
          <v-list dense color="blue-grey darken-3" dark>
            <div v-for="(items , index ) in item.subList"
              :key="items.url+items.title">
              <v-divider v-if="index!=0" dark ></v-divider>
              <v-list-item
                @click="menuClickEvent(item.title , items.title , items.url)"
                :class="getSubMenuClass(item.title , items.title)">
                <v-list-item-content><v-list-item-title>{{ items.title }}</v-list-item-title></v-list-item-content>
              </v-list-item>
            </div>
     
          </v-list>
        </v-menu>
      </template>
    </v-app-bar>
  </div>
</template>

<script>
import { mapState } from "vuex";
import commInfo from "@/mixins/comInfo";
export default {
  mixins: [commInfo],
  methods: {
    getSubMenuClass(menuName , subMenuName){
      if(this.menuName == menuName && this.subMenuName == subMenuName ) return "subMenu active";
      else return "subMenu";
    }
  },
}
</script>

<style scoped>
  .menuBtn {
    width:20%;
  }
  .menuBtn.active {
    color: darkcyan;
    font-size: 15px;
    font-weight: bold;
    background-color: ghostwhite;
  }
  .subMenu{
    cursor: pointer;
  }
  .subMenu.active{
    background-color: #90A4AE;
  }

  .v-list-item.active .v-list-item__title {
    font-weight: 900;
    color: rgb(23, 59, 59);
  }

</style>