// 운영 - 화면빌드빌드(npm run build)-> 스프링 리소스 정적 부분에 설정 -> 빌드후dist 폴더 통채로 스프링정적부분으로 이동
import axios from 'axios'
import { DOMAIN } from "../Constants";
//const DOMAIN = '/'; 

export const request = (method , url , data) =>{
  return axios({
    method,
    url : DOMAIN + url,
    data
  }).then((res)=>{
    return res.data
  }).catch( res=>{
    console.error(res);
    throw res.response.data;
  });
}
// 정산조회부분
export const InqireDelng = {
  // 전체정보 모두
  list(payload){
    return request('post' , '/bpw/InqireDelng',payload);
  }
}

// 대리점관리
export const InqireDealer = {
  // 목록
  list(payload){
    return request('post' ,'/bpw/InqireDealer' , payload);
  },
  // 개별대리점 상세정보
  get(userId){
    return request('get' ,`/bpw/dealerbyId/${userId}`); 
  },
  // 개별대리점 신규 입력
  create(payload){
    return request('post' ,'/bpw/dealerCreate' , payload);
  },
  // 개별대리점 정보 변경
  update(payload){
    return request('post' ,'/bpw/dealerModify' , payload);
  },
  // 대리점아이디 확인
  dealerIdCheck(userId){
    return request('get' ,`/bpw/dealerIdCheck/${userId}`);
  }
}

// 입출내역관리
export const InqireAtm = {
  list(payload){
    return request('post' ,'/bpw/InqireAtm' , payload);
  }
}

// 로그인 유저 정보 관련 부분
export const Member = {
  //사용자 정보조회
  auth(payload){
    return request('post' ,'/auth/login' , payload);
  },

  // 사용자별 대리점 정보 가져오기
  dealerInfo(val){
    return request('get',`/mem/dealerinfo/${val}`);
  }
}

// 출금정지상태정보 /출금정지상태 변경
export const atmConfig = {
  //출금정지상태정보 /출금정지상태 변경
  info(){
    // 코드번호는 000 으로 지정된상태
    return request('get','/bpw/atmConfig/000');
  },
  //출금정지상태정보 /출금정지상태 변경
  update(payload){
    return request('post',`/bpw/atmConfig` , payload);
  }
}
