import agencyManage from "../api/agencyManage";

const agencyManageStore = {
  namespaced: true,
  state : {
    agencyList : [],
    sellerList : [],
    agencyKindList : [], // 대리점정류
    settingAgencyList : [] // 기준대리점
   
  },
  getters : {},
  mutations : {
   set_agency_list(state , list){
     state.agencyList = list;
   },
   set_seller_list(state , list){
     state.sellerList = list;
   },
   set_agency_kind_list(state , list){
      state.agencyKindList = list;
   },
   set_setting_agency_list(state , list){
      state.settingAgencyList = list;
   },
  },
  actions : {    
    // 대리점목록
    async fetch_agency_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agencyManage.agencyList(payload);
        await commit('set_agency_list' ,list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 대리점 정보 수정
    async modify_agency_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        
        return await agencyManage.updateAgency(payload);

      } catch (error) {
        console.error(error);
        // 향후 에러 안내 팝업생성하지 지금은 진도 빼자
        commit('SET_ALERT_DIALOG',{ flag : true , message : '저장실패-잠시후 다시시도해주세요' , title : '오류' , type : 'error'}, { root: true });

      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 대리점 정보 입력
    async insert_agency({commit} , payload){
      try {

        await commit('SET_OVERLAY',true, { root: true });
        
        return await agencyManage.insertAgency(payload);
        
      } catch (error) {
        console.error(error);
        // 향후 에러 안내 팝업생성하지 지금은 진도 빼자
        commit('SET_ALERT_DIALOG',{ flag : true , message : '대리점신규입력에 문제가 있습니다.' , title : '오류' , type : 'error'}, { root: true });
        throw error;

      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 판매자목록
    async fetch_seller_list({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        const list = await agencyManage.sellerList(payload);
        await commit('set_seller_list' ,list);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },
    // 판매자 정보 수정
    async modify_seller({commit} , payload){
      try {
        await commit('SET_OVERLAY',true, { root: true });
        return await agencyManage.updateSeller(payload);
      } catch (error) {
        console.error(error);
      } finally {
        await commit('SET_OVERLAY',false, { root: true });
      }
    },

    // 기준대리점 정보확보
    async fetch_setting_agency_list({commit} ,  {memberCode , agencyCode}){
      try {
        const mapObj = await agencyManage.settingAgencyList(memberCode , agencyCode);
        await commit('set_agency_kind_list' , mapObj.agencyKindList);
        await commit('set_setting_agency_list' ,mapObj.settingAgencyList);
      } catch (error) {
        console.error(error);
      }
    }
  }
}

export default agencyManageStore;